import { VSWebClient } from '../../../base/vsweb-client-impl';
import { appStates } from '../../../app/app-states';
const SVG_NS = 'http://www.w3.org/2000/svg';
const XLINK_NS = 'http://www.w3.org/1999/xlink';

export interface ViewItem
{
    position: string,
    name: string,
    image: string
}

export interface ConfigurationViewsData
{
	Image: string
	Name: string
	Position: string
}


export interface ConfigurationViewChangedEvent
{
    pos: number,
	view: ConfigurationViewsData
}

export class MobileViewPane
{
    wrapperEl: HTMLElement
    paneEl: HTMLElement
    client: VSWebClient
    views: ViewItem[]
    currentPosition: number = 0
    positionArray: number[] = []
    duration: number
    viewsWrapper: HTMLElement
    buttons:HTMLElement
    scrollStart: number
    wrapperHeight: number
    offset: number
    viewElements: HTMLElement[]
    hasMultiSelect: boolean = false
    inDragPos: number = -1
    constructor(paneEl: HTMLElement, client: VSWebClient)
    {
        this.paneEl = paneEl;
        this.client = client;
        this.scrollStart = 0;

        let titleOffset = $('#ID_VIEWS').outerHeight();

        this.wrapperEl = document.createElement('div');
        this.wrapperEl.className = 'mobile-view-browser';

        this.buttons = document.createElement('div');
        this.buttons.classList.add('mobile-pane-buttons-tab');
        this.buttons.style.top = titleOffset + 'px';

        let addView = this.createButton('viewpane-add', 'view_button_plus');
        addView.addEventListener('click', ev =>
        {
            const createConfigurationView = '<Call Method="CreateConfigurationView"></Call>';
            this.client.command.executeXML(createConfigurationView);
            const getConfigurationView = '<Call Method="GetConfigurationViewInformation"><Image>true</Image></Call>';
            this.client.command.executeXML(getConfigurationView);
        });

        let deleteView = this.createButton('viewpane-delete', 'view_button_cross');
        deleteView.addEventListener('click', ev =>
        {
            let mutliSelectLength = this.positionArray.length;
            if (mutliSelectLength > 0)
            {
                let numArray = this.positionArray.sort((a, b) => a - b);
                for (let i = 0; i < mutliSelectLength; i++)
                {
                    let positionXml = '<Position>' + (numArray[i] - i) + '</Position>';
                    let deleteConfigurationView = '<Call Method="DeleteConfigurationView">' + positionXml + '</Call>';

                    this.client.command.executeXML(deleteConfigurationView);
                }
            }
            else
            {
                let positionXml = '<Position>' + this.currentPosition + '</Position>';
                let deleteConfigurationView = '<Call Method="DeleteConfigurationView">' + positionXml + '</Call>';
                this.client.command.executeXML(deleteConfigurationView);
            }
            let getConfigurationView = '<Call Method="GetConfigurationViewInformation"><Image>true</Image></Call>';
            this.client.command.executeXML(getConfigurationView);
        });

        let updateView = this.createButton('viewpane-update', 'view_button_refresh');
        updateView.addEventListener('click', ev =>
        {
            let mutliSelectLength = this.positionArray.length;
            if (mutliSelectLength > 0)
            {
                for (let i = 0; i < mutliSelectLength; i++)
                {
                    let positionXml = '<Position>' + this.positionArray[i] + '</Position>';
                    let updateConfigurationView = '<Call Method="UpdateConfigurationView">' + positionXml + '</Call>';
                    this.client.command.executeXML(updateConfigurationView);
                }
            }
            else
            {
                let positionXml = '<Position>' + this.currentPosition + '</Position>';
                let updateConfigurationView = '<Call Method="UpdateConfigurationView">' + positionXml + '</Call>';
                this.client.command.executeXML(updateConfigurationView);
            }
            let getConfigurationView = '<Call Method="GetConfigurationViewInformation"><Image>true</Image></Call>';
            this.client.command.executeXML(getConfigurationView);
        });

        let home = this.createButton('viewpane-last-activated', 'view_button_home');
        home.addEventListener('click', ev =>
        {
            let xml = '<Call Method="ActivateOnSaveConfigurationView"></Call>';
            this.client.command.executeXML(xml);
        });

        let reversePlay = this.createButton('viewpane-reverse-play', 'view_button_left');
        reversePlay.addEventListener('click', ev =>
        {
            let xml = '<Call Method="StartPlayConfigurationViews"><Reverse>true</Reverse><ConfigurationView>' + this.currentPosition + '</ConfigurationView></Call>';
            this.client.command.executeXML(xml);
        });

        let stop = this.createButton('viewpane-stop', 'view_button_stop');
        stop.addEventListener('click', ev =>
        {
            stopPlayMode();
        });

        this.client.on('OpenFile', ev =>
        {
            stopPlayMode();
        });

        this.client.on('NewScene3D', ev =>
        {
            stopPlayMode();
        });

        this.client.on('ImportFile', ev =>
        {
            stopPlayMode();
        });

        this.client.on('GetSceneSettings', (response) =>
        {
            if (response.Error === 'SUCCESS')
            {
                this.duration = parseInt(response.Settings.Scene.ConfigurationView.ViewPlay.DurationTime);
            }
        });
        let xml = '<Call Method="GetSceneSettings"></Call>';
        this.client.command.executeXML(xml);

        let play = this.createButton('viewpane-play', 'view_button_right');
        play.addEventListener('click', ev =>
        {
            let xml = '<Call Method="StartPlayConfigurationViews"><Reverse>false</Reverse><ConfigurationView>' + this.currentPosition + '</ConfigurationView></Call>';
            this.client.command.executeXML(xml);
        });

        this.client.on('ConfigurationViewPlaySelectionChange', res =>
        {
            let pos = parseInt(res.Selection);
            let activeViewEvent: ConfigurationViewChangedEvent = {
                pos: pos,
                view: this.client.getConfigurationViews()[pos]
            };
            this.client.emit('ConfigurationViewChanged', activeViewEvent);
        });

        this.client.on('ConfigurationViewChanged', (event: ConfigurationViewChangedEvent) =>
        {
            this.currentPosition = event.pos;
            this.positionArray = [event.pos];
            this.setHighlight();
            if ($(this.viewsWrapper).outerHeight() <= $(this.wrapperEl).outerHeight() - this.offset)
            {
                return;
            }
            let viewItemHeight = $('.viewpane-item').height();
            let offsetToCurrentView = $(this.viewElements[this.currentPosition]).offset().top + viewItemHeight;
            let offsetLowerEdge = $(this.wrapperEl).outerHeight();
            if (offsetToCurrentView > offsetLowerEdge)
            {
                let newOffsetTop = this.calculateMinMaxScroll(-1, parseInt($(this.viewsWrapper).css('top')) - viewItemHeight);
                this.viewsWrapper.style.top = newOffsetTop;
            }

        });

        let stopPlayMode = () =>
        {
            let xml = '<Call Method="StopPlayConfigurationViews"></Call>';
            this.client.command.executeXML(xml);
        }

        let multiselect = this.createButton('viewpane-multi', 'mobile_UI_check_light');
        let iconElement = $(multiselect).find('use');

        multiselect.addEventListener('click', () =>
        {
            this.hasMultiSelect = !this.hasMultiSelect;
            if (!this.hasMultiSelect)
            {
                iconElement[0].setAttributeNS(XLINK_NS, 'href', '#mobile_UI_check_light');
            }
            else
            {
                iconElement[0].setAttributeNS(XLINK_NS, 'href', '#mobile_UI_check_full');
            }
        }, false);


        let selectAllButton = document.createElement('i');
        selectAllButton.className = 'fas fa-check-double view-pane-icon';
        selectAllButton.style.bottom = '5px';
        selectAllButton.style.fontSize = '12px';
        selectAllButton.onclick = e =>
        {
            this.selectAll();

        }

        let downloadPDFButton = document.createElement('i');
        downloadPDFButton.className = 'fas fa-file-pdf view-pane-icon';
        downloadPDFButton.style.bottom = '5px';
        downloadPDFButton.style.fontSize = '12px';
        downloadPDFButton.onclick = e =>
        {
            this.createPDF();

        }

        this.buttons.appendChild(multiselect);
        this.buttons.appendChild(selectAllButton);
        this.buttons.appendChild(home);
        this.buttons.appendChild(addView);
        this.buttons.appendChild(updateView);
        this.buttons.appendChild(deleteView);
        this.buttons.appendChild(reversePlay);
        this.buttons.appendChild(stop);
        this.buttons.appendChild(play);
        this.buttons.appendChild(downloadPDFButton);

        this.paneEl.appendChild(this.buttons);
        this.paneEl.appendChild(this.wrapperEl);

        this.offset = $(this.buttons).outerHeight() + titleOffset + 10;

        this.client.on('GetConfigurationViewInformation', (response) =>
        {
            if (!response.ConfigurationView)
            {
                return;
            }
            this.views = [];
            var configurationViewsJson = Array.isArray(response.ConfigurationView) ? response.ConfigurationView : [response.ConfigurationView];
            this.client.setConfigurationViews(configurationViewsJson);
            for (let i = 0; i < configurationViewsJson.length; i++)
            {
                let viewObject = configurationViewsJson[i];
                let view: ViewItem = {
                    position: viewObject.Position,
                    name: viewObject.Name,
                    image: viewObject.Image
                };
                this.views.push(view);
            }
            this.positionArray = [];
            this.build();
            if (this.viewElements.length && (!this.currentPosition || this.currentPosition === 0))
            {
                this.client.setDefaultConfigurationView(configurationViewsJson[0]);
            }

            let imagesPerRow = Math.floor($(this.paneEl).width() / 170);
            let remainingSpace = $(this.paneEl).width() - (imagesPerRow * 170) - 20;
            let imageHeightRatio = 90 / 160;
            let newImageWidth = 160 + remainingSpace / imagesPerRow;
            let newImageHeight = newImageWidth * imageHeightRatio;
            this.wrapperEl.style.width = imagesPerRow * (newImageWidth + 10) + 'px';
            for (let i = 0; i < this.viewElements.length; i++)
            {
                $(this.viewElements[i]).children('img').width(newImageWidth - 40);
                $(this.viewElements[i]).children('img').height(newImageHeight - 40);
            }
        })
        this.client.command.executeXML('<Call Method="GetConfigurationViewInformation"></Call>');

        $(this.paneEl).kendoTouch({
            drag: (e) =>
            {
                this.drag(e);
            },
            dragstart: (e) =>
            {
                this.dragStart(e);
            }
        });

        this.client.on('Resize', () =>
        {
            setTimeout(() =>
            {
                let imagesPerRow = Math.floor($(this.paneEl).width() / 170);
                let remainingSpace = $(this.paneEl).width() - (imagesPerRow * 170) - 20;
                let imageHeightRatio = 90 / 160;
                let newImageWidth = 160 + remainingSpace / imagesPerRow;
                let newImageHeight = newImageWidth * imageHeightRatio;
                this.wrapperEl.style.width = imagesPerRow * (newImageWidth + 10) + 'px';
                for (let i = 0; i < this.viewElements.length; i++)
                {
                    $(this.viewElements[i]).children('img').width(newImageWidth - 40);
                    $(this.viewElements[i]).children('img').height(newImageHeight - 40);
                }
            }, 500)
        })

    }

    drag(e)
    {
        if ($(this.viewsWrapper).outerHeight() <= $(this.wrapperEl).outerHeight() - this.offset)
        {
            return;
        }
        let distance = e.touch.y.location - this.scrollStart;
        let scrollTop = parseInt($(this.viewsWrapper).css('top'));
        if (distance > 15 || distance < -15)
        {
            this.scrollStart = e.touch.y.location;
        }
        if ( distance < 0)
        {
            this.viewsWrapper.style.top = this.calculateMinMaxScroll(-1, distance + scrollTop);
        }
        else if (distance > 0 )
        {
            this.viewsWrapper.style.top = this.calculateMinMaxScroll(1, distance + scrollTop);
        }
    }

    calculateMinMaxScroll(orientation, scrollSum)
    {
        this.wrapperHeight = $(this.viewsWrapper).outerHeight() - $(this.wrapperEl).outerHeight();
        if (orientation < 0 && scrollSum < -(this.wrapperHeight))
        {
            return -(this.wrapperHeight) + 'px';
        }
        else if (orientation > 0 && scrollSum > this.offset)
        {
            return this.offset + 'px';
        }
        else
        {
            return scrollSum + 'px';
        }
    }

    dragStart(e)
    {
        this.scrollStart = e.touch.y.location;
    }

    build()
    {
        $(this.wrapperEl).empty();

        this.viewsWrapper = document.createElement('div');
        this.viewsWrapper.style.position = 'absolute';
        this.viewsWrapper.style.zIndex = '10';
        this.viewsWrapper.style.top = this.offset + 'px';
        this.wrapperEl.appendChild(this.viewsWrapper);

        this.update();

    }

    selectAll ()
    {
        this.positionArray = [];
        this.viewElements.forEach((view: HTMLElement, index: number) =>
        {
            this.positionArray.push(index);
        });
        this.setHighlight();
    }

    createPDF ()
    {
        let xml = '<Call Method="GetViewsAsPDF" CustomId="WVViewPDF">';
        if (this.positionArray.length > 0)
        {
            let sortedArray = this.positionArray.sort((a, b) => a - b);
            sortedArray.forEach(pos =>
            {
                xml += '<Id>' + pos + '</Id>';
            });
        }
        else
        {
            xml += '<Id>' + this.currentPosition + '</Id>';
        }
        xml += '</Call>';
        this.client.command.executeXML(xml);
        this.client.once('GetViewsAsPDF' , res =>
        {
            if (res.CustomId && res.CustomId.indexOf('WVViewPDF') !== -1)
            {
                let name = appStates.fileName ? 'ViewExport_' + appStates.fileName + '.pdf' : 'ViewExport.pdf';
                let byteCharacters = atob(res.Image);
                let byteNumbers = new Array(byteCharacters.length);
                for (let i = 0; i < byteCharacters.length; i++)
                {
                    byteNumbers[i] = byteCharacters.charCodeAt(i);
                }
                let byteArray = new Uint8Array(byteNumbers);
                let blob = new Blob([byteArray], {
                    type: 'application/pdf'
                });
                //IE and edge
                if (window.navigator && window.navigator.msSaveOrOpenBlob)
                {
                    window.navigator.msSaveOrOpenBlob(blob, name);
                }
                else
                {
                    const link = document.createElement('a');
                    // create a blobURI pointing to our Blob
                    link.href = URL.createObjectURL(blob);
                    link.download = name;
                    // some browser needs the anchor to be in the doc
                    document.body.appendChild(link);
                    link.click();
                    link.remove();
                }
            }
        });
    }

    createButton(id: string, icon: string): HTMLElement
    {
        let el = document.createElement('span')
        el.id = id
        let elWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg')
        elWrapper.style.width = '19px'
        elWrapper.style.height = '19px'
        let elIcon = document.createElementNS(SVG_NS, 'use')
        elIcon.setAttributeNS(XLINK_NS, 'href', '#' + icon)
        elWrapper.appendChild(elIcon)
        el.appendChild(elWrapper)
        return el
    }

    update()
    {
        let tabindexBegin = 1000
        this.viewElements = [];
        for (let itemInfo of this.views)
        {
            let viewEl = document.createElement('div');
            viewEl.classList.add('viewpane-item');
            viewEl.tabIndex = tabindexBegin++;
            let viewImg = document.createElement('img');
            viewImg.width = 160;
            viewImg.height = 90;
            viewImg.style.border = '1px solid #000';
            viewImg.src = 'data:image/jpeg;base64,' + itemInfo.image;
            viewImg.draggable = false;
            viewImg.style.pointerEvents = 'none';
            viewEl.draggable = true;
            //Action buttons
            let iconBar = document.createElement('div');
            iconBar.className = 'viewpane-item-action-bar';
            //Activate view
            let activateIcon = document.createElement('i');
            activateIcon.className = 'fas fa-play view-action-icon';
            iconBar.appendChild(activateIcon);
            activateIcon.onclick = e =>
            {
                this.client.setConfigurationViewPosition(parseInt(itemInfo.position));
            }
            //Activate only the camera of the view
            let onlyCameraIcon = document.createElement('i');
            onlyCameraIcon.className = 'fas fa-video view-action-icon';
            iconBar.appendChild(onlyCameraIcon);
            onlyCameraIcon.onclick = e =>
            {
                this.client.setConfigurationViewPosition(parseInt(itemInfo.position), true, false);
            };
            let withoutCameraIcon = document.createElement('i');
            withoutCameraIcon.className = 'fas fa-video-slash view-action-icon';
            iconBar.appendChild(withoutCameraIcon);
            withoutCameraIcon.onclick = e =>
            {
                this.client.setConfigurationViewPosition(parseInt(itemInfo.position), false, true);
            }
            let viewName = document.createElement('p');
            viewName.appendChild(document.createTextNode(itemInfo.name));
            viewEl.appendChild(viewImg);
            viewEl.appendChild(iconBar);
            viewEl.appendChild(viewName);

            viewEl.addEventListener('contextmenu', ev =>
            {
                ev.preventDefault();
            });

            viewEl.ondragstart = e =>
            {
                this.inDragPos = parseInt(itemInfo.position);
            }
            viewEl.ondragenter = e =>
            {
                e.preventDefault();
                viewImg.style.borderLeft = '5px solid ' + appStates.mainColor;
            }
            viewEl.ondragleave = e =>
            {
                e.preventDefault();
                viewImg.style.borderLeft = '1px solid rgb(0, 0, 0)';
            }
            viewEl.ondragover = e =>
            {
                e.preventDefault();
                viewImg.style.borderLeft = '5px solid ' + appStates.mainColor;
            }
            viewEl.ondrop = e =>
            {
                if (this.inDragPos !== -1)
                {
                    let xml = '<Call Method="MoveConfigurationView">';
                    xml += '<ConfigurationView>' + this.inDragPos + '</ConfigurationView>';
                    xml += '<Position>' + parseInt(itemInfo.position) + '</Position></Call>';
                    this.inDragPos = -1;
                    xml += '<Call Method="GetConfigurationViewInformation"><Image>true</Image></Call>';
                    this.client.command.executeXML(xml);
                }
            }

            viewEl.addEventListener('click', ev =>
            {
                let pos = parseInt(itemInfo.position) || 0;
                if (this.hasMultiSelect)
                {
                    //Push new view to the array if it isn't already part of it
                    if (this.positionArray.indexOf(pos) === -1)
                    {
                        this.positionArray.push(pos);
                    }
                    //Extract new view from array if it is already part of it
                    else
                    {
                        this.positionArray.splice(this.positionArray.indexOf(pos), 1);
                    }
                    //Push the single selected element to the array if there was a single selection before
                    if (this.positionArray.indexOf(this.currentPosition) === -1 && this.currentPosition !== undefined)
                    {
                        this.positionArray.push(this.currentPosition);
                    }
                    this.setHighlight();
                    this.currentPosition = undefined;
                }
                else
                {
                    this.currentPosition = pos;
                    this.positionArray = [pos];
                    this.setHighlight();
                }
            });

            viewEl.addEventListener('dblclick', ev =>
            {
                this.activateView(itemInfo);
            });

            $(viewEl).kendoTouch({
                doubletap: ev =>
                {
                    this.activateView(itemInfo);
                },
                hold: ev =>
                {
                    ev.preventDefault();
                    let el = $(viewName);
                    el.attr('contenteditable', 'true');
                    el.focus();
                    el.blur(e =>
                    {
                        e.stopPropagation();
                        this.setName(e, itemInfo.position);
                        $(e.target).off('blur');
                        $(e.target).attr('contenteditable', 'false');
                    });
                    el.on('keydown', function (e)
                    {
                        e.stopPropagation();
                        if (e.keyCode === 13)
                        {
                            e.preventDefault();
                            $(e.target).off('keydown')
                            $(e.target).attr('contenteditable', 'false')
                        }
                    });
                    el.on('keyup', function (e)
                    {
                        e.stopPropagation();
                    });
                }
            });

            this.viewsWrapper.appendChild(viewEl);
            this.viewElements.push(viewEl)
            this.currentPosition = 0;
            this.positionArray = [0];
            this.setHighlight();
        };
    }

    setName(e, position: string) {
        var command = '<Call Method="UpdateConfigurationView"><Position>' + position + '</Position>';
        command += '<Name>' + e.target.textContent + '</Name></Call>';
        this.client.command.executeXML(command);
    }

    activateView(itemInfo: ViewItem)
    {
        this.client.setConfigurationViewPosition(parseInt(itemInfo.position));
    }

    setHighlight()
    {
        for (let i = 0; i < this.viewElements.length; i++)
        {
            if (this.positionArray.length > 0)
            {
                if (this.positionArray.indexOf(i) !== -1)
                {
                    $(this.viewElements[i]).addClass('mobile-active-view');
                }
                else
                {
                    $(this.viewElements[i]).removeClass('mobile-active-view');
                }
            }
            else
            {
                if (i === this.currentPosition)
                {
                    $(this.viewElements[i]).addClass('mobile-active-view');
                }
                else
                {
                    $(this.viewElements[i]).removeClass('mobile-active-view');
                }
            }
        }
    }
}



