import { Dialog } from './dialog'
import { EventEmitter } from 'events'
import { VSWebClient } from '../base/vsweb-client-impl'
import { WVTooltip } from '../app/tooltip';
import { settings } from '../app/app-settings';
var tinycolor = require("tinycolor2");
declare var vsClient: VSWebClient;
declare var _ColorPalette: string[][];
declare var _ColorPaletteWithAlpha: string[][];
const SVG_NS = 'http://www.w3.org/2000/svg';
const XLINK_NS = 'http://www.w3.org/1999/xlink';

const paletteColorsRGBA = [
	['rgba(0,0,0,1)', 'rgba(128,0,0,1)', 'rgba(0,128,0,1)', 'rgba(128,128,0,1)', 'rgba(0,0,128,1)'],
	['rgba(128,0,128,1)', 'rgba(0,128,128,1)', 'rgba(192,192,192,1)', 'rgba(192,220,192,1)', 'rgba(166,202,240,1)'],
	['rgba(255,251,240,1)', 'rgba(160,160,164,1)', 'rgba(128,128,128,1)', 'rgba(255,0,0,1)', 'rgba(0,255,0,1)'],
	['rgba(255,255,0,1)', 'rgba(0,0,255,1)', 'rgba(255,0,255,1)', 'rgba(0,255,255,1)', 'rgba(255,255,255,1)']]

const paletteColorsRGB = [
	['rgb(0,0,0)', 'rgb(128,0,0)', 'rgb(0,128,0)', 'rgb(128,128,0)', 'rgb(0,0,128)'],
	['rgb(128,0,128)', 'rgb(0,128,128)', 'rgb(192,192,192)', 'rgb(192,220,192)', 'rgb(166,202,240)'],
	['rgb(255,251,240)', 'rgb(160,160,164)', 'rgb(128,128,128)', 'rgb(255,0,0)', 'rgb(0,255,0)'],
    ['rgb(255,255,0)', 'rgb(0,0,255)', 'rgb(255,0,255)', 'rgb(0,255,255)', 'rgb(255,255,255)']]

let rgbPalette: string[][];
let rgbaPalette: string[][];
try
{
    rgbPalette = _ColorPalette;
}
catch (e)
{
    rgbPalette = paletteColorsRGB;
}

try
{
    rgbaPalette = _ColorPaletteWithAlpha;
}
catch (e)
{
    rgbaPalette = paletteColorsRGBA;
}

export class CustomPalette extends EventEmitter
{

	mount: HTMLElement
	width: number
	height: number
	spectrumEl: HTMLElement
	chosenColor: string
	activeColor: string
	showAlpha
	hexInputEl: HTMLInputElement
	canvas: HTMLCanvasElement
	pixelBox: HTMLElement
	colorPrevField: HTMLElement
	colorPrevText: HTMLElement
	nodes: string[] = []


	constructor(activeColor: string, showAlpha)
	{
		super()

		this.mount = document.createElement('div')
		this.mount.style.position = 'absolute'
		this.activeColor = activeColor
		this.showAlpha = showAlpha

		this.pixelBox = document.createElement('div');
		this.pixelBox.className = 'color-pipette-preview-wrapper';
		this.colorPrevField = document.createElement('span');
		this.colorPrevField.className = 'color-pipette-preview-field';
		this.colorPrevText = document.createElement('span');
		this.colorPrevText.className = 'color-pipette-preview-text';

		this.pixelBox.appendChild(this.colorPrevField);
		this.pixelBox.appendChild(this.colorPrevText);

		document.body.appendChild(this.pixelBox);
		$(this.pixelBox).hide();

		document.body.appendChild(this.mount)
		setTimeout(() =>
		{
			document.addEventListener('click', this.close)
		})

		this.spectrumEl = document.createElement('div')
		this.spectrumEl.id = 'spectrum-picker-el'
		this.mount.appendChild(this.spectrumEl)

		let $spEl = $(this.spectrumEl)
		$spEl.attr("alpha", this.showAlpha)
		$(this.spectrumEl).spectrum({
			flat: true,
			color: this.activeColor,
			showAlpha: this.showAlpha,
			showInput: true,
			showInitial: true,
			showPalette: true,
			showSelectionPalette: true,
			maxSelectionSize: 40,
			preferredFormat: "rgb",
			localStorageKey: 'customColorStorage',
			palette: this.showAlpha ? rgbaPalette : rgbPalette,
			show: function (color)
			{
				if ($('.sp-alpha-enabled').length > 0)
				{
					$('.color-palette-pipette-picker-wrapper').css('top', '274px');
				}
				if ($(this).attr("alpha") === 'false')
				{
					$('.sp-input').val(computeRgb(color))
					$('#sp-hex-input').val(computeHex(color))

					$('.sp-input').on('keyup', function (event)
					{
						if (event.keyCode === 13)
						{
							$(this).val(computeRgb(this.value))
							$('#sp-hex-input').val(computeHex(this.value))
						}
					})

					$('.sp-input').blur(function (event)
					{
						$(this).val(computeRgb(this.value))
						$('#sp-hex-input').val(computeHex(this.value))
					})
				}
				else
				{
					$('#sp-hex-input').val('#' + computeHexWithAlpha(color))

					$('.sp-input').on('keyup', function (event)
					{
						if (event.keyCode === 13)
						{
							$('#sp-hex-input').val(computeHexWithAlpha(this.value))
						}
					})

					$('.sp-input').blur(function (event)
					{
						$('#sp-hex-input').val(computeHexWithAlpha(this.value))
					})
				}
			},
			move: function (color)
			{
				if ($(this).attr("alpha") === 'false')
				{
					$('.sp-input').val(computeRgb(color))
					$('#sp-hex-input').val(computeHex(color))
				} else
				{
					$('#sp-hex-input').val(computeHexWithAlpha(color))
				}
			},
			change: (color) =>
            {
                let rgbColor;
                if (this.showAlpha)
                {
                    rgbColor = computeRgb(color);
                }
                else
                {
                    rgbColor = computeRgbWithAlpha(color);
                }
				this.chosenColor = rgbColor;
			}
		});

		let viewportWidth = Math.min(window.innerWidth, document.documentElement.offsetWidth)
		let viewportHeight = Math.max(window.innerHeight, document.documentElement.offsetHeight)

		let width = 280
		let height = 300

		this.mount.style.top = 0.5 * (viewportHeight - height) + 'px'
		this.mount.style.left = 0.5 * (viewportWidth - width) + 'px'
		this.mount.className = 'wv-color-picker-popup'

		let $mount = $(this.mount)

		this.hexInputEl = document.createElement('input')
		this.hexInputEl.id = 'sp-hex-input'
		this.hexInputEl.className = 'sp-input'
		this.hexInputEl.style.marginTop = '5px'
		$(this.hexInputEl).attr("alpha", this.showAlpha)
		if (this.showAlpha)
		{
			$(this.hexInputEl).val(computeHexWithAlpha(this.activeColor))
		} else
		{
			$(this.hexInputEl).val(computeHex(this.activeColor))
		}

		let pipette = document.createElement('div');
		pipette.className = 'color-palette-pipette-picker-wrapper';
		if (this.showAlpha)
		{
			pipette.style.top = '274px';
		}

		let pipetteWrapper = document.createElement('span');
		pipetteWrapper.className = 'color-palette-pipette-picker-icon';
		let pipetteToolTip = new WVTooltip(pipetteWrapper);
		pipetteToolTip.setTitle(this.translateTooltip('ID_COLOR_PICKER_PALETTE_CHOOSE_FROM_PART'));
		pipetteToolTip.setOrientation('bottom');
		let pipetteIcon = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
		pipetteIcon.style.width = '25px';
		pipetteIcon.style.height = '25px';
		let pipetteUseIcon = document.createElementNS(SVG_NS, 'use');
		pipetteUseIcon.setAttributeNS(XLINK_NS, 'href', '#10_auswahl_durch_farbe');
		pipetteIcon.appendChild(pipetteUseIcon);
		pipetteWrapper.appendChild(pipetteIcon);

		let paletteWrapper = document.createElement('span');
		paletteWrapper.className = 'color-palette-pipette-picker-icon';
		paletteWrapper.style.marginLeft = '5px';
		let paletteTooltip = new WVTooltip(paletteWrapper);
		paletteTooltip.setTitle(this.translateTooltip('ID_COLOR_PICKER_PALETTE_CHOOSE_FROM_PIXEL'));
		paletteTooltip.setOrientation('bottom');
		let paletteIcon = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
		paletteIcon.style.width = '25px';
		paletteIcon.style.height = '25px';
		paletteIcon.style.position = 'relative';
		paletteIcon.style.top = '2px';
		let paletteUseIcon = document.createElementNS(SVG_NS, 'use');
		paletteUseIcon.setAttributeNS(XLINK_NS, 'href', '#palette_pipette');
		paletteIcon.appendChild(paletteUseIcon);
		paletteWrapper.appendChild(paletteIcon);

		pipette.appendChild(pipetteWrapper);
		pipette.appendChild(paletteWrapper);
		this.pickPixel = this.pickPixel.bind(this);
		this.selectPixelColor = this.selectPixelColor.bind(this);
		paletteIcon.onclick = e =>
		{
			e.preventDefault();
			e.stopPropagation();
			this.canvas = vsClient.vsCanvas.canvas2D;
			$(this.pixelBox).show()
			$(this.mount).hide();
			this.canvas.addEventListener('mousemove', this.pickPixel);
			this.canvas.addEventListener('mousedown', this.selectPixelColor);
		}

		pipetteIcon.onclick = e =>
		{
			e.preventDefault();
			e.stopPropagation();
			$(this.mount).hide();
			let xml = '<Call Method="GetSelectedNodes"></Call>';
			vsClient.command.executeXML(xml);
			vsClient.once('GetSelectedNodes', nodeRes =>
			{
				if (Array.isArray(nodeRes.NodeId))
				{
					this.nodes = nodeRes.NodeId;
				}
				else
				{
					this.nodes = [nodeRes.NodeId];
				}
				vsClient.once('NodePropertiesChanged', res =>
				{
					if (res.General)
					{
						let rgba = computeRgbWithAlpha(res.General.Color);
						let oldSelectionCall = '<Call Method="SetSelectedNodes">';
						let selectedNodesLength = this.nodes.length;
						for (let i = 0; i < selectedNodesLength; i++)
						{
							oldSelectionCall += '<NodeId>' + this.nodes[i] + '</NodeId>';
						}
						oldSelectionCall += '</Call>';
						vsClient.command.executeXML(oldSelectionCall);
						vsClient.once('SetSelectedNodes', () =>
						{
							this.emit('customColorChosen', rgba);
							$(this.spectrumEl).spectrum('destroy');
							vsClient.command.executeXML('<Call Method="ModifySelection"><SelectionModifier>Deselect</SelectionModifier></Call>');
						})
					}
					//If user clicks on the canvas wait for the next click
					else
					{
						vsClient.once('NodePropertiesChanged', res =>
						{
							if (res.General)
							{
								let rgba = computeRgbWithAlpha(res.General.Color);
								let oldSelectionCall = '<Call Method="SetSelectedNodes">' + this.nodes.toString() + '</Call>';
								vsClient.command.executeXML(oldSelectionCall);
								vsClient.once('SetSelectedNodes', () =>
								{
									this.emit('customColorChosen', rgba);
									$(this.spectrumEl).spectrum('destroy');
									vsClient.command.executeXML('<Call Method="ModifySelection"><SelectionModifier>Deselect</SelectionModifier></Call>');
								})
							}
							else
							{
								$(this.spectrumEl).spectrum('destroy');
								vsClient.command.executeXML('<Call Method="ModifySelection"><SelectionModifier>Deselect</SelectionModifier></Call>');
							}
						})
					}
				})
			})
		}

		$('.sp-input-container').append(this.hexInputEl)
		$('.sp-picker-container').append(pipette)

		$(this.hexInputEl).blur(function (event)
		{
			if ($(this).attr("alpha") === 'false')
			{
				$('.sp-input').val(computeRgb(this.value))
				$(this).val(computeHex(this.value))
			} else
			{
				$('.sp-input').val(computeRgbWithAlpha(this.value))
				$(this).val(computeHexWithAlpha(this.value))
			}
		})

		$(this.hexInputEl).on('keyup', function (event)
		{
			if (event.keyCode === 13)
			{
				$('#spectrum-picker-el').spectrum('set', this.value)
				if ($(this).attr("alpha") === 'false')
				{
					$('.sp-input').val(computeRgb(this.value))
					$(this).val(computeHex(this.value))
				} else
				{
					$('.sp-input').val(computeRgbWithAlpha(this.value))
					$(this).val(computeHexWithAlpha(this.value))
				}
			}
		})

		$mount.find('.sp-cancel').on('click', e =>
		{
			$(this.pixelBox).remove();
			$spEl.spectrum('destroy')
		})
		$mount.find('.sp-choose').on('click', e =>
		{
			$(this.pixelBox).remove();
			let colorValue = tinycolor($spEl.spectrum('get'))
			let alpha = colorValue.getAlpha()
            colorValue.setAlpha(alpha === 1 ? 1.0 : alpha)
			let RGBString = this.showAlpha ? computeRgbWithAlpha(colorValue._originalInput) : computeRgb(colorValue._originalInput);
			this.emit('customColorChosen', RGBString)
			$spEl.spectrum('destroy')
		})

		function computeHexWithAlpha(color)
		{
			let colorObj;
			if (color._originalInput)
			{
				colorObj = tinycolor(color._originalInput)
			}
			else
			{
				colorObj = tinycolor(color)
			}
			return '#' + (Math.round(colorObj.toRgb().a * 255)).toString(16).toUpperCase() + colorObj.toHex().toUpperCase()
		}

		function computeHex(color)
		{
			let colorObj;
			if (color._originalInput)
			{
				colorObj = tinycolor(color._originalInput);
			}
			else
			{
				colorObj = tinycolor(color);
			}
			return '#' + colorObj.toHex().toUpperCase();
		}

		function computeRgb(color)
		{
			let rgb;
			if (color._originalInput)
			{
				rgb = tinycolor(color._originalInput).toRgb();
			}
			else
			{
				rgb = tinycolor(color).toRgb();
			}
			return 'rgb(' + rgb.r + ', ' + rgb.g + ', ' + rgb.b + ')'
		}

		function computeRgbWithAlpha(color)
		{
            let rgb;
            let alpha;
			if (color._originalInput)
            {
                rgb = tinycolor(color._originalInput).toRgb();
			}
			else
			{
                alpha = color.slice(1, 3);
				rgb = tinycolor('#' + color.slice(3) + alpha).toRgb();
			}
			return 'rgba(' + rgb.r + ', ' + rgb.g + ', ' + rgb.b + ', ' + rgb.a + ')'
		}
	}

	close = (ev: MouseEvent) =>
	{
		if (ev.target !== this.mount || !this.mount.contains(ev.target as Node))
		{
			this.mount.style.display = 'none'
			$(this.pixelBox).remove();

			document.body.removeChild(this.mount)
			document.removeEventListener('click', this.close)
		}
	}

	pickPixel = function (e)
	{
		let ctx = this.canvas.getContext('2d');
		let x = e.pageX - this.canvas.getBoundingClientRect().left;
		let y = e.pageY - this.canvas.getBoundingClientRect().top;
		this.pixelBox.style.top = (e.pageY + 22) + 'px';
		this.pixelBox.style.left = (e.pageX + 22) + 'px';
		var imageData = ctx.getImageData(x, y, 1, 1);
		var pixel = imageData.data;
		this.colorPrevField.style.backgroundColor = 'rgb(' + pixel[0] + ', ' + pixel[1] + ', ' + pixel[2] + ')';
		this.colorPrevText.textContent = 'rgb(' + pixel[0] + ', ' + pixel[1] + ', ' + pixel[2] + ')';
	}

	selectPixelColor = function (e)
	{
		e.preventDefault();
		e.stopPropagation();
		$(this.pixelBox).hide();
		this.canvas.removeEventListener('mousemove', this.pickPixel);
		this.canvas.removeEventListener('mousedown', this.selectPixelColor);
		let rgba = this.colorPrevText.textContent.replace('rgb', 'rgba').replace(')', ' ,1)');
		this.emit('customColorChosen', rgba);
		$(this.spectrumEl).spectrum('destroy');
	}

	translateTooltip(langId: string)
	{
		let tooltip: string;
		let lang = settings.lang;
		if (_lang_data[langId] && _lang_data[langId][lang])
		{
			tooltip = _lang_data[langId][lang]
		} else if (_custom_lang_data && _custom_lang_data[langId] && _custom_lang_data[langId][lang])
		{
			tooltip = _custom_lang_data[langId][lang]
		} else if (_lang_data[langId] && _lang_data[langId]['ENU'])
		{
			tooltip = _lang_data[langId]['ENU']
		}
		else if (_custom_lang_data && _custom_lang_data[langId] && _custom_lang_data[langId]['ENU'])
		{
			tooltip = _custom_lang_data[langId]['ENU']
		}
		else
		{
			tooltip = langId
		}
		return tooltip;
	}

}


