import { VSWebClient } from "../../base/vsweb-client-impl";
import XMLParser = require('../../base/DOMXMLParser');
import { FolderItem, FileItem, MobileFileBrowser } from "./templates/wv-files";
import { CustomPalette } from '../../pane/custom-palette';
import { MobileKeyValueEntry } from "./adapter";
import { TreeRow } from "./templates/wv-modeltree";
import { NodeType } from "../../app/node-types";
import * as ui from '../app/ui';
var tinycolor = require("tinycolor2");

const SVG_NS = 'http://www.w3.org/2000/svg';
const XLINK_NS = 'http://www.w3.org/1999/xlink';
declare var _MobileFilesPerPage: number;

export abstract class MobilePaneItem
{
    wrapperEl: HTMLElement
    visible: boolean
    paneEl: HTMLElement
    source: string
    client: VSWebClient
    val: any
    children: MobilePaneItem[]
    parent: MobileGroup
    key: string
    titleEl: HTMLElement
    tooltipWrapper: HTMLElement
    tooltip: string
    tooltipVisible: boolean = false
    readOnly: boolean
    constructor(paneEl: HTMLElement, readOnly: boolean, client: VSWebClient)
    {
        this.paneEl = paneEl;
        this.visible = false;
        this.client = client;
        this.val = undefined;
        this.readOnly = readOnly;
        this.titleEl = document.createElement('div');
        this.wrapperEl = document.createElement('div');

        this.wrapperEl.ontouchstart = e =>
        {
            if (ui.showTooltips && ((this instanceof MobileGroup && this.tooltip) || !(this instanceof MobileGroup)))
            {
                e.preventDefault();
                e.stopPropagation();
                this.showTooltip();
                this.tooltipVisible = true;
            }
        }

        this.wrapperEl.onmousedown = e =>
        {
            if (ui.showTooltips && ((this instanceof MobileGroup && this.tooltip) || !(this instanceof MobileGroup)))
            {
                e.preventDefault();
                e.stopPropagation();
                this.showTooltip();
                this.tooltipVisible = true;
            }
        }

        setTimeout(() =>
        {
            this.applyTooltipListener();
        });
    }

    abstract setValue(val);

    abstract setKey (key);

    applyTooltipListener()
    {
        this.tooltipWrapper = $(this.paneEl).parent().children('#mobile-pane-tooltip')[0] as HTMLElement;
        $(this.tooltipWrapper).hide();
    }

    highlightSearchString(searchString: string)
    {
        if (!searchString)
        {
            this.titleEl.innerHTML = this.key;
            return;
        }
        let length = searchString.length;
        let start = this.key.toLowerCase().indexOf(searchString);
        let titleLength = this.key.length;
        let formattedTitle = "";

        if (start !== -1)
        {
            for (let i = 0; i < titleLength; i++)
            {
                if (i >= start && i < start + length)
                {
                    formattedTitle += '<span class="pane-search-text-highlight">' + this.key[i] + '</span>';
                }
                else
                {
                    formattedTitle += this.key[i];
                }
            }
            this.titleEl.innerHTML = formattedTitle;
        }
        else
        {
            this.titleEl.innerHTML = this.key;
        }
    }

    showTooltip()
    {
        $(this.tooltipWrapper).empty();
        let tooltipTitle = document.createElement('div');
        tooltipTitle.textContent = this.key;
        tooltipTitle.className = 'mobile-pane-tooltip-title';

        let tooltipText = document.createElement('div');
        tooltipText.textContent = this.tooltip;
        tooltipText.className = 'mobile-pane-tooltip-message';

        this.tooltipWrapper.appendChild(tooltipTitle);
        this.tooltipWrapper.appendChild(tooltipText);
        $(this.tooltipWrapper).show();

        document.addEventListener('touchstart', this.detectOusideClick.bind(this));
        document.addEventListener('mousedown', this.detectOusideClick.bind(this));
    }

    setToolTip(tooltip: string)
    {
        this.tooltip = tooltip;
    }

    detectOusideClick(ev)
    {
        if (ev.target !== this.tooltipWrapper && !this.tooltipWrapper.contains(ev.target))// && ev.target !== this.wrapperEl && !this.wrapperEl.contains(ev.target))
        {
            $(this.tooltipWrapper).hide();
            document.removeEventListener('touchstart', this.detectOusideClick.bind(this));
            document.removeEventListener('mousedown', this.detectOusideClick.bind(this));
            this.tooltipVisible = false;
        }
    }

    hide()
    {
        if (this instanceof MobileGroup)
        {
            $(this.panelContainer).hide();
        }
        $(this.wrapperEl).hide();
    }

    show()
    {
        if (this.children && this instanceof MobileGroup)
        {
            let vis = false;
            for (let i = 0; i < this.children.length; i++)
            {
                if (this.children[i] instanceof MobileGroup)
                {
                    let group = this.children[i] as MobileGroup;
                    vis = this.hasVisibleChildren(group);
                    if (vis)
                    {
                        break;
                    }
                }
                else
                {
                    vis = this.children[i].visible;
                    if (vis)
                    {
                        break;
                    }
                }
            }
            if (vis && (!this.parent || !this.parent.isCollapsed))
            {
                $(this.wrapperEl).show();
                $(this.panelContainer).show();
            }
        }
        else if ((this.val !== undefined || (this instanceof MobileSelect && (this.valueEl.id === 'UID_PRESET' || this.valueEl.id === 'UID_DENSITY_PRESET')))
            && this.parent && !this.parent.isCollapsed)
        {
            $(this.wrapperEl).show();
        }
    }

    hasVisibleChildren(group: MobileGroup)
    {
        let vis = false;
        for (let i = 0; i < group.children.length; i++)
        {
            if (group.children[i] instanceof MobileGroup)
            {
                let childGroup = group.children[i] as MobileGroup;
                vis = this.hasVisibleChildren(childGroup);
                if (vis)
                {
                    return true;
                }
            }
            else
            {
                vis = group.children[i].visible;
                if (vis)
                {
                    return true;
                }
            }
        }
        return vis;
    }
}


export class MobileGroup extends MobilePaneItem
{
    isCollapsed: boolean
    panelContainer: HTMLElement
    level: number
    toggleIcon: HTMLElement
    id: string
    flag: MobileFlag

    constructor(paneEl: HTMLElement, groupName: string, isCollapsed: boolean, parent: MobileGroup, level: number, client: VSWebClient, flag?: MobileFlag)
    {
        super(paneEl, false, client);
        this.level = level;
        this.flag = flag;
        //el is the wrapper/toggle button with text
        this.wrapperEl.className = "mobile-pane-group";
        this.wrapperEl.style.marginLeft = (level * 10) + 'px';
        //panel container
        this.panelContainer = document.createElement('div');
        this.panelContainer.style.marginBottom = '5px';
        //Title element
        this.titleEl.className = 'mobile-pane-group-title';
        this.setKey(groupName);
        this.wrapperEl.appendChild(this.titleEl);

        this.toggleIcon = document.createElement('i');
        this.toggleIcon.className = 'fas fa-chevron-circle-right mobile-pane-group-toggle-icon';

        this.visible = true;
        this.val = 'group';
        if (!parent)
        {
            paneEl.appendChild(this.wrapperEl);
            paneEl.appendChild(this.panelContainer);
        }
        else
        {
            this.wrapperEl.appendChild(this.toggleIcon);
            parent.panelContainer.appendChild(this.wrapperEl);
            parent.panelContainer.appendChild(this.panelContainer);
        }
        this.isCollapsed = isCollapsed;
        this.children = [];
        this.parent = parent;
        if ((!this.isCollapsed && this.parent) || !parent)
        {
            this.show();
        }
        else
        {
            this.hide();
        }

        $(this.wrapperEl).click(e =>
        {
            e.stopPropagation();
            if (!this.parent)
            {
                return;
            }
            if (this.isCollapsed)
            {
                this.expand();
            }
            else
            {
                this.collapse();
            }
        });

        if (parent)
        {
            parent.addChild(this);
        }
    }

    hasChild(child: MobilePaneItem)
    {
        let isChild = false;
        for (let i = 0; i < this.children.length; i++)
        {
            if (this.children[i] instanceof MobileGroup)
            {
                let childGroup = this.children[i] as MobileGroup;
                isChild = childGroup.hasChild(child);
                if (isChild)
                {
                    return true;
                }
            }
            else
            {
                isChild = this.children[i] === child;
                if (isChild)
                {
                    return true;
                }
            }
        }
        return isChild;
    }

    setValue(val)
    {
    }

    setKey(key)
    {
        this.key = key.toString();
        this.titleEl.textContent = key;
    }

    addChild(child: MobilePaneItem)
    {
        this.children.push(child);
    }

    collapse(hideSelf: boolean = false)
    {
        this.isCollapsed = true;
        $(this.panelContainer).hide();
        $(this.toggleIcon).removeClass('fa-chevron-circle-down');
        $(this.toggleIcon).addClass('fa-chevron-circle-right');
        for (let i = 0; i < this.children.length; i++)
        {
            this.children[i].hide();
            if (this.children[i] instanceof MobileGroup)
            {
                let group = this.children[i] as MobileGroup;
                if (group.isCollapsed)
                {
                    group.collapse();
                }
            }
        }
        if (hideSelf)
        {
            this.hide();
        }
    }

    expand(showSelf: boolean = false)
    {
        if (showSelf)
        {
            this.show();
        }
        this.isCollapsed = false;
        $(this.panelContainer).show();
        $(this.toggleIcon).removeClass('fa-chevron-circle-right');
        $(this.toggleIcon).addClass('fa-chevron-circle-down');
        for (let i = 0; i < this.children.length; i++)
        {
            if (this.children[i].visible)
            {
                this.children[i].show();
            }
            if (this.children[i] instanceof MobileGroup && this.children[i].visible)
            {
                let group = this.children[i] as MobileGroup;
                if (!group.isCollapsed)
                {
                    group.expand();
                }
            }
        }
    }

    clear ()
    {
        $(this.panelContainer).empty();
        for (let i = 0; i < this.children.length; i++)
        {
            if (this.children[i] instanceof MobileGroup)
            {
                let childGroup = this.children[i] as MobileGroup
                childGroup.clear();
            }
        }
        this.children = [];
    }

}

export class MobileInput extends MobilePaneItem
{
    valueEl: HTMLElement
    path: string[]
    parent: MobileGroup
    level: number
    client: VSWebClient
    callback: any
    suffix: string
    unit: string
    isNumber: boolean = false

    constructor(paneEl: HTMLElement, source: string, path: string[], title: string, parent: MobileGroup, level: number, readOnly:boolean, client: VSWebClient, callback?: any)
    {
        super(paneEl, readOnly, client);
        this.path = path;
        this.parent = parent;
        this.level = level;
        this.source = source;
        this.client = client;
        this.callback = callback;
        this.wrapperEl.className = "mobile-pane-input-wrapper";
        this.wrapperEl.style.marginLeft = (level * 10) + 'px';
        if (!parent)
        {
            paneEl.appendChild(this.wrapperEl);
        }
        else
        {
            parent.panelContainer.appendChild(this.wrapperEl);
        }
        //Title element
        this.titleEl.className = 'mobile-pane-input-title';
        this.setKey(title);
        this.wrapperEl.appendChild(this.titleEl);
        //Input;
        this.valueEl = document.createElement('div');
        this.valueEl.contentEditable = (!this.readOnly).toString();
        if (!this.readOnly)
        {
            this.valueEl.style.border = '1px solid rgb(169, 169, 169)';
            this.valueEl.style.padding = '2px';
        }
        this.valueEl.className = 'mobile-pane-input-val';
        this.wrapperEl.appendChild(this.valueEl);

        if (!this.parent || !this.parent.isCollapsed)
        {
            this.show();
        }
        else
        {
            this.hide();
        }

        if (this.path || this.callback)
        {
            this.valueEl.addEventListener('keyup', ev =>
            {
                ev.preventDefault();
                ev.stopPropagation();
            })
            this.valueEl.addEventListener('change', ev =>
            {
                if (this.unit)
                {
                    this.valueEl.textContent += this.unit === '' ? this.unit : ' ' + this.unit;
                }
                this.fireChange();
            });
            this.valueEl.addEventListener('blur', ev =>
            {
                if (this.unit)
                {
                    this.valueEl.textContent += this.unit === '' ? this.unit : ' ' + this.unit;
                }
                this.fireChange();
            });
            this.valueEl.addEventListener('focus', ev =>
            {
                if (this.suffix)
                {
                    this.valueEl.textContent = this.val;
                }
                else if (this.unit || this.isNumber)
                {
                    this.valueEl.textContent = parseFloat(this.val).toString();
                }
            });
        }
        else
        {
            this.visible = true;
            this.val = "none";
        }
        if (parent)
        {
            parent.addChild(this);
        }
    }

    fireChange()
    {
        if (this.callback)
        {
            this.callback(this.valueEl.textContent);
        }
        else
        {
            let val = (this.suffix && this.suffix === '%') ? (parseFloat(this.valueEl.textContent) / 100).toString() : this.valueEl.textContent;
            let xml = XMLParser.serializeWithValue(this.path, val);
            if (this.source !== 'setting')
            {
                xml = createSettingsPath(xml, this.source);
            }
            else
            {
                if ($('#mobile-settings-pane-default-toggle').val() === 'true'
                    && (this.paneEl.parentElement.style.left === '0px' || this.paneEl.parentElement.style.right === '0px'))
                {
                    xml = '<Call Method="SetSettings">' + xml + '</Call>';
                }
                else
                {
                    xml = '<Call Method="SetSceneSettings">' + xml + '</Call>';
                }
            }
            this.client.command.executeXML(xml.replace(/\n<\//, "</"));
            if (this.path.indexOf('ClashDetection') !== -1)
            {
                this.client.emit('ClashVisualizationChanged');
            }
        }
    }

    setValue(val)
    {
        this.val = val;
        let displayVal = val;
        if (this.suffix && this.valueEl !== document.activeElement && !this.unit)
        {
            displayVal = this.suffix === '%' ? displayVal * 100 : displayVal;
            this.val = displayVal;
            displayVal += this.suffix === '' ? this.suffix : ' ' + this.suffix;
        }
        this.valueEl.textContent = displayVal;
    }

    setKey(key)
    {
        this.key = key.toString();
        this.titleEl.textContent = key;
    }

    setUnit (unit: string)
    {
        this.unit = unit;
    }

}

export class MobileFlag
{
    stateString: string = "";
    options: MobileFlagOption[] = [];
    path: string[];
    client: VSWebClient;
    paneEl: HTMLElement;
    constructor (path: string[], client: VSWebClient, paneEl: HTMLElement)
    {
        this.path = path;
        this.client = client;
        this.paneEl = paneEl;
    }

    addOption (option: MobileFlagOption)
    {
        this.options.push(option);
    }

    setOptions (string: String)
    {
        let stringParts: string[];
        if (string === undefined || string === null || string === '')
        {
            stringParts = [''];
        }
        else
        {
            stringParts = string.split(' ');
        }
        this.options.forEach((option: MobileFlagOption) =>
        {
            if (stringParts.indexOf(option.optionVal) === -1)
            {
                option.setValue('false');
            }
            else
            {
                option.setValue('true');
            }
        });
    }

    triggerChange ()
    {
        this.stateString = "";
        for (let i = 0; i < this.options.length; i++)
        {
            this.stateString += this.options[i].val ? this.options[i].optionVal : "";
            if (i !== this.options.length - 1)
            {
                this.stateString += " ";
            }
        }
        let xml = XMLParser.serializeWithValue(this.path, this.stateString);
        if ($('#mobile-settings-pane-default-toggle').val() === 'true'
            && (this.paneEl.parentElement.style.left === '0px' || this.paneEl.parentElement.style.right === '0px'))
        {
            xml = '<Call Method="SetSettings">' + xml + '</Call>';
        }
        else
        {
            xml = '<Call Method="SetSceneSettings">' + xml + '</Call>';
        }
        this.client.command.executeXML(xml);
    }
}

export class MobileFlagOption extends MobilePaneItem
{
    boxEl: HTMLElement
    handleEl: HTMLElement
    parent: MobileGroup
    flag: MobileFlag
    level: number
    hiddenCheckbox: HTMLInputElement
    optionVal: string

    constructor(paneEl: HTMLElement, optionVal: string, title: string, parent: MobileGroup, level: number, readOnly: boolean, client: VSWebClient, flag: MobileFlag)
    {
        super(paneEl, readOnly, client);
        this.parent = parent;
        this.level = level;
        this.flag = flag;
        this.val = false;
        this.optionVal = optionVal;
        this.wrapperEl.className = "mobile-pane-switch-wrapper";
        this.wrapperEl.style.marginLeft = (level * 10) + 'px';
        if (!parent)
        {
            paneEl.appendChild(this.wrapperEl);
        }
        else
        {
            parent.panelContainer.appendChild(this.wrapperEl);
        }
        //Hidden checkbox
        this.hiddenCheckbox = document.createElement('input');
        this.hiddenCheckbox.type = 'checkbox';
        this.hiddenCheckbox.value = this.val;
        this.hiddenCheckbox.style.display = 'none';
        this.wrapperEl.appendChild(this.hiddenCheckbox);
        //Title element
        this.titleEl.className = 'mobile-pane-switch-title';
        this.setKey(title);
        this.wrapperEl.appendChild(this.titleEl);
        //Box element
        this.boxEl = document.createElement('div');
        this.boxEl.className = 'switch-wrapper switch-on mobile-switch';
        this.wrapperEl.appendChild(this.boxEl);
        //Handle element
        this.handleEl = document.createElement('div')
        this.handleEl.className = 'switch-handle';
        this.boxEl.appendChild(this.handleEl);

        $(this.boxEl).click(e =>
        {
            this.setValue(!this.val);
            this.flag.triggerChange();
        });

        if (!this.parent || !this.parent.isCollapsed)
        {
            this.show();
        }
        else
        {
            this.hide();
        }

        if (parent)
        {
            parent.addChild(this);
        }
    }

    setValue(val)
    {
        this.val = (val === 'true' || val === true);
        this.hiddenCheckbox.value = this.val;
        if (this.val)
        {
            $(this.boxEl).removeClass('switch-off').addClass('switch-on');
        }
        else
        {
            $(this.boxEl).removeClass('switch-on').addClass('switch-off');
        }
    }

    setKey(key)
    {
        this.key = key.toString();
        this.titleEl.textContent = key;
    }
}

export class MobileSwitch extends MobilePaneItem
{
    boxEl: HTMLElement
    handleEl: HTMLElement
    path: string[]
    parent: MobileGroup
    level: number
    hiddenCheckbox: HTMLInputElement

    constructor(paneEl: HTMLElement, source: string, path: string[], title: string, parent: MobileGroup, level: number, readOnly: boolean, client: VSWebClient, callback?)
    {
        super(paneEl, readOnly, client);
        this.path = path;
        this.parent = parent;
        this.level = level;
        this.source = source;
        this.wrapperEl.className = "mobile-pane-switch-wrapper";
        this.wrapperEl.style.marginLeft = (level * 10) + 'px';
        if (!parent)
        {
            paneEl.appendChild(this.wrapperEl);
        }
        else
        {
            parent.panelContainer.appendChild(this.wrapperEl);
        }
        //Hidden checkbox
        this.hiddenCheckbox = document.createElement('input');
        this.hiddenCheckbox.type = 'checkbox';
        this.hiddenCheckbox.value = this.val;
        this.hiddenCheckbox.style.display = 'none';
        this.wrapperEl.appendChild(this.hiddenCheckbox);
        //Title element
        this.titleEl.className = 'mobile-pane-switch-title';
        this.setKey(title);
        this.wrapperEl.appendChild(this.titleEl);
        //Box element
        this.boxEl = document.createElement('div');
        this.boxEl.className = 'switch-wrapper switch-on mobile-switch';
        this.wrapperEl.appendChild(this.boxEl);
        //Handle element
        this.handleEl = document.createElement('div')
        this.handleEl.className = 'switch-handle';
        this.boxEl.appendChild(this.handleEl);

        $(this.boxEl).click(e =>
        {
            if (callback)
            {
                callback(this.val);
                return;
            }
            if (path)
            {
                let val = this.val ? 'false' : 'true';
                let xml = XMLParser.serializeWithValue(this.path, val);
                if (this.source !== 'setting')
                {
                    xml = createSettingsPath(xml, this.source);
                }
                else
                {
                    if ($('#mobile-settings-pane-default-toggle').val() === 'true'
                        && (this.paneEl.parentElement.style.left === '0px' || this.paneEl.parentElement.style.right === '0px'))
                    {
                        xml = '<Call Method="SetSettings">' + xml + '</Call>';
                    }
                    else
                    {
                        xml = '<Call Method="SetSceneSettings">' + xml + '</Call>';
                    }
                }
                client.command.executeXML(xml);
                if (this.path.indexOf('ClashDetection') !== -1)
                {
                    client.emit('ClashVisualizationChanged');
                }
            }
            else
            {
                this.setValue(!this.val + '');
            }
        });

        if (!this.parent || !this.parent.isCollapsed)
        {
            this.show();
        }
        else
        {
            this.hide();
        }

        if (parent)
        {
            parent.addChild(this);
        }
    }

    setValue(val)
    {
        this.val = (val === 'true');
        this.hiddenCheckbox.value = this.val;
        if (this.val)
        {
            $(this.boxEl).removeClass('switch-off').addClass('switch-on');
        }
        else
        {
            $(this.boxEl).removeClass('switch-on').addClass('switch-off');
        }

    }

    setKey(key)
    {
        this.key = key.toString();
        this.titleEl.textContent = key;
    }

}


export class MobileSelect extends MobilePaneItem
{
    valueEl: HTMLSelectElement
    path: string[]
    parent: MobileGroup
    level: number
    source: string
    options: any[]

    constructor(paneEl: HTMLElement, source: string, path: string[], title: string, parent: MobileGroup, level: number, readOnly: boolean, options: any[], client: VSWebClient, callback?)
    {
        super(paneEl, readOnly, client);
        this.path = path;
        this.parent = parent;
        this.level = level;
        this.source = source;
        this.wrapperEl.className = "mobile-pane-select-wrapper";
        this.wrapperEl.style.marginLeft = (level * 10) + 'px';
        if (!parent)
        {
            paneEl.appendChild(this.wrapperEl);
        }
        else
        {
            parent.panelContainer.appendChild(this.wrapperEl);
        }
        //Title element
        this.titleEl.className = 'mobile-pane-select-title';
        this.setKey(title);
        this.wrapperEl.appendChild(this.titleEl);
        //Input
        this.valueEl = document.createElement('select');
        this.valueEl.className = 'mobile-pane-select-val';
        if (options && options.length > 0)
        {
            this.addOptions(options);
        }
        this.wrapperEl.appendChild(this.valueEl);

        if (!this.parent || !this.parent.isCollapsed)
        {
            this.show();
        }
        else
        {
            this.hide();
        }

        this.valueEl.addEventListener('change', ev =>
        {
            let val = this.valueEl.value;
            if (callback)
            {
                callback(val);
                return;
            }
            if (this.valueEl.id === 'UID_PRESET')
            {
                let xml = '<Event Type="NodePropertiesChanged"><PhysicalProperties><Density>';
                xml += val;
                xml += '</Density></PhysicalProperties></Event>';
                client.command.executeXML(xml);
                return;
            }
            let xml = XMLParser.serializeWithValue(this.path, val);
            if (this.source !== 'setting')
            {
                if (this.path[1] === 'Preset' && val !== 'USERDEF')
                {
                    let xml = '<Call Method="ActivatePreset"><Type>WallthicknessHeatmap</Type>';
                    xml += '<Identifier>' + val + '</Identifier></Call>';
                    client.command.executeXML(xml);
                    client.on('ActivatePreset', ev =>
                    {
                        client.command.executeXML('<Call Method="GetSceneSettings"></Call>');
                    })
                }
                xml = createSettingsPath(xml, this.source);
            }
            else
            {
                if ($('#mobile-settings-pane-default-toggle').val() === 'true'
                    && (this.paneEl.parentElement.style.left === '0px' || this.paneEl.parentElement.style.right === '0px'))
                {
                    xml = '<Call Method="SetSettings">' + xml + '</Call>';
                }
                else
                {
                    xml = '<Call Method="SetSceneSettings">' + xml + '</Call>';
                }
            }
            client.command.executeXML(xml);
            if (this.path.indexOf('ClashDetection') !== -1)
            {
                client.emit('ClashVisualizationChanged');
            }
        });

        if (parent)
        {
            parent.addChild(this);
        }
    }

    clearOptions ()
    {
        this.options = [];
        $(this.valueEl).empty();
    }

    addOptions (options: any[])
    {
        this.options = options;
        for (let i = 0; i < options.length; i++) {
            let optionEl = document.createElement('option');
            optionEl.value = options[i].value;
            optionEl.textContent = options[i].display;
            this.valueEl.appendChild(optionEl);
		}
	}

    setValue(val)
    {
        for (let i = 0; i < this.options.length; i++)
        {
            if (this.options[i].value === val)
            {
                this.val = this.options[i].value;
                this.valueEl.value = val;
                break;
            }
        }
    }

    setKey(key)
    {
        this.key = key.toString();
        this.titleEl.textContent = key;
    }

}

