import { settings } from '../../app/app-settings';
import { VSWebClient } from '../../base/vsweb-client-impl';
import { MobileRibbon } from '../ribbon/index';
import { RibbonAdapter } from './adapter';
import { appStates } from '../../app/app-states';
import { handleActivateInteraction } from '../../ribbon/menu';
import * as ui from '../app/ui';

const SVG_NS = 'http://www.w3.org/2000/svg';
const XLINK_NS = 'http://www.w3.org/1999/xlink';

declare var _lang_data: any;
declare var _custom_lang_data: any;
declare var _MobileRightMoreBadge: string[];
declare var _DisabledPanes: string[];
declare var vsClient: VSWebClient;

export type ClickCallback = (client: VSWebClient, state: any, adapter: RibbonAdapter) => void

export class RibbonButton
{
    label: HTMLElement
    icon: SVGSVGElement
    el: HTMLElement
    id: string
    UID: string
    context: any
    target: HTMLElement
    adapter: RibbonAdapter
    state: any
    shown: boolean
    interaction: string
    constructor(UID: string, id: string, target: HTMLElement, context:any, adapter: RibbonAdapter)
    {
        this.el = document.createElement('div');
        this.el.id = id;
        this.el.classList.add('mobile-ribbon-button');
        this.context = context;
        this.target = target;
        this.id = id;
        this.UID = UID;
        this.adapter = adapter;
        this.state = { active: false, level: adapter.levelArr.length + 1}
        this.shown = false;
        this.interaction = context.interaction;

        let innerWrapper = document.createElement('div');
        innerWrapper.classList.add('mobile-ribbon-button-wrapper');

        this.label = document.createElement('div');
        this.label.classList.add('mobile-ribbon-button-text');

        if (this.context.iconId !== '')
        {
            this.el.style.padding = '6px';
            if (this.context.children)
            {
                let chevronUp = document.createElement('i');
                chevronUp.className = 'fas fa-chevron-up';
                this.el.appendChild(chevronUp);
                this.el.style.paddingTop = '4px';
            }
            else
            {
                this.label.style.paddingTop = '10px';
                innerWrapper.style.paddingTop = '2px';
            }
            this.icon = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
            this.icon.setAttribute('class', 'mobile-ribbon-button-icon');

            let useEl = document.createElementNS(SVG_NS, 'use')
            useEl.setAttributeNS(XLINK_NS, 'href', '#' + this.context.iconId)
            this.icon.appendChild(useEl)
            this.label.style.display = 'none'
            innerWrapper.appendChild(this.icon);
        }
        this.el.appendChild(this.label);
        this.el.appendChild(innerWrapper);

        this.addClick(this.state, context.method);
        if (this.target)
        {
            this.target.appendChild(this.el);
        }
    }

    showText()
    {
        if (this.context.iconId !== '')
        {
            this.label.style.display = '';
            this.icon.style.height = '20px';
            this.el.style.paddingBottom = '6px';
            if (!this.context.children)
            {
                this.el.style.paddingTop = '6px';
            }
        }
    }

    hideText()
    {
        if (this.context.iconId !== '')
        {
            this.label.style.display = 'none';
            this.icon.style.height = '30px';
            this.el.style.paddingBottom = '9px';
            if (!this.context.children)
            {
                this.el.style.paddingTop = '12px';
            }
        }
    }

    setText()
    {
        let langId = this.context.langId.replace('__N', '');
        if (_lang_data[langId] && _lang_data[langId][settings.lang])
        {
            return _lang_data[langId][settings.lang];
        }
        else if (_custom_lang_data[langId] && _custom_lang_data[langId][settings.lang])
        {
            return _custom_lang_data[langId][settings.lang];
        }
        else if (_lang_data[langId] && _lang_data[langId]['ENU'])
        {
            return _lang_data[langId]['ENU'];
        }
        else if (_custom_lang_data[langId] && _custom_lang_data[langId]['ENU'])
        {
            return _custom_lang_data[langId]['ENU'];
        }
        return langId;
    }

    render(target:HTMLElement)
    {
        this.label.textContent = this.setText();
        if (this.context.interactionClass)
        {
            switch (this.context.interactionClass)
            {
                case 'Interaction':
                    if (appStates.interactionMode.indexOf(this.context.interaction) !== -1)
                    {
                        this.highlight(true);
                    }
                    break;
                case 'Select':
                    if (appStates.selectionMode === this.context.interaction)
                    {
                        this.highlight(true);
                    }
                    else if (this.shown)
                    {
                        this.highlight(false)
                    }
                    break;
                case 'LMB':
                    if (appStates.mouseLeftButtonMode === this.context.interaction)
                    {
                        this.highlight(true);
                    }
                    else if (this.shown)
                    {
                        this.highlight(false)
                    }
                    break;
                case 'RenderMode':
                    if (appStates.renderingModes.indexOf(this.context.interaction) !== -1)
                    {
                        this.highlight(true);
                    }
                    else
                    {
                        this.highlight(false);
                    }
                    break;
            }

        }
        //Check if button has to respresent a scene settings state (outside of the appStates)
        this.checkSettings(true);
        this.shown = true;
        target.appendChild(this.el);
        this.target = target;
    }

    checkSettings(forceHighlight)
    {
        if (ui.paneController && ui.paneController.sceneSettings)
        {
            let dimensioning: boolean = ui.paneController.sceneSettings.Scene.Tools.Dimensioning.Dimensioning === 'true';
            let grid: boolean = ui.paneController.sceneSettings.Scene.Visualization.ShowGrid === 'true';
            let arcMode: boolean = ui.paneController.sceneSettings.Scene.Tools.CircleSelection.SelectCircleWith3Points === 'false';
            let selectAssociatedNode: boolean = ui.paneController.sceneSettings.Scene.Tools.Callout.SelectAssociatedNodes === 'true';
            let multiAnchors: boolean = ui.paneController.sceneSettings.Scene.Tools.Callout.MultiAnchorGroups === 'true';
            let freeRotateMouseMode: boolean = ui.paneController.sceneSettings.Scene.Mouse.UseFreeRotate === 'true';
            let shouldAnimate: boolean = ui.paneController.sceneSettings.Scene.General.Animation === 'true';
            if (this.id === 'ID_RIBBON_MEASUREMENT__N')
            {
                this.setSettingDependentState(!dimensioning, forceHighlight);
            }
            else if (this.id === 'ID_RIBBON_DIMENSION')
            {
                this.setSettingDependentState(dimensioning, forceHighlight);
            }
            else if (this.id === 'rGrid')
            {
                this.setSettingDependentState(grid, forceHighlight);
            }
            else if (this.id === 'ID_RIBBON_ARC')
            {
                this.setSettingDependentState(arcMode, forceHighlight);
            }
            else if (this.id === 'ID_RIBBON_3_POINTS')
            {
                this.setSettingDependentState(!arcMode, forceHighlight);
            }
            else if (this.id === 'ID_RIBBON_CALLOUTS_SELECT_ASSOCIATED_NODES')
            {
                this.setSettingDependentState(selectAssociatedNode, forceHighlight);
            }
            else if (this.id === 'ID_RIBBON_CALLOUTS_MULTIPLE_CONNECTIONS')
            {
                this.setSettingDependentState(multiAnchors, forceHighlight);
            }
            else if (this.id === 'ID_MOUSE_USE_FREE_ROTATE')
            {
                this.setSettingDependentState(freeRotateMouseMode, forceHighlight);
            }
            else if (this.id === 'ID_RIBBON_ANIMATE' && this.UID === 'rAnimationStart')
            {
                this.setSettingDependentState(shouldAnimate, forceHighlight);
            }
        }
    }

    setSettingDependentState(bool, forceHighlight)
    {
        this.state.active = bool;
        this.state.settingsDependent = true;
        if (forceHighlight)
        {
            this.highlight(bool);
        }
    }

    destroy()
    {
        this.shown = false;
        $(this.el).remove();
    }

    update()
    {
        this.label.textContent = this.setText();
    }

    addClick(state: any, clickCallback: ClickCallback)
    {
        this.el.addEventListener('click', ev =>
        {
            if (clickCallback)
            {
                clickCallback(vsClient, state, this.adapter);
                this.adapter.closeHigherLevel(this.state.level);
            }
            else
            {
                if (this.context.children)
                {
                    let newRow = [];
                    let row = this.adapter.createRow(this.context.children, newRow, this);
                    this.adapter.registerRow(newRow, row)
                }
            }
            if (this.interaction && this.context.interactionClass === 'Interaction')
            {
                this.adapter.handleActivateInteraction(this.interaction);
            }
            else if (this.interaction && this.context.interactionClass === 'LMB')
            {
                let xml = '<Call Method="SetSceneSettings"><Settings><Scene><Mouse><LeftButtonFunction>';
                xml += this.interaction;
                xml += '</LeftButtonFunction></Mouse></Scene></Settings></Call>';
                vsClient.command.executeXML(xml);
            }
            else if (this.interaction && this.context.interactionClass === 'Select')
            {
                var xml = '<Call Method="SetSelectionMode"><SelectionMode>' + this.interaction + '</SelectionMode></Call>';
                vsClient.command.executeXML(xml);
            }
            if (this.context.paneId)
            {
                let paneId = ui.paneController.mapPaneTemplateToId(this.context.paneId);
                let hiddenPane = false;
                try
                {
                    if (_DisabledPanes.indexOf(paneId) !== -1)
                    {
                        hiddenPane = true;
                    }
                }
                catch (e) { }
                let wasDeleted = false;
                if (!hiddenPane)
                {
                    wasDeleted = ui.togglePaneBadge(this.context.paneId);
                }
                if (wasDeleted)
                {
                    try
                    {
                        if (_MobileRightMoreBadge.indexOf(paneId) !== -1)
                        {
                            ui.paneController.render(paneId, 'right');
                        }
                        else
                        {
                            ui.paneController.render(paneId, 'left');
                        }
                    }
                    catch (e)
                    {
                        ui.paneController.render(paneId, 'left');
                    }
                }
                if (this.context.paneId !== 'Custom_AppSettings')
                {
                    if (this.context.paneId === 'ClashPane' && !wasDeleted)
                    {
                        let clashCloseCall = '<Call Method="DeactivateClash"></Call>';
                        vsClient.command.executeXML(clashCloseCall);
                    }
                    this.highlight(wasDeleted);
                }
                else
                {
                    $(this.el).addClass('ribbon-active');
                    setTimeout(() =>
                    {
                        $(this.el).removeClass('ribbon-active');
                    }, 200);
                }
            }
            this.context.ribbon.emit('button-clicked', this)
        })
    }

    highlight(bool)
    {
        if (this.context.interaction || this.context.paneId)
        {
            this.state.active = bool;
        }
        if (bool === true)
        {
            if (this.context.children)
            {
                $(this.el).addClass('ribbon-parent-active');
            }
            else
            {
                $(this.el).addClass('ribbon-active');
                if (!this.context.interaction && !this.state.active)
                {
                    setTimeout(() =>
                    {
                        if (!this.state.active)
                        {
                            $(this.el).removeClass('ribbon-active');
                        }
                    }, 200);
                }
            }
        }
        else
        {
            if (this.context.children)
            {
                $(this.el).removeClass('ribbon-parent-active');
            }
            else
            {
                $(this.el).removeClass('ribbon-active');
            }
        }
    }

}


