import { VSWebClient, Options } from '../../base/vsweb-client-impl'
import * as ui from './ui'
import * as js2xmlparser from 'js2xmlparser'
import { appStates } from '../../app/app-states'
import XMLParser = require('../../base/DOMXMLParser')
import { StatusRow } from './ui';
import { TreeInfo } from '../pane/templates/wv-modeltree';
import { SplitSession } from '../../app-panes/split-pane'

declare var vsClient: VSWebClient

declare var _UseCachedSettings: boolean
declare var _MobileTouchControl: string
declare var _MobileRightMoreBadge: string[]
declare var _3DReducedSet: string[]
declare var _2DReducedSet: string[]
declare var _DisableSessionRestore: boolean;

declare global
{
    interface Array<T>
    {
        contains(item: T): boolean;
    }
}



//Exports
export let settings: any = {}
export let sceneSettings: any = {}

//Touch
let holdActive: boolean = false;
let hasTouchHandlers: boolean = false;
let dbHandledInTouch: boolean = false;
let hasClicked: boolean = false;
let middleClick: boolean = false;

///////////////////////////////////////////////////////////////////////////////
//  main()    
///////////////////////////////////////////////////////////////////////////////
let wvCanvas = ui.init()
let websocketAddress =  "ws://localhost:8085/ws";
try
{
    websocketAddress = g_sWebSocketAdress;
}
catch (e)
{
    console.warn('Please define a WebSocket address!');
}

//Compute init options for client session
let parkedSession = localStorage.getItem('WVParkedSession');
let client = localStorage.getItem('WVClient');
let connectedToSession: boolean = false;
let initOptions: Options = {};
initOptions.blockOnLoad = true;
let isNewSession: boolean = false;
let clientInitialized: boolean = false;
//Set client if available
if (client && client !== null)
{
    initOptions.testForSessions = true;
    initOptions.client = client;
}
else
{
    initOptions.testForSessions = false;
}
//Connect to parked session after UI change
if (parkedSession && parkedSession !== null)
{
    initOptions.sessionToConnect = parkedSession;
    localStorage.removeItem('WVParkedSession');
    connectedToSession = true;
    vsClient.initialize(wvCanvas, websocketAddress, initOptions);
}
//Test for crashed sessions
else
{
    vsClient.initialize(wvCanvas, websocketAddress, initOptions);
}
$(document.body).addClass('mobile');


vsClient.on('GetSessions', res =>
{
    let disableSessionRestore;
    try
    {
        disableSessionRestore = _DisableSessionRestore;
    }
    catch (e)
    {
        disableSessionRestore = false;
    }
    var queryString = window.location.search.substr(1);
    var kvs = queryString.split('&');
    var query = {};
    for (let kv of kvs)
    {
        let [k, v] = kv.split('=');
        query[k] = v;
    }
    if (typeof query['session'] !== 'undefined')
    {
        return;
    }
    if (res && res.Session && !Array.isArray(res.Session))
    {
        res.Session = [res.Session];
    }
    if (res.Session && res.Session.length > 0 && !disableSessionRestore)
    {
        ui.showSessionRestoreDialog(res.Session);
    }
    else
    {
        vsClient.connect();
        if (appStates.backendToken)
        {
            vsClient.SessionToken( appStates.backendToken);
        }
    }
});

vsClient.on('ConnectionLost', () =>
{
    ui.blockUI('Connection lost.\n Trying to reconnect..');
});

vsClient.on('ConnectionRestored', () =>
{
    ui.unblockUI();
});


vsClient.on('initialized', (response: any) =>
{
    ui.resizeHandler();
    let xml = '<Call Method="GetSettings"></Call>';
    vsClient.command.executeXML(xml);
    var queryString = window.location.search.substr(1);
    var kvs = queryString.split('&');
    var query = {};
    for (let kv of kvs)
    {
        let [k, v] = kv.split('=');
        query[k] = v;
    }

    vsClient.once('NewScene3D', function (response)
    {
        let events = '<Call Method="AddEventListener">';
        events += '<Event>ActivateInteraction</Event>';
        events += '<Event>DeactivateInteraction</Event>';
        events += '<Event>SelectionChanged</Event>';
        events += '<Event>SelectionChangedCustomId</Event>';
        events += '<Event>NodePropertiesChanged</Event></Call>';
        vsClient.command.executeXMLNoMirror(events);

        vsClient.AutoResize();
        openFileHandler(response);
    });

    if (typeof query['session'] !== 'undefined')
    {
        vsClient.initOptions.sessionToConnect = query['session'];
        connectedToSession = true;
        vsClient.connect();
    }
    if (typeof query['token'] !== 'undefined')
    {
        appStates.backendToken = decodeURIComponent(query['token']);
        vsClient.on('UpdateScene', function (response)
        {
            handleVisShareOpen(response);
        });
        if (!client)
        {
            vsClient.SessionToken( appStates.backendToken);
        }
        else
        {
            vsClient.sessionInfo.token = appStates.backendToken;
        }
        vsClient.on('OpenFile', function (response)
        {
            handleVisShareOpen(response);
            let events = '<Call Method="AddEventListener">'
            events += '<Event>ActivateInteraction</Event>'
            events += '<Event>DeactivateInteraction</Event>'
            events += '<Event>SelectionChanged</Event>'
            events += '<Event>SelectionChangedCustomId</Event>'
            events += '<Event>NodePropertiesChanged</Event></Call>'

            vsClient.command.executeXMLNoMirror(events)

            vsClient.AutoResize()

            openFileHandler(response)
        });

        vsClient.on('SessionInitialized', res =>
        {
            setSessionInfo(res);
            prepareApp(true);
        });

        vsClient.on('VisShareSave', res =>
        {
            let shareBackendCall = {
                Method: 'ShareSave',
                Token: vsClient.sessionInfo.token
            };
            vsClient.command.executeXML(JSON.stringify(shareBackendCall));
            ui.blockUI();
            vsClient.once('SaveAs', res =>
            {
                ui.unblockUI();
            });
        });

        vsClient.on('VisShareSaveAs', res =>
        {
            let shareBackendCall = {
                Method: 'ShareSaveAs',
                Token: vsClient.sessionInfo.token
            };
            vsClient.command.executeXML(JSON.stringify(shareBackendCall));
            ui.blockUI();
            vsClient.once('SaveAs', res =>
            {
                ui.unblockUI();
            });
        });
    }
    else
    {
        ui.ribbon.deleteShareRibbon(true);
        vsClient.on('SessionInitialized', res =>
        {
            setSessionInfo(res);
            prepareApp(false);
        });
    }
});


function setSessionInfo (res)
{
    let session = res.Session;
    let client = res.Client;
    isNewSession = res.NewSession;
    if (client)
    {
        localStorage.setItem('WVClient', client);
    }
    if (session)
    {
        //Retry failed due to timeout or invalid session id
        if (appStates.session && isNewSession)
        {
            let sessionError = {
                ErrorCode: 0,
                ErrorInfo: 'SessionInvalid',
                Identifier: 'SessionInvalid',
                Message: 'Invalid session.',
                Type: ['Event', 'Error']
            };
            vsClient.emit('ShowMessageBox', sessionError);
            vsClient.vsCanvas.setDefaultImage();
        }
        appStates.session = session;
    }
}

function mapLanguage (browserLang: string)
{
    let country = browserLang.split('-')[0].toLowerCase();
    switch (country)
    {
        case 'en':
            return 'ENU';
        case 'de':
            return 'DEU';
        case 'it':
            return 'ITA';
        case 'es':
            return 'ESP';
        case 'fr':
            return 'FRA';
        case 'ko':
            return 'KOR';
        case 'zh':
            return 'CHS';
        case 'ja':
            return 'JPN';
        default:
            return 'ENU';
    }
}

function prepareApp(isBackendMode)
{
    if (clientInitialized)
    {
        return;
    }
    clientInitialized = true;
    //Compute language
    let lang = localStorage.getItem('WVLang');
    if (!lang)
    {
        lang = mapLanguage(navigator.language);
    }
    let xml = '<Call Method="SetSettings"><Settings><Renderer><General><Language>' + lang + '</Language></General></Renderer></Settings></Call>';
    vsClient.command.executeXML(xml);
    vsClient.emit('@changeLang', lang);
    let GetSettings = `<Call Method="GetSettings"></Call>`
    vsClient.command.executeXML(GetSettings);
    vsClient.once('GetSettings', (response, xml) =>
    {
        //First GetSettings call to initialize app
        let cachedSettings = localStorage.getItem('VSWVSettings');
        response = removeProp(response, '__text__');
        response = removeProp(response, '__ordinal__');
        delete response.Settings.Renderer.General.Language;
        let version;
        if (response.Settings.Version)
        {
            if (Array.isArray(response.Settings.Version))
            {
                version = response.Settings.Version[0];
            }
            else
            {
                version = response.Settings.Version;
            }
        }
        delete response.Settings.Version;
        let call = js2xmlparser.parse('Settings', response.Settings).replace("<?xml version='1.0'?>", "");
        call = call.replace('<Settings>', '<Settings Version="' + version + '">');
        localStorage.setItem('VSWVDefaultSettings', call);
        if (cachedSettings === null)
        {
            localStorage.setItem('VSWVSettings', call);
        }
        else
        {
            applyCachedSettings();
        }
        vsClient.on('GetSettings', (response, xml) =>
        {
            response = removeProp(response, '__text__');
            response = removeProp(response, '__ordinal__');
            delete response.Settings.Renderer.General.Language;
            let version;
            if (response.Settings.Version)
            {
                if (Array.isArray(response.Settings.Version))
                {
                    version = response.Settings.Version[0];
                }
                else
                {
                    version = response.Settings.Version;
                }
            }
            delete response.Settings.Version;
            let call = js2xmlparser.parse('Settings', response.Settings).replace("<?xml version='1.0'?>", "");
            call = call.replace('<Settings>', '<Settings Version="' + version + '">');
            localStorage.setItem('VSWVSettings', call);
        });
        //Set left mouse mode to rotate for mobile usage
        let mouseXML = '<Call Method="SetSettings"><Settings><Scene><Mouse><LeftButtonFunction>Rotate</LeftButtonFunction></Mouse></Scene></Settings></Call>';
        vsClient.command.executeXML(mouseXML);

        vsClient.on('SetSettings', (e) =>
        {
            let GetSettings = `<Call Method="GetSettings"></Call>`;
            vsClient.command.executeXML(GetSettings);
        })

    })

    vsClient.on('GetSceneSettings', GetSceneSettingsCallback);
    if (isBackendMode === false)
    {
        vsClient.on('OpenFile', openFileHandler)

        if (window.location.search.length > 0)
        {
            interceptQueryString(false)
        }
        else
        {
            ui.createFileOpenBadge();
        }
    }
    else
    {
        interceptQueryString(true);
    }
    if (connectedToSession)
    {
        let type = localStorage.getItem('WVFileType') ? localStorage.getItem('WVFileType') : '3D';
        localStorage.removeItem('WVFileType');
        let visShareSpec = localStorage.getItem('WVVisShareSpec') ? localStorage.getItem('WVVisShareSpec') : '';
        localStorage.removeItem('WVVisShareSpec');
        let fileName = localStorage.getItem('WVFileName') ? localStorage.getItem('WVFileName') : '';
        localStorage.removeItem('WVFileName');
        appStates.fileType = type;
        vsClient.emit('OpenFile', { Error: 'SUCCESS', Type: type, CustomId: visShareSpec, FileName: fileName });
        ui.resizeHandler();
    }

    const RegisterEvents = `
    <Call Method="AddEventListener">
        <Event>MeasurementChanged</Event>
        <Event>TransformChanged</Event>
        <Event>DraftAngleChanged</Event>
        <Event>SectionChanged</Event>
        <Event>ProjectedAreaChanged</Event>
        <Event>TextSearchChanged</Event>
        <Event>NeighbourhoodSearchChanged</Event>
        <Event>ShowMessageBox</Event>
        <Event>ImportFinished</Event>
        <Event>CalloutSelectionChanged</Event>
        <Event>SavedSelectionsChanged</Event>
        <Event>SplitResultUpdated</Event>
        <Event>WallthicknessValuesChanged</Event>
        <Event>ActivateCompare</Event>
        <Event>CalloutAutoAlignmentChanged</Event>
        <Event>BandAnalysis</Event>
        <Event>ClashResultUpdated</Event>
        <Event>ConfigurationViewPlaySelectionChange</Event>
        <Event>PresetChanged</Event>
    </Call>
    `
    vsClient.on('DraftAngleChanged', response =>
    {
        let message = createStatusRow(response);
        ui.openStatusDialog('ID_wv-draftangle', message, 0);
    });

    vsClient.on('MeasurementChanged', response =>
    {
        if (response.Mock)
        {
            return;
        }
        let message = createStatusRow(response);
        ui.openStatusDialog('ID_wv-measurement', message, 0);
    })

    vsClient.on('TransformChanged', response =>
    {
        let transformInteraction = false;
        for (let i = 0; i < appStates.interactionMode.length; i++)
        {
            if (appStates.interactionMode[0].indexOf('Transform') === 0)
            {
                transformInteraction = true;
            }
        }
        if (transformInteraction)
        {
            let message = createStatusRow(response.Position);
            ui.openStatusDialog('ID_wv-transform', message, 0);
        }
    })
    vsClient.command.executeXML(RegisterEvents);
    //Get version
    let versionCall = '<Call Method="GetVersionInfo"></Call>';
    vsClient.command.executeXMLAsync(versionCall);
    vsClient.once("GetVersionInfo", response =>
    {
        let versionArr = response.Version.split('.');
        versionArr.pop();
        appStates.version = versionArr.join('.');
    });
}

function handleVisShareOpen (response)
{
    if (response.CustomId && response.CustomId.indexOf('VSOpen') !== -1)
    {
        appStates.VisShareSpec = response.CustomId;
        if (response.CustomId.indexOf('VSName') !== -1)
        {
            document.title = response.CustomId.split('VSName')[1];;
        }
        let accessRights = response.CustomId.split('VSOpen')[1].split('_')[0];
        if (accessRights === 'Write')
        {
            ui.ribbon.deleteShareRibbon(false);
        }
        else if (accessRights === 'None')
        {
            ui.ribbon.deleteShareRibbon(true);
        }
    }
    else
    {
        ui.ribbon.deleteShareRibbon(true);
    }
}

function createStatusRow(event)
{
    let type = event.__ordinal__;
    let rows: StatusRow[] = [];
    for (let i = 0; i < type.length; i++)
    {
        if (type[i] !== 'Type' && type[i] !== 'ViewId')
        {
            let typeName = type[i];
            let suffix = event[typeName].__ordinal__[0];
            let text = event[typeName].__text__ + " " + event[typeName][suffix];
            let row: StatusRow = {
                key: typeName,
                value: text
            };
            rows.push(row);
        }
    }

    return rows;
}


