vsClient.on('NodePropertiesChanged', function (ev)
{
    if (ev.General)
    {
         //If node type is Markup_Text disable the zoom/select rectangle
         appStates.drawRect = ev.General.Type !== 'Markup_Text';
        //Highlight color in color search results
        let color = ev.General.Color.replace('#', '')
        $('.mobile-filter-color-list-row').each(function ()
        {
            if (this.id === 'ColorSearchResult_' + color)
            {
                $(this).css('background-color', 'rgb(255, 128, 0)')
            }
            else
            {
                $(this).css('background-color', 'white')
            }
        })
    }
    else
    {
        $('.mobile-filter-color-list-row').each(function ()
        {
            $(this).css('background-color', 'white')
        })
    }
})

