function GetSceneSettingsCallback(response, xml)
{
    if (response['Error'] === 'SUCCESS')
    {
        let renderModes = response['Settings']['Scene']['Visualization']['RenderMode'] as string;
        vsClient.emit('RenderMode', renderModes.split(' '));
        sceneSettings = response.Settings;

        let leftMouseButtonMode = response.Settings.Scene.Mouse.LeftButtonFunction;
        let rightMouseButtonMode = response.Settings.Scene.Mouse.RightButtonFunction;
        let middleMouseButtonMode = response.Settings.Scene.Mouse.MiddleButtonFunction;
        appStates.mouseLeftButtonMode = leftMouseButtonMode;
        appStates.mouseRightButtonMode = rightMouseButtonMode;
        appStates.mouseMiddleButtonMode = middleMouseButtonMode;
        vsClient.emit('LMB', leftMouseButtonMode);
    }
}


/**    touch handlers */
function TouchPosCorrectionX(pos)
{
    let rect = wvCanvas.getBoundingClientRect()
    var mouseX = pos - rect.left
    return mouseX
}

function TouchPosCorrectionY(pos)
{
    let rect = wvCanvas.getBoundingClientRect()
    var mouseY = pos - rect.top
    return mouseY
}

function HandleMouseForTouchGestureStart(touchevent)
{
    //initialize
    appStates.lastGestureDistance = touchevent.distance;

    appStates.lastGestureCenterX = TouchPosCorrectionX(touchevent.center.x);
    appStates.lastGestureCenterY = TouchPosCorrectionY(touchevent.center.y);
    //activate pan
    var sCommands = '<Call Method="SetMouseButtonFunctions" Response="false"><LeftMouseButtonFunction>Pan</LeftMouseButtonFunction></Call>';
    //down pan
    sCommands += '<Call Method="MouseDown" Response="false"><X>' + appStates.lastGestureCenterX + '</X><Y>' + appStates.lastGestureCenterY + '</Y><MouseButton>Left</MouseButton></Call>';
    vsClient.command.executeXMLMouse(sCommands);
}

function HandleMouseForTouchGesture(touchevent)
{
    var bSendZoom = false;
    appStates.lastGestureDelta = touchevent.distance - appStates.lastGestureDistance;

    if (Math.abs(appStates.lastGestureDelta) > 10)
    {
        bSendZoom = true;
        appStates.lastGestureDistance = touchevent.distance;
    }

    appStates.lastGestureCenterX = TouchPosCorrectionX(touchevent.center.x);
    appStates.lastGestureCenterY = TouchPosCorrectionY(touchevent.center.y);

    var sMouseButtonDown = '<MouseButton>Left</MouseButton>';
    var callMove = '<Call Method="MouseMove" Response="true"><X>' + appStates.lastGestureCenterX + '</X><Y>' + appStates.lastGestureCenterY + '</Y>' + sMouseButtonDown + '</Call>';

    if (bSendZoom)
    {
        vsClient.command.executeXMLZoomMouseWheel(appStates.lastGestureCenterX, appStates.lastGestureCenterY, appStates.lastGestureDelta)
    }
    else
    {
        vsClient.command.executeXMLMouse(callMove)
    }
}

function HandleMouseForTouchGestureEnd()
{
    //combined touch pan and zoom
    var sCommands = '<Call Method="MouseUp" Response="false"><X>' + appStates.lastGestureCenterX + '</X><Y>' + appStates.lastGestureCenterY + '</Y><MouseButton>Left</MouseButton></Call>';
    //reset mouse mode
    sCommands += '<Call Method="SetMouseButtonFunctions" Response="false"><LeftMouseButtonFunction>' + appStates.mouseLeftButtonMode + '</LeftMouseButtonFunction></Call>';
    vsClient.command.executeXMLMouse(sCommands);
}

function HandleTouchDragStart(touchevent)
{

    if (!appStates.touchGestureActive && !appStates.mouseActive)
    {
        touchevent.preventDefault();

        var mouseX = TouchPosCorrectionX(touchevent.touch.x.client);
        var mouseY = TouchPosCorrectionY(touchevent.touch.y.client);

        if (appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode == 'RectSelect')
        {
            vsClient.startRect(mouseX, mouseY);
        }

        //mouse move before down to simulate hover
        var s = '<Call Method="MouseMove" Response="true"><X>' + mouseX + '</X><Y>' + mouseY + '</Y><MouseButton>None</MouseButton></Call>';
        let mouseButton = 'Left';
        if (appStates.interactionMode.indexOf('Flythrough') !== -1)
        {
            mouseButton = 'Right';
        }
        s += '<Call Method="MouseDown" Response="false"><X>' + mouseX + '</X><Y>' + mouseY + '</Y><MouseButton>' + mouseButton + '</MouseButton></Call>';
        vsClient.command.executeXMLMouse(s);
    }
}

function HandleTouchDrag(touchevent)
{
    if (!appStates.touchGestureActive && !appStates.mouseActive)
    {
        touchevent.preventDefault();

        var mouseX = TouchPosCorrectionX(touchevent.touch.x.client);
        var mouseY = TouchPosCorrectionY(touchevent.touch.y.client);

        var mouseButton = '<MouseButton>Left</MouseButton>';
        if (appStates.interactionMode.indexOf('Flythrough') !== -1)
        {
            mouseButton = '<MouseButton>Right</MouseButton>';
        }
        var callMove = '<Call Method="MouseMove" Response="true"><X>' + mouseX + '</X><Y>' + mouseY + '</Y>' + mouseButton + '</Call>';

        if ((appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode == 'RectSelect') && appStates.drawRect
            && appStates.interactionMode.indexOf('Flythrough') === -1)
        {
            vsClient.drawRect(mouseX, mouseY)
        }
        else
        {
            vsClient.command.executeXMLMouseMove(callMove);
        }
    }
}

function HandleTouchDragEnd(touchevent)
{
    if (!appStates.touchGestureActive && !appStates.mouseActive)
    {
        touchevent.preventDefault();
        var mouseX = TouchPosCorrectionX(touchevent.touch.x.client);
        var mouseY = TouchPosCorrectionY(touchevent.touch.y.client);

        var mouseButton = 'Left';
        if (appStates.interactionMode.indexOf('Flythrough') !== -1)
        {
            mouseButton = 'Right';
        }
        var xml = '<Call Method="MouseUp" Response="false"><X>' + mouseX + '</X><Y>' + mouseY + '</Y><MouseButton>' + mouseButton + '</MouseButton></Call>';
        vsClient.command.executeXMLMouse(xml);
    }
}

/** mouse handlers */
function mousedownHandler(ev: JQueryEventObject)
{
    if (appStates.touchActive)
    {
        return;
    }
    appStates.mouseActive = true;
    hasClicked = true;
    let boundingRect = wvCanvas.getBoundingClientRect();
    let offsetLeft = boundingRect.left;
    let offsetTop = boundingRect.top;

    var mouseX = ev.pageX - offsetLeft;
    var mouseY = ev.pageY - offsetTop;

    var button = 'None';
    switch (ev.which)
    {
        case 1:
            $('#wv-dock').toggleClass('disable-selection');
            appStates.mouseLeftButtonDown = true;
            button = 'Left';
            if (appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode === 'RectSelect')
            {
                vsClient.startRect(mouseX, mouseY);
            }
            break
        case 2:
            button = 'Middle';
            if (appStates.mouseMiddleButtonMode === 'RectZoom' || appStates.mouseMiddleButtonMode === 'RectSelect')
            {
                vsClient.startRect(mouseX, mouseY);
            }
            break
        case 3:
            button = 'Right';
            if (appStates.mouseRightButtonMode === 'RectZoom' || appStates.mouseRightButtonMode === 'RectSelect')
            {
                vsClient.startRect(mouseX, mouseY);
            }
            break
    }



    var xml = '<Call Method="MouseDown"><X>'
        + mouseX + '</X><Y>'
        + mouseY + '</Y>'
        + '<MouseButton>'
        + button + '</MouseButton></Call>'
    vsClient.command.executeXML(xml)
}


function mousemoveHandler(ev: JQueryEventObject)
{
    if (appStates.touchActive)
    {
        return;
    }
    // prevent the inexplicable mousemove event being triggered in some situation when mouse does not move
    if (ev.pageX === appStates.lastPagePosX && ev.pageY === appStates.lastPagePosY)
    {
        return
    }
    appStates.lastPagePosX = ev.pageX
    appStates.lastPagePosY = ev.pageY

    let boundingRect = wvCanvas.getBoundingClientRect()
    let offsetLeft = boundingRect.left
    let offsetTop = boundingRect.top
    var mouseX = ev.pageX - offsetLeft
    var mouseY = ev.pageY - offsetTop
    vsClient.mouseX = mouseX;
    vsClient.mouseY = mouseY;

    var button = 'None'
    switch (ev.which)
    {
        case 1:
            if (ev.button > 1)
            {
                button = 'Middle';
                if ((appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode == 'RectSelect') && appStates.drawRect
                    && appStates.interactionMode.indexOf('Flythrough') === -1)
                {
                    if (!middleClick)
                    {
                        vsClient.startRect(mouseX, mouseY);
                        middleClick = true;
                        var xml = '<Call Method="MouseDown"><X>'
                            + mouseX + '</X><Y>'
                            + mouseY + '</Y>'
                            + '<MouseButton>'
                            + button + '</MouseButton></Call>'

                        vsClient.command.executeXMLNoMirror(xml);
                        return;
                    }
                    vsClient.drawRect(mouseX, mouseY);
                    return;
                }
                break;
            }
            button = 'Left';
            if ((appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode == 'RectSelect')
                && appStates.drawRect && appStates.interactionMode.indexOf('Flythrough') === -1)
            {
                vsClient.drawRect(mouseX, mouseY);
                return;
            }
            break;
        case 2:
            if ((appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode == 'RectSelect') && appStates.drawRect
                && appStates.interactionMode.indexOf('Flythrough') === -1)
            {
                vsClient.drawRect(mouseX, mouseY);
                return;
            }
            button = 'Middle';
            break;
        case 3:
            if ((appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode == 'RectSelect')
                && appStates.drawRect && appStates.interactionMode.indexOf('Flythrough') === -1)
            {
                vsClient.drawRect(mouseX, mouseY);
                return;
            }
            break;
    }

    var xml = '<Call Method="MouseMove" Response="true"><X>'
        + mouseX + '</X><Y>'
        + mouseY + '</Y><MouseButton>'
        + button + '</MouseButton></Call>';

    vsClient.command.executeXMLMouseMove(xml);
}

function mouseupHandler(ev: JQueryEventObject)
{
    if (appStates.touchActive)
    {
        return;
    }
    let boundingRect = wvCanvas.getBoundingClientRect();
    let offsetLeft = boundingRect.left;
    let offsetTop = boundingRect.top;
    var mouseX = ev.pageX - offsetLeft;
    var mouseY = ev.pageY - offsetTop;

    var button = 'None';
    switch (ev.which)
    {
        case 1:
            $('#wv-dock').toggleClass('disable-selection');
            button = 'Left';
            appStates.mouseLeftButtonDown = false;
            break
        case 2:
            button = 'Middle';
            break;
        case 3:
            button = 'Right';
            break;
    }

    var mouseFlag = '';
    if (ev.ctrlKey === true || ui.mockCtrl)
    {
        mouseFlag = '<MouseFlag>Ctrl</MouseFlag>';
    }

    if (middleClick)
    {
        middleClick = false;
        button = 'Middle';
    }

    var xml = '<Call Method="MouseUp" Response="false"><X>'
        + mouseX + '</X><Y>'
        + mouseY + '</Y>'
        + mouseFlag
        + '<MouseButton>' + button + '</MouseButton></Call>'

    vsClient.command.executeXMLMouse(xml);

    appStates.mouseActive = false;
}
function dblclickHandler(ev)
{
    ev.preventDefault()
    ev.stopPropagation()

    let boundingRect = wvCanvas.getBoundingClientRect()
    let offsetLeft = boundingRect.left
    let offsetTop = boundingRect.top
    var mouseX = ev.pageX - offsetLeft
    var mouseY = ev.pageY - offsetTop

    var button = 'None'
    if (ev.which === 1) button = 'Left'

    dbClickMethod(mouseX, mouseY, button);
}
function dbClickMethod(x: number, y: number, button: string)
{
    if (dbHandledInTouch)
    {
        dbHandledInTouch = false;
        return;
    }

    let xml = '<Event Type="NodePropertiesChanged"></Event>';
    vsClient.command.executeXML(xml);
    vsClient.once('NodePropertiesChanged', res =>
    {
        if (res && res.General && res.General.Name === 'MarkupSet')
        {
            let xml = '<Call Method="Undo"><Steps>1</Steps></Call>';
            vsClient.command.executeXML(xml);
            vsClient.once('Undo', () =>
            {
                vsClient.once('NodePropertiesChanged', resp =>
                {
                    createMarkupOverlay(resp, x, y);
                });
            })
        }
        else if (res && res.General && res.General.Name === 'CreatedNotes')
        {
            let xml = '<Call Method="Undo"><Steps>1</Steps></Call>';
            vsClient.command.executeXML(xml);
            vsClient.once('Undo', () =>
            {
                if (res && res.General.Name === 'MarkupSet')
                {
                    let xml = '<Call Method="Undo"><Steps>1</Steps></Call>';
                    vsClient.command.executeXML(xml);
                    vsClient.once('Undo', () =>
                    {
                        vsClient.once('NodePropertiesChanged', response =>
                        {
                            createMarkupOverlay(response, x, y);
                        });
                    })
                }
                else
                {
                    let dbClickXML = '<Call Method="MouseDoubleClick" Response="false"><X>'
                        + x + '</X><Y>'
                        + y + '</Y><MouseButton>'
                        + button + '</MouseButton></Call>'
                    vsClient.command.executeXMLMouse(dbClickXML);
                }
            })
        }
        else if (res && res.Markup)
        {
            createMarkupOverlay(res, x, y);
        }
        else
        {
            let dbClickXML = '<Call Method="MouseDoubleClick" Response="false"><X>'
                + x + '</X><Y>'
                + y + '</Y><MouseButton>'
                + button + '</MouseButton></Call>'
            vsClient.command.executeXMLMouse(dbClickXML);
        }
    })
}

function createMarkupOverlay(res: any, x: number, y: number)
{
    let boundingRect = wvCanvas.getBoundingClientRect()
    let offsetLeft = boundingRect.left
    let offsetTop = boundingRect.top
    let text: string;

    let documentOverlay = document.createElement('div');
    documentOverlay.className = 'mobile-page-overlay';
    let appDiv = document.getElementById('wv-app');
    document.body.appendChild(documentOverlay);

    let markupTextField = document.createElement('div');
    markupTextField.contentEditable = 'true';
    markupTextField.className = 'mobile-editable-markup-overlay';
    let initialText = res.Markup.Text.Content;
    $(markupTextField).html(initialText);
    documentOverlay.appendChild(markupTextField);
    markupTextField.focus();
    window.getSelection().selectAllChildren(markupTextField);
    markupTextField.addEventListener('keydown', (e) =>
    {
        if (e.keyCode === 27)
        {
            let xml = '<Event Type="NodePropertiesChanged"><Markup><Text><Content>' + initialText + '</Content></Text></Markup></Event>';
            vsClient.command.executeXML(xml);
            $(markupTextField).off('blur');
            $(markupTextField).remove();
            $(documentOverlay).remove();
        }
    });
    $(markupTextField).on('blur', e =>
    {
        text = markupTextField.innerText;
        let xml = '<Event Type="NodePropertiesChanged"><Markup><Text><Content>' + text + '</Content></Text></Markup></Event>';
        vsClient.command.executeXML(xml.replace(/\n<\//, "</"));
        $(markupTextField).remove();
        $(documentOverlay).remove();
    });
}

function wheelHandler(ev)
{
    ev.preventDefault()

    let boundingRect = wvCanvas.getBoundingClientRect()
    let offsetLeft = boundingRect.left
    let offsetTop = boundingRect.top
    var mouseX = ev.pageX - offsetLeft
    var mouseY = ev.pageY - offsetTop

    var deltaY = ev.deltaY

    // firefox or not
    var correctedDeltaY = navigator.userAgent.indexOf("Firefox") != -1 ? deltaY * -0.1 : deltaY * -0.005

    var xml = '<Call Method="MouseWheel" Response="false"><X>'
        + mouseX + '</X><Y>' + mouseY + '</Y><Delta>'
        + correctedDeltaY + ' </Delta></Call>'

    vsClient.command.executeXMLMouse(xml);
}

/**    client callbacks */


function applyCachedSettings()
{

    try
    {
        if (_UseCachedSettings)
        {
            let settingsCall = '<Call Method="SetSettings">' + localStorage.getItem('VSWVSettings') + '</Call>';
            vsClient.command.executeXMLNoMirror(settingsCall);
        }
    }
    catch (e)
    {
    }
}

const removeProp = (obj, prop) =>
{
    Object.keys(obj).forEach(key =>
        (key === prop) && delete obj[key] ||
        (obj[key] && typeof obj[key] === 'object') && removeProp(obj[key], prop)
    );
    return obj;
};


function openFileHandler(response: any)
{
    try
    {
        if (response.Type === '3D')
        {
            for (let i = 0; i < _3DReducedSet.length; i++)
            {
                let id = _3DReducedSet[i];
                $('#' + id).hide();
            }
            for (let i = 0; i < _2DReducedSet.length; i++)
            {
                let id = _2DReducedSet[i];
                $('#' + id).show();
            }
        }
        else if (response.Type === '2D')
        {
            for (let i = 0; i < _3DReducedSet.length; i++)
            {
                let id = _3DReducedSet[i];
                $('#' + id).show();
            }
            for (let i = 0; i < _2DReducedSet.length; i++)
            {
                let id = _2DReducedSet[i];
                $('#' + id).hide();
            }
        }
    }
    catch (e) { }
    if (!response.SceneId)
    {
        appStates.fileName = response.FileName;
    }
    if (!(response.Error == "SUCCESS"))
    {
        alert("Error on loading file!");
    }
    else
    {
        $('#mobile-file-open-badge').remove();
        /** scene settings */
        let GetSettings = `<Call Method="GetSceneSettings"></Call>`;
        vsClient.command.executeXML(GetSettings);
        // reset to default
        appStates.reset();
        appStates.fileLoaded = true;
        appStates.fileType = response.Type;
        if (response.Type === '2D')
        {
            const pageCount = `<Call Method="GetPageCount"></Call>`;
            vsClient.command.executeXML(pageCount);
            vsClient.once('GetPageCount', res =>
            {
                if (res.Error !== 'SUCCESS')
                {
                    appStates.pageCount = '';
                } else
                {
                    appStates.pageCount = res.Pages;
                }
                ui.showDocumentBar();
                ui.show2DExtraLayerBadge();
            })
        }
        else
        {
            ui.hideDocumentBar();
            ui.hide2DExtraLayerBadge();
        }
        //Reset split pane
        if (ui.paneController.leftDefaultAdapter.sessions.length > 0)
        {
            ui.paneController.leftDefaultAdapter.sessions = [];
            ui.paneController.leftDefaultAdapter.activeSession = undefined;
            ui.paneController.leftDefaultAdapter.resultInitialized = false;
            $(ui.paneController.leftDefaultAdapter.resultGroup.wrapperEl).remove();
            $(ui.paneController.leftDefaultAdapter.resultGroup.panelContainer).remove();
            $(ui.paneController.leftDefaultAdapter.splitGroupTable).remove();
        }
        else if (ui.paneController.rightDefaultAdapter.sessions.length > 0)
        {
            ui.paneController.rightDefaultAdapter.sessions = [];
            ui.paneController.rightDefaultAdapter.activeSession = undefined;
            ui.paneController.rightDefaultAdapter.resultInitialized = false;
            $(ui.paneController.rightDefaultAdapter.resultGroup.wrapperEl).remove();
            $(ui.paneController.rightDefaultAdapter.resultGroup.panelContainer).remove();
            $(ui.paneController.rightDefaultAdapter.splitGroupTable).remove();
        }

        // tree view
        //const getTreeview = `<Call Method="VirtualTreeViewSetSize"><Size>${maxVisible}</Size></Call>`
        // vsClient.command.executeXMLAsync(getTreeview)

        //Set left mouse mode to the preset for mobile usage
        let leftMouseMode = 'Rotate';
        try
        {
            leftMouseMode = _MobileTouchControl;
        }
        catch(e){}
        let mouseXML = '<Call Method="SetSceneSettings"><Settings><Scene><Mouse><LeftButtonFunction>' + leftMouseMode + '</LeftButtonFunction></Mouse></Scene></Settings></Call>';
        vsClient.command.executeXML(mouseXML);

        //Selection mode
        appStates.selectionMode = 'Select';
        let xml = '<Call Method="SetSelectionMode"><SelectionMode>' + appStates.selectionMode + '</SelectionMode></Call>';
        vsClient.command.executeXML(xml);
        vsClient.emit('Select', 'Select');

        /** configuration view */
        const getConfigurationView = '<Call Method="GetConfigurationViewInformation"><Image>true</Image></Call>'
        vsClient.command.executeXMLAsync(getConfigurationView)

        if (response.Type !== '3D')
        {
            const pageCount = `<Call Method="GetPageCount"></Call>`
            vsClient.command.executeXMLAsync(pageCount)
            vsClient.once('GetPageCount', res =>
            {
                if (res.Error !== 'SUCCESS')
                {
                    appStates.pageCount = ''
                } else
                {
                    appStates.pageCount = res.Pages
                    // find the page input
                }
            })
        }

        // activate the mouse
        $(wvCanvas).off('mousedown').on('mousedown', (e) =>
        {
            mousedownHandler(e);
        })
        $(wvCanvas).off('mousemove').on('mousemove', (e) =>
        {
            mousemoveHandler(e);
        });
        $(wvCanvas).off('mouseup').on('mouseup', (e) =>
        {
            mouseupHandler(e);
        });
        wvCanvas.addEventListener('dblclick', dblclickHandler, false)
        wvCanvas.addEventListener('wheel', wheelHandler, false)


        wvCanvas.ontouchmove = stopDefaultMouseEventPropagation;

        if (!hasTouchHandlers)
        {
            hasTouchHandlers = true;
            $(wvCanvas).kendoTouch({
                doubleTapTimeout: 250,
                multiTouch: true,
                enableSwipe: false,
                touchstart: function (e)
                {
                    if (appStates.mouseActive || (e.event.originalEvent['pointerType'] && e.event.originalEvent['pointerType'] !== 'touch'))
                    {
                        e.sender.cancel();
                    }
                    else
                    {
                        appStates.touchActive = true;
                    }
                },
                gesturestart: function (e)
                {
                    appStates.touchGestureActive = true;
                    HandleMouseForTouchGestureStart(e);
                },
                gesturechange: function (e)
                {
                    HandleMouseForTouchGesture(e);
                },
                gestureend: function (e)
                {
                    e.preventDefault();
                    HandleMouseForTouchGestureEnd();
                },
                drag: function (e)
                {
                    if (holdActive)
                    {
                        TouchHoldMove(e);
                        return;
                    }
                    HandleTouchDrag(e);
                },
                dragstart: function (e)
                {
                    if (holdActive)
                    {
                        TouchHoldMove(e);
                        return;
                    }
                    HandleTouchDragStart(e);
                },
                dragend: function (e)
                {
                    if (holdActive)
                    {
                        e.sender.cancel();
                        holdActive = false;
                        var mouseX = TouchPosCorrectionX(e.touch.x.client);
                        var mouseY = TouchPosCorrectionY(e.touch.y.client);

                        var mouseFlag = '';

                        if (e.event.altKey === true || e.event.ctrlKey === true || ui.mockCtrl)
                        {
                            mouseFlag = '<MouseFlag>Ctrl</MouseFlag>';
                        }

                        //mouse move before down to simulate hover
                        var s = '<Call Method="MouseMove" Response="true"><X>' + mouseX + '</X><Y>' + mouseY + '</Y><MouseButton>None</MouseButton></Call>';
                        s += '<Call Method="MouseUp" Response="false"><X>' + mouseX + '</X><Y>' + mouseY + '</Y>' + mouseFlag + '<MouseButton>Left</MouseButton></Call>';
                        vsClient.command.executeXMLMouse(s);
                        return;
                    }
                    HandleTouchDragEnd(e);
                    appStates.touchActive = false;
                    appStates.touchGestureActive = false;
                },
                hold: function (e)
                {
                    if (appStates.interactionMode.indexOf('Flythrough') !== -1)
                    {
                        TouchHoldMove(e);
                    }
                },
                doubletap: function (e)
                {
                    e.preventDefault();
                    dbHandledInTouch = false;
                    let boundingRect = wvCanvas.getBoundingClientRect();
                    let offsetLeft = boundingRect.left;
                    let offsetTop = boundingRect.top;
                    var mouseX = e.touch.x.location - offsetLeft;
                    var mouseY = e.touch.y.location - offsetTop;

                    var button = 'None';
                    if (e.event.which !== undefined && (e.event.which === 1 || e.event.which === 0))
                    {
                        button = 'Left';
                    }
                    dbClickMethod(mouseX, mouseY, button);
                    dbHandledInTouch = true;
                },
                tap: function (e)
                {
                    if (hasClicked)
                    {
                        hasClicked = false;
                        return;
                    }
                    else
                    {
                        if (ui.paneCloseTouch)
                        {
                            ui.setPaneCloseTouch(false);
                            return;
                        }
                        let boundingRect = wvCanvas.getBoundingClientRect();
                        let offsetLeft = boundingRect.left;
                        let offsetTop = boundingRect.top;

                        var mouseX = e.touch.x.location - offsetLeft;
                        var mouseY = e.touch.y.location - offsetTop;

                        var button = 'Left';
                        $('#wv-dock').toggleClass('disable-selection');
                        appStates.mouseLeftButtonDown = true;
                        if (appStates.mouseLeftButtonMode === 'RectZoom' || appStates.mouseLeftButtonMode === 'RectSelect')
                        {
                            vsClient.startRect(mouseX, mouseY);
                        }

                        let xml = '<Call Method="MouseMove" Response="true"><X>';
                        xml += mouseX;
                        xml += '</X><Y>';
                        xml += mouseY;
                        xml += '</Y><MouseButton>None</MouseButton></Call>';;

                        vsClient.command.executeXMLNoMirror(xml);

                        xml = '<Call Method="MouseDown"><X>';
                        xml += mouseX + '</X><Y>';
                        xml += mouseY + '</Y>';
                        xml += '<MouseButton>';
                        xml += button + '</MouseButton></Call>';

                        vsClient.command.executeXMLNoMirror(xml);

                        xml = '<Call Method="MouseUp"><X>';
                        xml += mouseX + '</X><Y>';
                        xml += mouseY + '</Y>';
                        if (e.event.altKey === true || e.event.ctrlKey === true || ui.mockCtrl)
                        {
                            xml += '<MouseFlag>Ctrl</MouseFlag>';
                        }
                        xml += '<MouseButton>';
                        xml += button + '</MouseButton></Call>';

                        vsClient.command.executeXMLNoMirror(xml);
                    }
                }
            })
        }
    }
}

function TouchHoldMove(e)
{
    if (!appStates.touchActive)
    {
        e.sender.cancel();
        return;
    }

    var mouseX = TouchPosCorrectionX(e.touch.x.client);
    var mouseY = TouchPosCorrectionY(e.touch.y.client);

    var mouseFlag = '';

    if (e.event.altKey === true || e.event.ctrlKey === true || ui.mockCtrl)
    {
        mouseFlag = '<MouseFlag>Ctrl</MouseFlag>';
    }

    //mouse move before down to simulate hover
    var s = '<Call Method="MouseMove" Response="true"><X>' + mouseX + '</X><Y>' + mouseY + '</Y><MouseButton>None</MouseButton></Call>';
    if (!holdActive)
    {
        s += '<Call Method="MouseDown" Response="false"><X>' + mouseX + '</X><Y>' + mouseY + '</Y><MouseButton>Left</MouseButton></Call>';
        holdActive = true;
    }
    vsClient.command.executeXMLMouse(s);
}
//Workaround to enable tap/double tap and kendo touch events (kendo does not provide the 'touchMove' event)
function stopDefaultMouseEventPropagation(e)
{
    e.preventDefault();
    e.stopPropagation();
}



/** Escape onKey for Deactivate_AllInteractions and reset Controls and Selection*/
window.addEventListener("keyup", function (e)
{
    let xmlSelect = ''
    let xml = ''
    switch (e.keyCode)
    {
        case 27:    // Escape
            // Inactivate all Interactions
            let s = '<Call Method="ActivateInteraction"><Interaction>Deactivate_AllInteractions</Interaction></Call>'
            vsClient.command.executeXML(s)
            vsClient.once('ActivateInteraction', (response) =>
            {
            })

            // Reset Controls
            let settings = localStorage.getItem('VSWVSettings');
            if (settings === null)
            {
                settings = localStorage.getItem('VSWVDefaultSettings');
            }
            let settingsObj = XMLParser.parseXML(settings);
            let leftMouseMode = settingsObj.Settings.Scene.Mouse.LeftButtonFunction;
            try
            {
                leftMouseMode = _MobileTouchControl;
            }
            catch (e) { }
            let rightMouseMode = settingsObj.Settings.Scene.Mouse.RightButtonFunction;
            let middleMouseMode = settingsObj.Settings.Scene.Mouse.MiddleButtonFunction;
            let mouseSettingsCall = '<Call Method="SetSceneSettings">';
            let settingsPrePath = '<Settings><Scene><Mouse>';
            let settingsPostPath = '</Mouse></Scene></Settings></Call>';
            //Left mouse
            mouseSettingsCall += settingsPrePath + '<LeftButtonFunction> ' + leftMouseMode + ' </LeftButtonFunction>';
            mouseSettingsCall += '<RightButtonFunction>' + rightMouseMode + '</RighButtonFunction>';
            mouseSettingsCall += '<MiddleButtonFunction>' + middleMouseMode + '</MiddleButtonFunction>' + settingsPostPath;
            vsClient.command.executeXML(mouseSettingsCall);

            // Reset SelectionMode
            appStates.selectionMode = 'Select';
            xmlSelect = '<Call Method="SetSelectionMode"><SelectionMode>' + appStates.selectionMode + '</SelectionMode></Call>';
            vsClient.command.executeXML(xmlSelect);
            $('#mobile-text-search-panel').hide();

            ui.hideAll();
            //Deactivate quick compare
            if (ui.paneBadgeIdx.indexOf('wv-quickcompare') !== -1)
            {
                ui.togglePaneBadge('QuickComparePane');
            }
            let quickCompareButton = ui.ribbon.adapter.getButtonByUID('rQuickCompare');
            if (quickCompareButton)
            {
                quickCompareButton.highlight(false);
            }
            break;
        // case 116: // F5 --> Fit all
        // Reset SelectionMode
        // xml = '<Call Method="FitAll"></Call>'
        // vsClient.command.executeXML(xml)
        // break
        case 46: // DEL --> Delete selected node
            xml = '<Call Method="ModifySelection"><SelectionModifier>Delete</SelectionModifier></Call>';
            vsClient.command.executeXML(xml);
            break;
    }
},
    false);

window.addEventListener("keydown", function (e)
{
    let xml = ''
    switch (e.keyCode)
    {
        case e.altKey && 65:    // Alt + A: Show all
            xml = '<Call Method="ShowAll"></Call>'
            break
        case e.altKey && 73:    // Alt + I: Isolate Selection
            xml = '<Call Method="ModifySelection"><SelectionModifier>Isolate</SelectionModifier></Call>'
            break
        case e.altKey && 72:     // Alt + H: Hide Selection
            xml = '<Call Method="ModifySelection"><SelectionModifier>Hide</SelectionModifier></Call>'
            break
        case e.altKey && 71:     // Alt + G: Ghost Selection
            xml = '<Call Method="ModifySelection"><SelectionModifier>Ghost</SelectionModifier></Call>'
            break
        case e.altKey && 83:     // Alt + S: Unghost Selection
            xml = '<Call Method="ModifySelection"><SelectionModifier>UnGhost</SelectionModifier></Call>'
            break
        case e.altKey && 65:     // Alt + A: Show all
            xml = '<Call Method="ShowAll"></Call>'
            break
        case e.ctrlKey && 65:    // Ctrl + A: Select All or Ctrl + Shift + A: Deselect
            e.preventDefault();
            if (!e.shiftKey)
            {
                xml = '<Call Method="SelectAllGeometries"></Call>'
            }
            else
            {
                xml = '<Call Method="ModifySelection"><SelectionModifier>Deselect</SelectionModifier></Call>'
            }
            break
        case e.ctrlKey && e.shiftKey && 65:    // Ctrl + Shift + A: Deselect All
            xml = '<Call Method="ModifySelection"><SelectionModifier>Deselect</SelectionModifier></Call>'
            break
        case e.ctrlKey && 73:    // Ctrl + I: Invert Selection
            xml = '<Call Method="ModifySelection"><SelectionModifier>Invert</SelectionModifier></Call>'
            break
        case e.ctrlKey && 67:    // Ctrl + C: Copy Object
            xml = '<Call Method="ModifySelection"><SelectionModifier>Copy</SelectionModifier></Call>'
            break
        case e.ctrlKey && 86:    // Ctrl + V: Paste Object 
            xml = '<Call Method="ModifySelection"><SelectionModifier>PasteToSelection</SelectionModifier></Call>'
            break
        case e.ctrlKey && 90:    // Ctrl + Z: Undo last action
            xml = '<Call Method="Undo"><Steps>1</Steps></Call>'
            break
        case e.ctrlKey && 85:    // Ctrl + U: Toggle grid
            e.preventDefault()
            xml = '<Call Method="GetSceneSettings"></Call>'
            vsClient.command.executeXMLAsync(xml)
            vsClient.once('GetSceneSettings', (response) =>
            {
                let gridStatus = response.Settings.Scene.Visualization.ShowGrid
                gridStatus = gridStatus === 'true' ? 'false' : 'true'
                xml = '<Call Method="SetSceneSettings">'
                xml += '<Settings><Scene><Visualization><ShowGrid>' + gridStatus
                xml += '</ShowGrid></Visualization></Scene></Settings></Call>'
                vsClient.command.executeXMLAsync(xml)
            })
            return
        case 112:   //F1: Open about dialog
            e.preventDefault()
            $('#wv-ribbon-about').click()
            return
        default:
            return
    }
    vsClient.command.executeXML(xml)
},
    false);

    vsClient.on('Relay', res =>
    {
        let message = res.__text__;
        //Handle VisShare calls
        if (message === 'VisShare_SaveAsDone')
        {
            ui.toast(true);
        }
        else if (message.startsWith('ShowNativeLoadingModal_') && !connectedToSession)
        {
            let parsedFileArr = message.split('_');
            parsedFileArr.shift();
            let filename = parsedFileArr.join('_');
            let showNativeLoading = ui.openNativeLoadingModal(filename, resumeBackendLoading.bind(vsClient));
            if (!showNativeLoading)
            {
                resumeBackendLoading(filename);
            }
        }
        else if (message.startsWith('VisShareBlockUI'))
        {
            let method = message.split('_')[1];
            vsClient.blockUI();
            vsClient.once(method, res =>
            {
                vsClient.unblockUI();
            })
        }
    });

function resumeBackendLoading (fileName: string)
{
    vsClient.relayMessageToBackend('NativeLoadingModalFinished');
}



function interceptQueryString (isBackend: boolean)
{
    var queryString = window.location.search.substr(1);
    var kvs = queryString.split('&')
    var query = {}
    for (let kv of kvs)
    {
        let [k, v] = kv.split('=')
        query[k] = v
    }

    var file1, file2;
    if (query['closesessions'])
    {
        appStates.sessionUrlClose = query['closesessions'];
        localStorage.removeItem('WVSessionCloseID');
        localStorage.setItem('WVSessionCloseID', query['closesessions']);
        window.addEventListener('storage', e =>
        {
            let urlCloseCommand = localStorage.getItem('WVSessionCloseID');
            if (e.key === 'WVSessionCloseID' && urlCloseCommand && urlCloseCommand !== null && urlCloseCommand !== '' && appStates.sessionUrlClose === urlCloseCommand)
            {
                vsClient.destroy();
                let url = location.href.split('/');
                url.pop();
                window.open(url.join('/') + '/session_in_use_index.html', '_self');
            }
        });
    }
    if (query['compare'] === '2d')
    {

        if (query['file1'])
        {
            file1 = decodeURIComponent(query['file1'])
        }
        if (query['file2'])
        {
            file2 = decodeURIComponent(query['file2'])
        }
        if (query['open1'])
        {
            file1 = decodeURIComponent(escape(window.atob(query['open1'])))
        }
        if (query['open2'])
        {
            file2 = decodeURIComponent(escape(window.atob(query['open2'])))
        }

        if (file1 && file2)
        {
            vsClient.OpenFile(file1)

            vsClient.on('ActivateGraphicalCompare', function (ev)
            {

                if (ev.CustomId == 'full_ui_2d_compare')
                {
                    if (!(ev.Error == "SUCCESS"))
                    {
                        alert("Error on 2D compare: " + ev.Error + " Info: " + ev.ErrorInfo)
                        console.log("Error on 2D compare: " + ev.Error + " Info: " + ev.ErrorInfo)
                    }
                }
            })

            vsClient.on('OpenFile', function ()
            {
                const XML = `
                <Call Method="ImportFile">
                <FileName>${file2}</FileName>
              </Call>`

                vsClient.command.executeXML(XML)
                const XML2 = `
                <Call Method="ActivateGraphicalCompare"><Activate>true</Activate> CustomId="full_ui_2d_compare"</Call>`

                vsClient.command.executeXML(XML2)
            });
        }

    }
    else if (query['compare'] === 'quick')
    {
        compareByURI(query, 'full_ui_quick_compare', 'ActivateGraphicalCompare');
    }
    else if (query['compare'] === 'exact')
    {
        compareByURI(query, 'full_ui_exact_compare', 'ComputeBrepCompare');
    }
    else if (typeof query['file'] !== 'undefined')
    {
        let showModal = query['NativeLoadingModal'];
        let filename = decodeURIComponent(query['file']);
        if (showModal && showModal === 'true')
        {
            ui.openNativeLoadingModal(filename, vsClient.OpenFile.bind(vsClient));
        }
        else
        {
            vsClient.OpenFile(filename);
        }

    } else if (typeof query['open'] !== 'undefined')
    {
        let filename = decodeURIComponent(escape(window.atob(query['open'])));
        let showModal = query['NativeLoadingModal'];
        if (showModal && showModal === 'true')
        {
            ui.openNativeLoadingModal(filename, vsClient.OpenFile.bind(vsClient));
        }
        else
        {
            vsClient.OpenFile(filename);
        }
    }
}

function compareByURI (query, customId: string, compareMethod: string)
{
    let file1: string, file2: string;
    if (query['file1'])
    {
        file1 = decodeURIComponent(query['file1'])
    }
    if (query['file2'])
    {
        file2 = decodeURIComponent(query['file2'])
    }
    if (query['open1'])
    {
        file1 = decodeURIComponent(escape(window.atob(query['open1'])))
    }
    if (query['open2'])
    {
        file2 = decodeURIComponent(escape(window.atob(query['open2'])))
    }
    if (file1 && file2)
    {
        // show the progress indicator
        if (vsClient.initOptions.blockOnLoad)
        {
            vsClient.blockUI();
        }

        vsClient.once(compareMethod, function (ev)
        {
            vsClient.unblockUI();
            if (ev.CustomId === customId)
            {
                if (ev.Error != "SUCCESS")
                {
                    if (compareMethod === 'ComputeBrepCompare')
                    {
                        console.log("Error on exact compare: " + ev.ErrorInfo + " Try quick compare.");
                        compareByURI(query, 'full_ui_quick_compare', 'ActivateGraphicalCompare');
                    }
                    else
                    {
                        console.log("Error on quick compare: " + ev.Error + " Info: " + ev.ErrorInfo);
                    }
                }
                else
                {
                    handleCompare(ev);
                }
            }
        });

        vsClient.NewScene3D();

        vsClient.once('NewScene3D', function (response)
        {
            handleNewSceneForCompare(response, file1, file2, customId, compareMethod);
        });
    }
}

function handleCompare (ev)
{
    if (ev.Error == 'SUCCESS')
    {

        let xml = `<Call Method="AlignView"><Alignment>Isometric</Alignment></Call><Call Method="ModifySelection"><SelectionModifier>Deselect</SelectionModifier></Call>`;
        vsClient.command.executeXML(xml);
    }
    let xml = `<Call Method="ResumeRendering"/>`;
    vsClient.command.executeXML(xml);
}

function handleNewSceneForCompare (response, file1: string, file2: string, customId: string, compareMethod: string)
{
    openFileHandler(response);
    let xml = `
    <Call Method="SetSettings">
        <Settings>
            <Import3D>
                <General>
                    <ReadMode>PreferGeometry</ReadMode>
                    <ReadPMIs>false</ReadPMIs>
                    <ReadSolids>true</ReadSolids>
                    <ReadConstructionAndReferences>false</ReadConstructionAndReferences>
                </General>
                <_3DVS>
                    <IncrementalLoading>false</IncrementalLoading>
                    <RemoveBrep>false</RemoveBrep>
                </_3DVS>
            </Import3D>
        </Settings>
    </Call>

    <Call Method="PauseRendering"/>

    <Call Method="UpdateScene">
        <VSXML>
            <Node CustomId="Root1" Name="Compare1" Type="Root">
                <Node CustomId="V1" Name="V1" Type="ProductOccurrence" FileName="${file1}" />
            </Node>
            <Node CustomId="Root2" Name="Compare2" Type="Root">
                <Node CustomId="V2" Name="V2" Type="ProductOccurrence" FileName="${file2}" />
            </Node>
        </VSXML>
    </Call>

    <Call Method="FitAll"/>

    <Call Method="SetSelectedNodes">
    <NodeId>#V1</NodeId>
    <NodeId>#V2</NodeId>
    </Call>

        `;
    let fulLCompareCall = '<Call Method="ComputeBrepCompare" CustomId="full_ui_exact_compare"></Call>';
    let quickCompareCall = '<Call Method="ActivateGraphicalCompare" CustomId="full_ui_quick_compare"><Activate>true</Activate></Call>';
    xml += compareMethod === 'ComputeBrepCompare' ? fulLCompareCall : quickCompareCall;
    vsClient.command.executeXML(xml);
}


