import { VSWebClient } from '../../base/vsweb-client-impl'
import { appStates } from '../../app/app-states'
import { settings } from '../../app/app-settings'
import events = require('events')
import * as ui from '../app/ui'
import * as adapter from './adapter'

declare var vsClient: VSWebClient
declare var _DisabledPanes: string[];


export class MobilePanes extends events.EventEmitter
{
    ribbonBody: HTMLElement
    languageSwitch: HTMLElement
    aboutDialog: HTMLElement

    aboutOpen: boolean

    leftDefaultAdapter: adapter.PaneAdapter
    rightDefaultAdapter: adapter.PaneAdapter

    settings: any
    sceneSettings: any
    loadedTemplates: any[] = []
    langChanged: boolean = false


    constructor(leftEl: HTMLElement, rightEl: HTMLElement)
    {
        super();
        this.leftDefaultAdapter = new adapter.PaneAdapter(leftEl, vsClient, 'left');
        this.rightDefaultAdapter = new adapter.PaneAdapter(rightEl, vsClient, 'right');
        vsClient.on('GetSettings', response =>
        {
            this.settings = response.Settings;
            this.updateSettings();
        });

        vsClient.on('GetSceneSettings', response =>
        {
            this.sceneSettings = response.Settings;
            this.updateSettings();
        });

        vsClient.on('SetSceneSettings', response =>
        {
            vsClient.command.executeXML('<Call Method="GetSceneSettings"></Call>');
        });

        vsClient.on('openFileDialog', response =>
        {
            $(ui.leftPaneWrapper).css('left', 0);
            this.leftDefaultAdapter.openFileBrowser('open');
        });

        vsClient.on('importFileDialog', response =>
        {
            $(ui.leftPaneWrapper).css('left', 0);
            this.leftDefaultAdapter.openFileBrowser('import');
        });

        vsClient.on('@changeLang', key =>
        {
            this.langChanged = true;
            for (let i = 0; i < this.loadedTemplates.length; i++)
            {
                let paneId = this.loadedTemplates[i].paneId;
                let paneTemplate = this.mapPaneIdToTemplate(paneId);
                let xml = '<Call Method="GetTemplate"><Type>' + paneTemplate + '</Type></Call>';
                vsClient.command.executeXML(xml);
                vsClient.once('GetTemplate.' + paneTemplate, (response: any) =>
                {
                    if (response.Error === 'SUCCESS')
                    {
                        let template = response['Source']['Template'];
                        this.loadedTemplates[i].template = template;
                        if (this.leftDefaultAdapter.openPane && this.leftDefaultAdapter.openPane === paneId)
                        {
                            this.leftDefaultAdapter.load(template, paneId);
                        }
                        else if(this.rightDefaultAdapter.openPane && this.rightDefaultAdapter.openPane === paneId)
                        {
                            this.rightDefaultAdapter.load(template, paneId);
                        }
                    }
                })
            }
        });
    }

    updateSettings()
    {
        this.leftDefaultAdapter.settings = this.settings;
        this.leftDefaultAdapter.sceneSettings = this.sceneSettings;
        this.leftDefaultAdapter.updateValues();
        this.rightDefaultAdapter.settings = this.settings;
        this.rightDefaultAdapter.sceneSettings = this.sceneSettings;
        this.rightDefaultAdapter.updateValues();
    }

    render(paneId, position)
    {
        if (ui.showTooltips === true)
        {
            ui.toggleTooltipClick();
        }
        try
        {
            if (_DisabledPanes.indexOf(paneId) !== -1)
            {
                return;
            }
        }
        catch (e) { }

        let adapter: adapter.PaneAdapter;
        let paneTemplate = paneId;
        if (paneId.indexOf('wv-') === 0)
        {
            paneTemplate = this.mapPaneIdToTemplate(paneId);
        }
        else
        {
            paneId = this.mapPaneTemplateToId(paneId);
        }
        if (position === 'left')
        {
            adapter = this.leftDefaultAdapter;
            adapter.paneEl.style.left = '0';
            this.rightDefaultAdapter.paneEl.style.right = 'calc(-90%)';
        }
        else
        {
            adapter = this.rightDefaultAdapter;
            adapter.paneEl.style.right = '0';
            this.leftDefaultAdapter.paneEl.style.left = 'calc(-90%)';
            if (this.leftDefaultAdapter.openPane === 'wv-file')
            {
                this.leftDefaultAdapter.clearFileBrowser();
            }
        }
        $(adapter.helpIcon).removeClass('mobile-toggle-tooltip-icon-active');
        if (adapter.openPane === paneId && !this.langChanged)
        {
            if (paneId === 'wv-measurement')
            {
                adapter.cleanGroups();
            }
            return;
        }
        this.langChanged = false; 
        let newPane = true;
        //Handle custom panes
        switch (paneId)
        {
            case 'wv-views':
                adapter.load("custom", paneId);
                return;
            case 'wv-modeltree':
                adapter.load("custom", paneId);
                return;
            case 'wv-selections':
                adapter.load("custom", paneId);
                return;
            case 'wv-appsettings':
                adapter.load("custom", paneId);
                return;
        }
        for (let i = 0; i < this.loadedTemplates.length; i++)
        {
            if (this.loadedTemplates[i].paneId === paneId)
            {
                adapter.load(this.loadedTemplates[i].template, paneId);
                newPane = false; 
            }
        }
        if (newPane)
        {
            let xml = '<Call Method="GetTemplate"><Type>' + paneTemplate + '</Type></Call>';
            vsClient.command.executeXML(xml);
            vsClient.once('GetTemplate.' + paneTemplate, (response: any) =>
            {
                if (response.Error === 'SUCCESS')
                {
                    let template = {
                        template: response['Source']['Template'],
                        paneId: paneId
                    };
                    this.loadedTemplates.push(template);
                    adapter.load(template.template, paneId);
                }
            })
        }
    }

    mapPaneIdToTemplate(paneId)
    {
        switch (paneId)
        {
            case 'wv-settings':
                return 'SettingsPane';
            case 'wv-modeltree':
                return 'Custom_ModelTree';
            case 'wv-views':
                return 'Custom_Views';
            case 'wv-distortion':
                return 'DistortionPane';
            case 'wv-draftangle':
                return 'DraftAnglePane';
            case 'wv-exactcompare':
                return 'ExactComparePane';
            case 'wv-filter':
                return 'FilterPane';
            case 'wv-flythrough':
                return 'FlyThroughPane';
            case 'wv-markup':
                return 'MarkupPane';
            case 'wv-markup-reduced':
                return 'MarkupPane_Reduced';
            case 'wv-measurement':
                return 'MeasurementPane';
            case 'wv-mirror':
                return 'MirrorPane';
            case 'wv-projectedarea':
                return 'ProjectedAreaPane';
            case 'wv-properties':
                return 'PropertiesPane';
            case 'wv-quickcompare':
                return 'QuickComparePane';
            case 'wv-section':
                return 'SectionPane';
            case 'wv-textsearch':
                return 'TextSearchPane';
            case 'wv-transform':
                return 'TransformPane';
            case 'wv-visibilitytest':
                return 'VisibilityTest';
            case 'wv-wallthickness':
                return 'WallThicknessPane';
            case 'wv-drillhole':
                return 'DrillholePane';
            case 'wv-neutralaxis':
                return 'NeutralAxisPane';
            case 'wv-neighbourhoodsearch':
                return 'NeighbourhoodSearchPane';
            case 'wv-split':
                return 'SplitPane';
            case 'wv-bom':
                return 'BOMPane';
            case 'wv-clash':
                return 'ClashPane';
            case 'wv-callout':
                return 'CalloutPane';
            case 'wv-appsettings':
                return 'Custom_AppSettings';
            case 'wv-explode':
                return 'ExplodePane';
            case 'wv-animation':
                return 'AnimationPane';
            case 'wv-geometry-tools':
                return 'GeometryToolsPane';
            case 'wv-bandanalysis':
                return 'BandAnalysisPane';
        }
    }

    mapPaneTemplateToId(paneId)
    {
        switch (paneId)
        {
            case 'SettingsPane':
                return 'wv-settings';
            case 'Custom_ModelTree':
                return 'wv-modeltree';
            case 'Custom_Views':
                return 'wv-views';
            case 'DistortionPane':
                return 'wv-distortion';
            case 'DraftAnglePane':
                return 'wv-draftangle';
            case 'ExactComparePane':
                return 'wv-exactcompare';
            case 'FilterPane':
                return 'wv-filter';
            case 'FlyThroughPane':
                return 'wv-flythrough';
            case 'MarkupPane':
                return 'wv-markup';
            case 'MarkupPane_Reduced':
                return 'wv-markup-reduced';
            case 'MeasurementPane':
                return 'wv-measurement';
            case 'MirrorPane':
                return 'wv-mirror';
            case 'ProjectedAreaPane':
                return 'wv-projectedarea';
            case 'PropertiesPane':
                return 'wv-properties';
            case 'QuickComparePane':
                return 'wv-quickcompare';
            case 'SectionPane':
                return 'wv-section';
            case 'TextSearchPane':
                return 'wv-textsearch';
            case 'TransformPane':
                return 'wv-transform';
            case 'VisibilityTestPane':
                return 'wv-visibilitytest';
            case 'WallThicknessPane':
                return 'wv-wallthickness';
            case 'DrillholePane':
                return 'wv-drillhole';
            case 'NeutralAxisPane':
                return 'wv-neutralaxis';
            case 'NeighbourhoodSearchPane':
                return 'wv-neighbourhoodsearch';
            case 'SplitPane':
                return 'wv-split';
            case 'ExplodePane':
                return 'wv-explode';
            case 'BOMPane':
                return 'wv-bom';
            case 'ClashPane':
                return 'wv-clash';
            case 'CalloutPane':
                return 'wv-callout';
            case 'Custom_AppSettings':
                return 'wv-appsettings';
            case 'DrillholePane':
                return 'wv-animation';
            case 'AnimationPane':
                return 'wv-animation';
            case 'GeometryToolsPane':
                return 'wv-geometry-tools';
            case 'BandAnalysisPane':
                return 'wv-bandanalysis';
        }
    }
}


