//Flythrough control
interface FlyDimensioning
{
    center: number[],
    widthRatio: number,
    heightRatio: number
};
let flyXDirection: number;
let flyYDirection: number;
let canvasControlRatio: FlyDimensioning;
let lastDirection: number[] = [];
let maxSpeed: number;
let speedPos: number;
