export function togglePaneBadge (paneId: string)
{
    paneId = paneController.mapPaneTemplateToId(paneId);
    if (paneId === 'wv-appsettings')
    {
        return true;
    }
    let bool = true;
    if (paneBadgeIdx.indexOf(paneId) === -1)
    {
        let hastLeftMoreBadge = localLeftMoreBadge.length > 0 ? 1 : 0;
        if ((paneBadges.length + 1) * 45 > wvDock.clientHeight - 60)
        {
            let lastBadge = paneBadges.pop();
            localLeftMoreBadge.push(lastBadge.id.replace('_paneBadge', ''));
            $(lastBadge).remove();
            fillLeftMoreBadge();
        }
        paneBadgeIdx.push(paneId);
        calculateLeftBadgePositions();
    }
    else
    {
        deletePaneBadge(paneId);
        bool = false;
    }
    return bool;
}


