import { VSWebClient } from '../../../base/vsweb-client-impl';
import * as ui from '../../app/ui';
import { settings } from '../../../app/app-settings';
const SVG_NS = 'http://www.w3.org/2000/svg';
const XLINK_NS = 'http://www.w3.org/1999/xlink';


interface SelectionItem
{
    id: string,
    count: string
}

export class MobileSelectionPane
{
    wrapperEl: HTMLElement
    paneEl: HTMLElement
    client: VSWebClient
    tableTitle: HTMLElement;
    buttons: HTMLElement;
    selectedRow: HTMLElement;
    selectionsWrapper: HTMLElement;
    rows: HTMLElement[] = [];
    multiSelect: boolean = false;
    lastIndex: number = 0;
    selections: SelectionItem[] = [];
    multiSelections: string[] = [];
    multiRows: HTMLElement[] = [];
    rowWrapper: HTMLElement;

    constructor(paneEl: HTMLElement, client: VSWebClient)
    {
        this.paneEl = paneEl;
        this.client = client;


        let titleOffset = $('#ID_SELECTIONS_WND').outerHeight();

        this.wrapperEl = document.createElement('div');
        this.wrapperEl.className = 'mobile-pane-wrapper';

        this.buttons = document.createElement('div');
        this.buttons.classList.add('mobile-pane-buttons-tab');
        this.buttons.style.position = 'relative';

        this.paneEl.appendChild(this.buttons);

        this.selectionsWrapper = document.createElement('div');
        this.selectionsWrapper.className = 'mobile-pane-group';

        this.wrapperEl.appendChild(this.selectionsWrapper);


        this.wrapperEl.addEventListener('contextmenu', ev =>
        {
            ev.preventDefault();
            $('#contextMenu').remove();
            $(document).off('mousedown', this.client.vsCanvas.closeContextMenu);
            $(document).off('keyup', this.client.vsCanvas.closeContextMenu);
            this.client.vsCanvas.createContextMenu(ev);
        })

        //Title row
        let titleRow = document.createElement('span');
        titleRow.style.paddingLeft = '5px';
        titleRow.id = 'ID_SELECTIONS_COLUMN_NAME';
        titleRow.textContent = ui.translate('ID_SELECTIONS_COLUMN_NAME', settings.lang);
        ui.translationObjects.push(titleRow);

        this.selectionsWrapper.appendChild(titleRow);

        this.client.on('OpenFile', ev =>
        {
            this.reset();
        });

        this.client.on('NewScene3D', ev =>
        {
            this.reset();
        });

        this.client.on('GetSavedSelections', res =>
        {
            this.buildSelectionTable(res);
        });

        this.client.on('SavedSelectionsChanged', res =>
        {
            this.buildSelectionTable(res, false);
        });

        this.rowWrapper = document.createElement('div');
        this.wrapperEl.appendChild(this.rowWrapper);

        let multiselectEl = document.createElement('span');
        multiselectEl.style.verticalAlign = 'super';
        let multiselectIcon = document.createElement('i');
        $(multiselectIcon).addClass('far fa-check-square mobile-selections-multiselect-icon');
        multiselectEl.appendChild(multiselectIcon);

        multiselectIcon.addEventListener('click', () =>
        {
            $(multiselectIcon).toggleClass('mobile-modeltree-multiselect-enabled');
            if ($(multiselectIcon).hasClass('fa'))
            {
                this.multiSelect = false;
                $(multiselectIcon).removeClass('fa').addClass('far');
            }
            else
            {
                this.multiSelect = true;
                $(multiselectIcon).removeClass('far').addClass('fa');
            }
        }, false);

        let addSelection = createButton('selectionspane-add', 'view_button_plus');
        addSelection.addEventListener('click', ev =>
        {
            let createCall = '<Call Method="SaveCurrentSelection"></Call>';
            this.client.command.executeXML(createCall);
        });

        let updateSelection = createButton('selectionspane-update', 'view_button_refresh')
        updateSelection.addEventListener('click', ev =>
        {
            let row = this.selectedRow;
            let updateXML = '<Call Method="UpdateSavedSelection"><Index>' + row.id.split('_')[1] + '</Index></Call>';
            this.client.command.executeXML(updateXML);
        });


        let removeSelection = createButton('selectionspane-remove', 'view_button_cross')
        removeSelection.addEventListener('click', ev =>
        {
            let xml = '<Call Method="DeleteSavedSelections">';
            for (let i = 0; i < this.multiRows.length; i++)
            {
                let row = this.multiRows[i];
                xml += '<Index>' + row.id.split('_')[1] + '</Index>';
            }
            xml += '</Call>';
            this.client.command.executeXML(xml);
            this.multiRows = [];
        });

        this.buttons.appendChild(multiselectEl);
        this.buttons.appendChild(addSelection);
        this.buttons.appendChild(updateSelection);
        this.buttons.appendChild(removeSelection);

        this.paneEl.appendChild(this.wrapperEl);
    }

    reset (getNewSessions: boolean = true, resetSelection: boolean = true)
    {
        for (let i = 0; i < this.rows.length; i++)
        {
            $(this.rows[i]).remove();
        }
        this.rows = [];
        if (resetSelection)
        {
            this.multiRows = [];
            this.multiSelections = [];
            this.selectedRow = undefined;
        }
        this.selections = [];
        if (getNewSessions)
        {
            let xml = '<Call Method="GetSavedSelections"></Call>';
            this.client.command.executeXML(xml);
        }
    }

    buildSelectionTable (res, resetSelection: boolean = true)
    {
        this.reset(false, resetSelection);
        if (!res.Selection)
        {
            return;
        }
        if (res.Selection && !Array.isArray(res.Selection))
        {
            res.Selection = [res.Selection];
        }
        for (let i = 0; i < res.Selection.length; i++)
        {
            this.addRow(res.Selection[i].Index, res.Selection[i].Name, !resetSelection);
        }
    }

    enableTextChange(element: HTMLElement)
    {
        let el = $(element);
        el.attr('contenteditable', 'true');

        //Select text
        let range = document.createRange();
        range.selectNodeContents(element);
        let sel = window.getSelection();
        sel.removeAllRanges();
        sel.addRange(range);

        el.focus();
        el.blur( e =>
        {
            $(e.target).off('blur');
            $(e.target).attr('contenteditable', 'false');
            let newName = e.target['textContent'];
            let index = e.target.parentElement.parentElement.id.split('_')[1];
            let xml = '<Call Method="RenameSavedSelection"><Index>' + index + '</Index>';
            xml += '<Name>' + newName + '</Name></Call>';
            this.client.command.executeXML(xml);
        })
        el.on('keydown', function (e)
        {
            if (e.keyCode === 13)
            {
                e.preventDefault();
                $(e.target).off('keyup');
                $(e.target).attr('contenteditable', 'false');
            }
        })
    }

    addRow (index, name, applySelectionState: boolean = false)
    {
        let newRow = document.createElement('div');
        newRow.className = 'mobile-selection-row';
        newRow.id = 'Selection_' + index;

        let newRowEditIcon = document.createElement('i');
        newRowEditIcon.style.fontSize = '16px';
        newRowEditIcon.className = 'fas fa-edit selection-pane-row-icon';

        newRowEditIcon.onclick = ev =>
        {
            this.enableTextChange(newRowText);
        };

        let newRowText = document.createElement('span');
        newRowText.textContent = name;
        newRowText.addEventListener('keyup', ev =>
        {
            ev.preventDefault();
            ev.stopPropagation();
        });

        let newRowBadge = document.createElement('div');
        newRowBadge.className = 'selection-pane-row-badge';
        newRowBadge.style.marginTop = '3px';
        newRowBadge.id = 'Selection_' + index + '_Number';
        newRowBadge.textContent = '-';

        let changeText = false;

        $(newRowText).mousedown(ev =>
        {
            changeText = true;
            setTimeout(() =>
            {
                if (changeText)
                {
                    this.enableTextChange(newRowText);
                }
            }, 500)
        });

        $(newRowText).mouseup(ev =>
        {
            changeText = false;
        });

        newRow.onclick = ev =>
        {
            //Get clicked selection
            let selection: string = newRow.id.split('_')[1];
            //Multiselection
            if (ev.ctrlKey || this.multiSelect)
            {
                $(newRow).toggleClass('selection-pane-row-active');
                if (this.multiRows.indexOf(newRow) === -1)
                {
                    this.multiRows.push(newRow);
                    //Push selection to multi selection array
                    if (this.multiSelections.indexOf(selection) === -1)
                    {
                        this.multiSelections.push(selection);
                    }
                    //Select all nodes
                    let xml = '<Call Method="ActivateSavedSelections">';
                    for (let n = 0; n < this.multiSelections.length; n++)
                    {
                        xml += '<Index>' + this.multiSelections[n] + '</Index>';
                    }
                    xml += '</Call>';
                    this.client.command.executeXML(xml);
                }
                else
                {
                    let index = this.multiRows.indexOf(newRow);
                    this.multiRows.splice(index, 1);
                    //Remove selection from selection array
                    for (let s = 0; s < selection.length; s++)
                    {
                        if (this.multiSelections.indexOf(selection) !== -1)
                        {
                            this.multiSelections.splice(this.multiSelections.indexOf(selection), 1);
                        }
                    }
                    //Select all nodes
                    let xml = '<Call Method="ActivateSavedSelections">';
                    for (let n = 0; n < this.multiSelections.length; n++)
                    {
                        xml += '<Index>' + this.multiSelections[n] + '</Index>';
                    }
                    xml += '</Call>';
                    this.client.command.executeXML(xml);
                }
            }
            else
            {
                $('.selection-pane-row-active').each(function (ev)
                {
                    $(this).removeClass('selection-pane-row-active');
                });
                $(newRow).addClass('selection-pane-row-active');
                this.selectedRow = newRow;
                this.multiRows = [];
                this.multiSelections = [];
                this.multiRows.push(newRow);
                this.multiSelections.push(selection);
                let xml = '<Call Method="ActivateSavedSelections">';
                xml += '<Index>' + selection + '</Index>';
                xml += '</Call>';
                this.client.command.executeXML(xml);
            }
        };

        newRow.appendChild(newRowEditIcon);
        newRow.appendChild(newRowText);
        //newRow.appendChild(newRowBadge);
        this.rowWrapper.appendChild(newRow);
        this.rows.push(newRow);
        let newSelection = {
            id: 'Selection_' + index,
            count: '-'
        };
        this.selections.push(newSelection);
        if (applySelectionState && this.selectedRow && this.selectedRow.id.split('_')[1] === index)
        {
            $(newRow).addClass('selection-pane-row-active');
        }
    }
}

function createButton(id: string, icon: string): HTMLElement
{
    let el = document.createElement('span');
    el.id = id;
    el.style.verticalAlign = 'middle';
    let elWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
    elWrapper.style.width = '19px';
    elWrapper.style.height = '19px';
    let elIcon = document.createElementNS(SVG_NS, 'use');
    elIcon.setAttributeNS(XLINK_NS, 'href', '#' + icon);
    elWrapper.appendChild(elIcon);
    el.appendChild(elWrapper);
    return el;
}


