    createPDF (open: boolean)
    {
        let xml = '<Call Method="GetPrintImageAsPDF"></Call>';
        this.client.command.executeXML(xml);
        ui.blockUI();
        let name = appStates.fileName ? appStates.fileName + '.pdf' : 'print.pdf';
        this.client.once('GetPrintImageAsPDF', res =>
        {
            ui.unblockUI();
            let byteCharacters = atob(res.Image);
            let byteNumbers = new Array(byteCharacters.length);
            for (let i = 0; i < byteCharacters.length; i++)
            {
                byteNumbers[i] = byteCharacters.charCodeAt(i);
            }
            let byteArray = new Uint8Array(byteNumbers);
            let blob = new Blob([byteArray], {
                type: 'application/pdf'
            });
            //IE and edge
            if (window.navigator && window.navigator.msSaveOrOpenBlob)
            {
                if (open)
                {
                    window.navigator.msSaveOrOpenBlob(blob);
                }
                else
                {
                    window.navigator.msSaveOrOpenBlob(blob, name);
                }
            } else
            {
                let blobUrl = URL.createObjectURL(blob);
                if (open)
                {
                    window.open(blobUrl);
                }
                else
                {
                    const link = document.createElement('a');
                    // create a blobURI pointing to our Blob
                    link.href = URL.createObjectURL(blob);
                    link.download = name;
                    // some browser needs the anchor to be in the doc
                    document.body.appendChild(link);
                    link.click();
                    link.remove();
                }
            }
        });
    }

