    fillOptions (template)
    {
        //Empty options
        this.printRangeEnum = [];
        this.printScaleEnum = [];
        this.printOrientationEnum = [];
        this.printLineWidthUnit = [];
        let enums: any[] = template.Source.Template.Enum;
        //Fill print range
        let range = enums[0].Options.Option;
        let rangeLength = range.length;
        for (let r = 0; r < rangeLength; r++)
        {
            let rangeOpt: PrintOption = {
                value: range[r].Value,
                langId: range[r].DisplayName
            }
            this.printRangeEnum.push(rangeOpt);
        }
        //Fill print scaling
        let scale = enums[1].Options.Option;
        let scaleLength = scale.length;
        for (let s = 0; s < scaleLength; s++)
        {
            let scaleOpt: PrintOption = {
                value: scale[s].Value,
                langId: scale[s].DisplayName
            }
            this.printScaleEnum.push(scaleOpt);
        }
        //Fill print orientation
        let orientation = enums[2].Options.Option;
        let orientationLength = orientation.length;
        for (let o = 0; o < orientationLength; o++)
        {
            let orientationOpt: PrintOption = {
                value: orientation[o].Value,
                langId: orientation[o].DisplayName
            }
            this.printOrientationEnum.push(orientationOpt);
        }
        //Fill print line width unit
        let unit = enums[3].Options.Option;
        let unitLength = unit.length;
        for (let u = 0; u < unitLength; u++)
        {
            let unitOpt: PrintOption = {
                value: unit[u].Value,
                langId: unit[u].DisplayName
            }
            this.printLineWidthUnit.push(unitOpt);
        }
    }

    resize ()
    {
    }


    hide ()
    {
        window.removeEventListener('resize', this.resize);
        document.body.removeEventListener('mousedown', this.detectOutsideClick);
        document.body.removeEventListener('keyup', this.detectEscPressed);
        this.isOpen = false;
        $(this.modalWrapper).remove();
    }


    detectOutsideClick (e)
    {
        if (!(e.target === this.modalWrapper || this.modalWrapper.contains(e.target) || $('.wv-color-picker-popup').find(e.target).length > 0))
        {
            this.submit();
        }
    }

    detectEscPressed (e)
    {
        e.preventDefault();
        e.stopPropagation();
        if (e.keyCode === 27)
        {
            this.submit();
        }
    }

    submit ()
    {
        this.hide();
    }
