import { VSWebClient } from '../../../base/vsweb-client-impl';
import { MobileTreeRow } from '../widgets';
import { setTimeout } from 'timers';

declare var _SceneNameOverride;

const SVG_NS = 'http://www.w3.org/2000/svg';
const XLINK_NS = 'http://www.w3.org/1999/xlink';
export interface TreeRow
{
    ExpanderState: number,
    Level: number,
    NodeId: number,
    NodeName: string,
    NodeType: number,
    Selection: number,
    Visibility: number,
    Parent: number
}

export interface TreeInfo
{
    ExpandedNodes: number
    VirtualScrollPosition: number
    Items: TreeRow[]
}


export class MobileModeltree
{
    wrapperEl: HTMLElement
    paneEl: HTMLElement
    client: VSWebClient
    expandedNodesSize: number
    touchStart: number
    rows: TreeRow[]
    expandAllEl: HTMLElement
    expandEl: HTMLElement
    collapseAllEl: HTMLElement
    collapseEl: HTMLElement
    toggleLevelEl: HTMLElement
    selectSameLevelEl: HTMLElement
    treeWrapper: HTMLElement
    multiselectIcon: HTMLElement
    selectedRow: MobileTreeRow
    selectedRows: MobileTreeRow[]
    maxVisible: number
    dragAccelerator: number
    lastDragEndTimeStamp: number
    dragTick: number
    bar: HTMLElement
    virtualScrollPos: number
    isScrolling: boolean
    multiselect: boolean
    rowElementArray: MobileTreeRow[] = []
    //Mouse and key scroll
    firstScrollHit: number = 0
    scrollBarRail: HTMLElement
    scrollPosition: number = 0
	scrollTravelled: number = 0
    lastPageY: number
    mouseScrollActive: boolean = false

    constructor(paneEl: HTMLElement, client: VSWebClient)
    {
        this.paneEl = paneEl;
        this.client = client;
        this.dragAccelerator = 4;
        this.dragTick = 0;
        this.isScrolling = false;
        this.multiselect = false;
        this.selectedRows = [];
        this.lastDragEndTimeStamp = new Date().getTime();
        this.wrapperEl = document.createElement('div');
        this.wrapperEl.className = 'mobile-modeltree';
        this.bar = document.createElement('div');
        this.bar.className = 'mobile-modeltree-bar';
        //Action row
        let actionRow = document.createElement('div');
        actionRow.className = 'mobile-modeltree-action-row';
        this.paneEl.appendChild(actionRow);


        //Delete selection
        let deleteEl = document.createElement('span');
        deleteEl.className = 'mobile-modeltree-delete';
        let deleteIcon = document.createElement('i');
        $(deleteIcon).addClass('fa fa-trash mobile-modeltree-delete-icon');
        deleteEl.appendChild(deleteIcon);

        deleteIcon.addEventListener('click', () =>
        {
            xml = '<Call Method="ModifySelection"><SelectionModifier>Delete</SelectionModifier></Call>';
            this.client.command.executeXML(xml);
        }, false);
        actionRow.appendChild(deleteEl);

        //Toggle multiselect
        let multiselectEl = document.createElement('span');
        multiselectEl.className = 'mobile-modeltree-multiselect';
        this.multiselectIcon = document.createElement('i');
        $(this.multiselectIcon).addClass('far fa-check-square mobile-modeltree-multiselect-icon');
        multiselectEl.appendChild(this.multiselectIcon);

        this.multiselectIcon.addEventListener('click', () =>
        {
            $(this.multiselectIcon).toggleClass('mobile-modeltree-multiselect-enabled');
            if ($(this.multiselectIcon).hasClass('fa'))
            {
                $(this.multiselectIcon).removeClass('fa').addClass('far');
            }
            else
            {
                $(this.multiselectIcon).removeClass('far').addClass('fa');
            }
        }, false);
        actionRow.appendChild(multiselectEl);

        //ExpandAll button
        this.expandAllEl = document.createElement('span');
        this.expandAllEl.style.marginLeft = '66px';
        let expandAllWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
        expandAllWrapper.style.width = '20px';
        expandAllWrapper.style.height = '20px';
        expandAllWrapper.style.marginTop = '5px';
        let expandAllIcon = document.createElementNS(SVG_NS, 'use');
        expandAllIcon.setAttributeNS(XLINK_NS, 'href', '#' + 'button_plus_double');
        expandAllWrapper.appendChild(expandAllIcon);
        this.expandAllEl.appendChild(expandAllWrapper);
        this.expandAllEl.addEventListener('click', () =>
        {
            let s = '<Call Method="VirtualTreeViewExpandAll"></Call>';
            this.client.command.executeXML(s);
        }, false);
        actionRow.appendChild(this.expandAllEl);

        //CollapseAll button
        this.collapseAllEl = document.createElement('span');
        let collapseAllWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
        collapseAllWrapper.style.width = '20px';
        collapseAllWrapper.style.height = '20px';
        collapseAllWrapper.style.marginTop = '5px';
        let collapseAllIcon = document.createElementNS(SVG_NS, 'use');
        collapseAllIcon.setAttributeNS(XLINK_NS, 'href', '#' + 'button_minus_double');
        collapseAllWrapper.appendChild(collapseAllIcon);
        this.collapseAllEl.appendChild(collapseAllWrapper);
        this.collapseAllEl.addEventListener('click', () =>
        {
            let s = '<Call Method="VirtualTreeViewCollapseAll"></Call>';
            this.client.command.executeXML(s);
        }, false);
        actionRow.appendChild(this.collapseAllEl);

        //Expand button
        this.expandEl = document.createElement('span');
        let expandSelectedWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
        expandSelectedWrapper.style.width = '19px';
        expandSelectedWrapper.style.height = '19px';
        expandSelectedWrapper.style.marginTop = '5px';
        let expandSelectedIcon = document.createElementNS(SVG_NS, 'use');
        expandSelectedIcon.setAttributeNS(XLINK_NS, 'href', '#' + 'button_plus');
        expandSelectedWrapper.appendChild(expandSelectedIcon);
        this.expandEl.appendChild(expandSelectedWrapper);
        this.expandEl.addEventListener('click', () =>
        {
            let s = '<Call Method="VirtualTreeViewExpandSelected"></Call>';
            this.client.command.executeXML(s);
        }, false);
        actionRow.appendChild(this.expandEl);

        //Collapse button
        this.collapseEl = document.createElement('span');
        let collapseSelectedWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
        collapseSelectedWrapper.style.width = '19px';
        collapseSelectedWrapper.style.height = '19px';
        collapseSelectedWrapper.style.marginTop = '5px';
        let collapseSelectedIcon = document.createElementNS(SVG_NS, 'use');
        collapseSelectedIcon.setAttributeNS(XLINK_NS, 'href', '#' + 'button_minus');
        collapseSelectedWrapper.appendChild(collapseSelectedIcon);
        this.collapseEl.appendChild(collapseSelectedWrapper);
        this.collapseEl.addEventListener('click', () =>
        {
            let s = '<Call Method="VirtualTreeViewCollapseSelected"></Call>';
            this.client.command.executeXML(s);
        }, false);
        actionRow.appendChild(this.collapseEl);

        //Toggle level
        this.toggleLevelEl = document.createElement('span');
        let expandSelectedLevelWrapper = <SVGSVGElement>document.createElementNS(SVG_NS, 'svg');
        expandSelectedLevelWrapper.style.width = '19px';
        expandSelectedLevelWrapper.style.height = '19px';
        expandSelectedLevelWrapper.style.marginTop = '5px';
        let expandSelectedLevelIcon = document.createElementNS(SVG_NS, 'use');
        expandSelectedLevelIcon.setAttributeNS(XLINK_NS, 'href', '#' + 'button_divide');
        expandSelectedLevelWrapper.appendChild(expandSelectedLevelIcon);
        this.toggleLevelEl.appendChild(expandSelectedLevelWrapper);
        this.toggleLevelEl.addEventListener('click', () =>
        {
            let s = '<Call Method="VirtualTreeViewExpandToSelectedLevel"></Call>';
            this.client.command.executeXML(s);
        }, false);
        actionRow.appendChild(this.toggleLevelEl);

        //Select same level
        this.selectSameLevelEl = document.createElement('span');
        let selectSameLevelWrapper = <SVGSVGElement> document.createElementNS(SVG_NS, 'svg');
        selectSameLevelWrapper.style.width = '19px';
        selectSameLevelWrapper.style.height = '19px';
        selectSameLevelWrapper.style.marginTop = '5px';
        let selectSameLevelIcon = document.createElementNS(SVG_NS, 'use');
        selectSameLevelIcon.setAttributeNS(XLINK_NS, 'href', '#' + 'button_divide');
        selectSameLevelWrapper.appendChild(selectSameLevelIcon);
        this.selectSameLevelEl.appendChild(selectSameLevelWrapper);
        this.selectSameLevelEl.addEventListener('click', () =>
        {
            let s = '<Call Method="VirtualTreeViewSelectSameLevelNodes"></Call>';
            this.client.command.executeXML(s);
        }, false);
        actionRow.appendChild(this.selectSameLevelEl);

        this.treeWrapper = document.createElement('div');
        this.treeWrapper.className = 'mobile-modeltree-tree-wrapper';

        this.wrapperEl.appendChild(this.treeWrapper);
        this.paneEl.appendChild(this.wrapperEl);


        this.maxVisible = (this.wrapperEl.clientHeight - actionRow.clientHeight - 32) / 20;
        //Tree element height = pane height => real tree height = tree height - action row height - title height 
        let xml = '<Call Method="VirtualTreeViewSetSize"><Size>' + Math.floor(this.maxVisible) + '</Size></Call>';
        this.client.command.executeXML(xml);
        this.scrollOnKey = this.scrollOnKey.bind(this);
        this.wrapperEl.addEventListener('mouseenter', ev =>
        {
            this.toggleScrollEvents(true);
        });

        this.wrapperEl.addEventListener('mouseleave', ev =>
        {
            this.toggleScrollEvents(false);
        });

        this.wrapperEl.addEventListener('wheel', ev =>
        {
            this.bar.style.width = '15px';
            this.bar.style.right = -this.wrapperEl.scrollLeft + 'px';
            this.activateScrollBarForMouse();
            //Scroll down
            if (ev.deltaY > 0)
            {
                let xml = '<Call Method="VirtualTreeViewScrollDown"><Steps>1</Steps></Call>';
                this.client.command.executeXML(xml);
                this.client.once('VirtualTreeViewScrollDown', res =>
                {
                    this.calculateBar();
                });
            }
            //Scroll up
            else
            {
                let xml = '<Call Method="VirtualTreeViewScrollUp"><Steps>1</Steps></Call>';
                this.client.command.executeXML(xml);
                this.client.once('VirtualTreeViewScrollUp', res =>
                {
                    this.calculateBar();
                });
            }
        });
        this.wrapperEl.appendChild(this.bar);
        $(this.wrapperEl).off('contextmenu').on('contextmenu', ev =>
        {
            ev.preventDefault();
            $('#contextMenu').remove();
            $(document).off('mousedown', this.client.vsCanvas.closeContextMenu);
            $(document).off('keyup', this.client.vsCanvas.closeContextMenu);
            this.client.vsCanvas.createContextMenu(ev);
        });
    }

    activateScrollBarForMouse()
    {
        if (!this.mouseScrollActive)
        {
            this.scrollBarRail = document.createElement('div');
            this.scrollBarRail.className = 'modeltree-bar-rail';
            this.wrapperEl.appendChild(this.scrollBarRail);
            this.scrollBarRail.addEventListener('click' ,ev =>
            {
                let deltaClick = parseInt(this.bar.style.top.replace("px", "")) - ev.offsetY;
                let page = this.maxVisible;
                if (deltaClick > 0)
                {
                    let xml = '<Call Method="VirtualTreeViewScrollUp"><Steps>' + page + '</Steps></Call>';
                    this.client.command.executeXMLAsync(xml);
                }
                else
                {
                    let xml = '<Call Method="VirtualTreeViewScrollDown"><Steps>' + page + '</Steps></Call>';
                    this.client.command.executeXMLAsync(xml);
                }
            });
            this.bar.addEventListener('mousedown', this.barOnclick, false);
            this.bar.addEventListener('touchstart', this.barOntouchstart)
		    this.bar.addEventListener('touchmove', this.barOntouchmove)
		    this.bar.addEventListener('touchend', this.barOnstop)
            this.mouseScrollActive = true;
        }
    }

    barOntouchstart = (ev: TouchEvent) =>
	{
        this.lastPageY = ev.touches[0].pageY;
	}

	barOntouchmove = (ev: TouchEvent) =>
	{
        let delta = ev.touches[0].pageY - this.lastPageY;
        this.lastPageY = ev.touches[0].pageY;
        this.scrollPosition += delta;
        this.scrollTravelled += delta;
		if (this.scrollPosition < 0)
		{
            this.scrollPosition = 0;
		}

		if (this.scrollPosition > this.treeWrapper.offsetHeight - this.bar.offsetHeight)
		{
            this.scrollPosition = this.treeWrapper.offsetHeight - this.bar.offsetHeight;
		}
        this.bar.style.top = this.scrollPosition + 'px';
	}

    barOnclick = (ev: MouseEvent) =>
    {
        this.lastPageY = ev.pageY;
        document.addEventListener('mousemove', this.barOndrag, false);
        document.addEventListener('mouseup', this.barOnstop, false);
	}

	barOndrag = (ev: MouseEvent) =>
	{
        let delta = ev.pageY - this.lastPageY;
        this.lastPageY = ev.pageY;
        this.scrollPosition += delta;
        this.scrollTravelled += delta;

		if (this.scrollPosition < 0)
		{
            this.scrollPosition = 0;
		}

		if (this.scrollPosition > this.treeWrapper.offsetHeight - this.bar.offsetHeight)
		{
            this.scrollPosition = this.treeWrapper.offsetHeight - this.bar.offsetHeight;
		}

        this.bar.style.top = this.scrollPosition + 'px';
	}

	barOnstop = (ev: MouseEvent) =>
	{
        let step = Math.ceil(Math.abs((this.scrollTravelled) / (20 * this.maxVisible)) * this.expandedNodesSize);
        let s: string;
		if (this.scrollTravelled > 0)
		{
            s = '<Call Method="VirtualTreeViewScrollDown"><Steps>' + step + '</Steps></Call>';
            this.client.command.executeXML(s);
		} else if (this.scrollTravelled < 0)
		{
            s = '<Call Method="VirtualTreeViewScrollUp"><Steps>' + step + '</Steps></Call>';
            this.client.command.executeXML(s);
        }
        document.removeEventListener('mousemove', this.barOndrag);
        document.removeEventListener('mouseup', this.barOnstop);
        this.scrollTravelled = 0;
	}

    toggleScrollEvents(state)
	{
		if (state)
		{
            $(document).off('keydown', this.scrollOnKey).on('keydown', this.scrollOnKey);
		}
		else
		{
            $(document).off('keydown', this.scrollOnKey);
		}
	}

    drag(e)
    {
        let loc = e.touch.y.location;
        let distance = this.touchStart - loc;
        let xml: string;
        this.dragTick++;
        //Scroll after one item height
        if (distance > 30 && this.dragTick % 2 === 0)
        {
            this.touchStart = e.touch.y.location;
            xml = '<Call Method="VirtualTreeViewScrollDown"><Steps>' + this.dragAccelerator + '</Steps></Call>';
            this.client.command.executeXML(xml);
        }
        else if (distance < -30 && this.dragTick % 2 === 0)
        {
            this.touchStart = e.touch.y.location;
            xml = '<Call Method="VirtualTreeViewScrollUp"><Steps>' + this.dragAccelerator + '</Steps></Call>';
            this.client.command.executeXML(xml);
        }
    }

    dragStart(e)
    {
        this.bar.style.width = '5px';
        this.bar.style.right = -this.wrapperEl.scrollLeft + 'px';
        this.isScrolling = true;
        this.touchStart = e.touch.y.location;
        let timeStamp = new Date().getTime();
        if (timeStamp - this.lastDragEndTimeStamp < 500)
        {
            if (this.dragAccelerator < 32)
            {
                this.dragAccelerator *= 2;
            }
        }
        else
        {
            this.dragAccelerator = 4;
        }

    }

    dragEnd(e)
    {
        this.isScrolling = false;
        this.dragTick = 0;
        this.lastDragEndTimeStamp = new Date().getTime();
        setTimeout(() =>
        {
            if (!this.isScrolling)
            {
                this.bar.style.width = '0px';
            }
        },500);
    }

    build()
    {
        $(this.treeWrapper).detach();
        this.treeWrapper = document.createElement('div');
        this.treeWrapper.className = 'mobile-modeltree-tree-wrapper';

        this.wrapperEl.appendChild(this.treeWrapper);
        for (var item = this.rows.length - 1; item >= 0; item--)
        {
            let hasParent = -1;
            for (var predecessor = item - 1; predecessor >= 0; predecessor--)
            {
                if (this.rows[predecessor].Level + 1 === this.rows[item].Level)
                {
                    hasParent = predecessor;
                    break;
                }
            }
            this.rows[item].Parent = hasParent;
        }
        this.rowElementArray = [];
        let initialSelectionArray: MobileTreeRow[] = [];
        for (let i = 0; i < this.rows.length; i++)
        {
            if (this.rows[i].NodeType === 81)
			{
				let sceneName = this.rows[i].NodeName;
				try
				{
					sceneName = _SceneNameOverride;
				}
				catch (e) { }
				this.rows[i].NodeName = sceneName;
			}
            let row = new MobileTreeRow(this.treeWrapper, this.client, this.rows[i], i);
            this.rowElementArray.push(row);
            $(row.itemEl).click(ev =>
            {
                let el = ev.target;
                if ($(el).hasClass('modeltree-item-visibility') || $(el).hasClass('mobile-modeltree-expander'))
                {
                    return;
                }
                else
                {
                    if ($(this.multiselectIcon).hasClass('mobile-modeltree-multiselect-enabled'))
                    {
                        if (this.selectedRow)
                        {
                            this.selectedRows.push(this.selectedRow);
                            this.selectedRow = undefined;
                        }
                        let deselectRow = -1;
                        let nodeIdString = '';
                        for (let id = 0; id < this.selectedRows.length; id++)
                        {
                            if (this.selectedRows[id].options.NodeId === row.options.NodeId)
                            {
                                deselectRow = id;
                                continue;
                            }
                            let stringPart = '<NodeId>' + this.selectedRows[id].options.NodeId + '</NodeId>';
                            nodeIdString += stringPart;
                        }
                        if (deselectRow === -1)
                        {
                            this.selectedRows.push(row);
                            let stringPart = '<NodeId>' + row.options.NodeId + '</NodeId>';
                            nodeIdString += stringPart;
                        }
                        else
                        {
                            this.selectedRows.splice(deselectRow, 1);
                        }
                        let xml = '<Call Method="SetSelectedNodes">' + nodeIdString + '</Call>';
                        this.client.command.executeXML(xml);
                    }
                    else
                    {
                        this.selectedRows = [];
                        this.selectedRow = row;
                        let xml = '<Call Method="SetSelectedNodes"><NodeId>' + row.options.NodeId + '</NodeId></Call>';
                        this.client.command.executeXML(xml);
                    }
                }
            });
            if (row.selected === 1)
            {
                initialSelectionArray.push(row);
            }
        }
        let selectionCount = initialSelectionArray.length;
        if (initialSelectionArray.length > 1)
        {
            this.selectedRows = [];
            this.selectedRow = undefined;
            for (let sel = 0; sel < selectionCount; sel++)
            {
                this.selectedRows.push(initialSelectionArray[sel]);
            }
        }
        else if (selectionCount === 1)
        {
            this.selectedRows = [];
            this.selectedRow = initialSelectionArray[0];
        }

        $(this.treeWrapper).kendoTouch({
            drag: (e) =>
            {
                this.drag(e);
            },
            dragstart: (e) =>
            {
                this.dragStart(e);
            },
            dragend: (e) =>
            {
                this.dragEnd(e);
            }
        });
    }

    scrollOnKey(ev)
    {
        //Test if arrow left/right are pressed to expand/collapes level
        if (ev.keyCode === 37 || ev.keyCode === 39)
        {
            //Left arrow -> collapse level
            if (ev.keyCode === 37)
            {
                let s = '<Call Method="VirtualTreeViewCollapseSelected"></Call>';
                this.client.command.executeXML(s);
            }
            else
            {
                let s = '<Call Method="VirtualTreeViewExpandSelected"></Call>';
                this.client.command.executeXML(s);
            }
            return;
        }
        //Test if arrow up/down is pressed
        if (ev.keyCode !== 40 && ev.keyCode !== 38 || Date.now() - this.firstScrollHit < 200)
        {
            return;
        }
        this.firstScrollHit = Date.now();
        if (this.selectedRows.length === 0 && !this.selectedRow)
        {
            if (ev.keyCode === 40)
            {
                let xml = '<Call Method="VirtualTreeViewScrollDown"><Steps>1</Steps></Call>';
                this.client.command.executeXML(xml);
            }
            else if (ev.keyCode === 38)
            {
                let xml = '<Call Method="VirtualTreeViewScrollUp"><Steps>1</Steps></Call>';
                this.client.command.executeXML(xml);
            }
        }
        else
        {
            let itemCount = this.rows.length;
            let selectedItemIndex: number;
            for (let i = 0; i < itemCount; i++)
            {
                if (this.selectedRows.length > 0)
                {
                    if (this.rows[i].NodeId === this.selectedRows[this.selectedRows.length - 1].options.NodeId)
                    {
                         selectedItemIndex = i;
                        break;
                    }
                }
                else if (this.rows[i].NodeId === this.selectedRow.options.NodeId)
                {
                    selectedItemIndex = i;
                    break;
                }
            }
            if (selectedItemIndex === 0 && ev.keyCode === 38)
            {
                let xml = '<Call Method="VirtualTreeViewScrollUp"><Steps>1</Steps></Call>';
                this.client.command.executeXML(xml);
                this.client.once('TreeviewUpdated', () =>
                {
                    this.rowElementArray[0].itemEl.click();
                })
            }
            else if (selectedItemIndex === itemCount - 1 && ev.keyCode === 40)
            {
                let xml = '<Call Method="VirtualTreeViewScrollDown"><Steps>1</Steps></Call>';
                this.client.command.executeXML(xml);
                this.client.once('TreeviewUpdated', () =>
                {
                    this.rowElementArray[this.rowElementArray.length - 1].itemEl.click();
                })
            }
            else if (ev.keyCode === 38)
            {
                let newSelectedItem = this.rows[selectedItemIndex - 1];
                let length = this.rows.length;
                for (let i = 0; i < length; i++)
                {
                    if (this.rowElementArray[i].options.NodeId === newSelectedItem.NodeId)
                    {
                        this.rowElementArray[i].itemEl.click();
                    }
                }
            }
            else if (ev.keyCode === 40)
            {
                let newSelectedItem = this.rows[selectedItemIndex + 1]
                let length = this.rows.length;
                for (let i = 0; i < length; i++)
                {
                    if (this.rowElementArray[i].options.NodeId === newSelectedItem.NodeId)
                    {
                        this.rowElementArray[i].itemEl.click();
                    }
                }
            }
        }
    }

    updateTree(tree: TreeInfo)
    {
        this.rows = tree.Items;
        this.expandedNodesSize = tree.ExpandedNodes;
        this.virtualScrollPos = tree.VirtualScrollPosition
        this.calculateBar();
        this.build();
    }

    calculateBar()
    {
        if (this.expandedNodesSize > this.maxVisible)
        {
            let containerHeight = this.maxVisible * 20;
            //Calculate height
            let heightRatio = this.maxVisible / this.expandedNodesSize;
            let minHeight = 20;
            let barHeight = heightRatio * containerHeight;
            if (barHeight < minHeight)
            {
                this.bar.style.height = minHeight + 'px';
            }
            else
            {
                this.bar.style.height = heightRatio * 100 + '%';
            }
            //Calculate offset
            let positionRatio = this.virtualScrollPos / this.expandedNodesSize;

            let barTop = positionRatio * containerHeight
            if (barTop > containerHeight - minHeight)
            {
                barTop = containerHeight - minHeight;
            }
            this.bar.style.top = barTop + 'px';
        }
        else
        {
            this.bar.style.height = '0px';
        }

    }

}



