import events = require('events');
import { RibbonButton } from './ribbonButton'
import { MobileRibbon } from './index';
import { appStates } from '../../app/app-states';
import { settings } from '../../app/app-settings';
import * as ui from '../app/ui';

declare var _KISTERSWebViewerHideFullClient: boolean;
declare var _functions: any;
declare var _RibbonGroups: any;
declare var _Tabs: any;
declare var _3DReducedSet: string[];
declare var _2DReducedSet: string[];
declare var _CustomRibbon: any;
declare var _ShowFileRibbonOnUrlOpen: boolean;

interface Context
{
    iconId: string
    langId: string
    children: any
    childrenIdx: string[]
    ribbon: MobileRibbon
    method: (client: VSWebClient, state: any, ribbon: RibbonAdapter) => any
    interaction?: string,
    interactionClass?: string,
    paneId?: string
}

function getLanguages()
{
    let langs = [
        { key: 'ENU', value: 'English' },
        { key: 'DEU', value: 'Deutsch' },
        { key: 'ITA', value: 'Italiano' },
        { key: 'ESP', value: 'Espaol' },
        { key: 'FRA', value: 'Franais' },
        { key: 'KOR', value: '' },
        { key: 'CHS', value: '' },
        { key: 'JPN', value: '' },
    ];
    let ribbonLang = [];
    for (let i = 0; i < langs.length; i++)
    {
        let ribbon = {
            iconId: '',
            langId: langs[i].value,
            id: langs[i].key,
            method: function (client)
            {
                let xml = '<Call Method="SetSettings"><Settings><Renderer><General><Language>' + langs[i].key + '</Language></General></Renderer></Settings></Call>';
                client.command.executeXML(xml);
                client.emit('@changeLang', langs[i].key);
                localStorage.setItem('WVLang', langs[i].key);
            }
        }
        ribbonLang.push(ribbon);
    }
    return ribbonLang;
}

let appRibbon:any = {
    iconId: '',
    langId: 'App',
    id: 'rApp',
    children: [
        {
            iconId: 'mobile_UI_question_circle',
            langId: 'ID_RIBBON_ABOUT',
            id: 'rAbout',
            method: function (client)
            {
                client.emit('openAbout');
            }
        },
        {
            iconId: 'mobile_UI_language',
            langId: 'ID_LANGUAGE',
            id: 'rLangs',
            children: getLanguages()
        },
        {
            iconId: 'mobile_UI_gears',
            langId: 'ID_RIBBON_APP_SETTINGS',
            id: 'rAppSettings',
            paneId: 'Custom_AppSettings',
            method: function (client)
            {
            }
        }
    ]
}

let startDrag: number

export class RibbonAdapter extends events.EventEmitter
{
    ribbonConf;
    backendMode: boolean = false;
    urlOpenMode: boolean = false;
    firstLevel: RibbonButton[];
    el: HTMLElement;
    ribbon: MobileRibbon;
    levelArr: any[][];
    selectionButtons: RibbonButton[];
    LMBButtons: RibbonButton[];
    interactionButtons: RibbonButton[];
    renderModeButtons: RibbonButton[];
    buttons: RibbonButton[];
    visShareState: number = 2; //0: No VisShare or no write access, 1: Write access, 2: Private access

    constructor(el: HTMLElement, ribbon: MobileRibbon)
    {
        super();
        //Check for backend mode
        var queryString = window.location.search.substr(1);
        var kvs = queryString.split('&');
        var query = {};
        for (let kv of kvs)
        {
            let [k, v] = kv.split('=');
            query[k] = v;
        }

        if (typeof query['token'] !== 'undefined')
        {
            this.backendMode = true;
        }
        if (typeof query['file'] !== 'undefined' || typeof query['open'] !== 'undefined')
        {
            try
            {
                if (!_ShowFileRibbonOnUrlOpen)
                {
                    this.urlOpenMode = true;
                }
                else
                {
                    this.urlOpenMode = false;
                }
            }
            catch (e)
            {
                this.urlOpenMode = true;
            }
        }
        this.firstLevel = [];
        this.el = el;
        this.ribbon = ribbon;
        this.levelArr = [];
        this.selectionButtons = [];
        this.LMBButtons = [];
        this.interactionButtons = [];
        this.renderModeButtons = [];
        this.buttons = [];

        let showFullClientButton = true;
        try
        {
            if (_KISTERSWebViewerHideFullClient === true)
            {
                showFullClientButton = false;
            }
        }
        catch (e) { }

        if (showFullClientButton)
        {
            let fullClientRibbonButton = {
                iconId: 'mobile_UI_desktop',
                langId: 'ID_RIBBON_DESKTOP_UI',
                id: 'rSwitchDesktopUI',
                method: function (client, state, ribbon)
                {
                    localStorage.setItem('WVParkedSession', appStates.session);
                    localStorage.setItem('WVFileType', appStates.fileType);
                    localStorage.setItem('WVVisShareSpec', appStates.VisShareSpec);
                    localStorage.setItem('WVFileName', appStates.fileName);
                    localStorage.setItem('WVUseMobile', 'false');
                    let call = '<Request Type="Park"><Timeout>0</Timeout></Request>';
                    client.sessionInfo.socket.send(call);
                    setTimeout(() =>
                    {
                        window.location.reload();
                    }, 100)
                }
            };
            appRibbon.children.push(fullClientRibbonButton);
        }

        this.ribbon.on('button-clicked', (button: RibbonButton) =>
        {
            if (button.context.paneId === 'SplitPane' && !button.state.active)
            {
                if (ui.paneController.leftDefaultAdapter.sessions.length > 0)
                {
                    ui.paneController.leftDefaultAdapter.sessions = [];
                    ui.paneController.leftDefaultAdapter.activeSession = undefined;
                    ui.paneController.leftDefaultAdapter.resultInitialized = false;
                    $(ui.paneController.leftDefaultAdapter.resultGroup.wrapperEl).remove();
                    $(ui.paneController.leftDefaultAdapter.resultGroup.panelContainer).remove();
                    $(ui.paneController.leftDefaultAdapter.splitGroupTable).remove();
                }
                else if (ui.paneController.rightDefaultAdapter.sessions.length > 0)
                {
                    ui.paneController.rightDefaultAdapter.sessions = [];
                    ui.paneController.rightDefaultAdapter.activeSession = undefined;
                    ui.paneController.rightDefaultAdapter.resultInitialized = false;
                    $(ui.paneController.rightDefaultAdapter.resultGroup.wrapperEl).remove();
                    $(ui.paneController.rightDefaultAdapter.resultGroup.panelContainer).remove();
                    $(ui.paneController.rightDefaultAdapter.splitGroupTable).remove();
                }
            }
            let level = button.state.level - 1;
            for (let i = 0; i < this.levelArr[level].length; i++)
            {
                if (this.levelArr[level][i].UID === button.UID)
                {
                    if (!this.levelArr[level][i].state.settingsDependent && !this.levelArr[level][i].context.paneId)
                    {
                        this.levelArr[level][i].highlight(true);
                    }
                    else
                    {
                        this.ribbon.vsClient.once('GetSceneSettings', ev =>
                        {
                            if (this.levelArr[level] && this.levelArr[level][i])
                            {
                                this.levelArr[level][i].checkSettings(true);
                            }
                        })
                    }
                }
                else
                {
                    if (this.levelArr[level][i].context.interaction === undefined
                        && !this.levelArr[level][i].state.settingsDependent
                        && !this.levelArr[level][i].context.paneId)
                    {
                        this.levelArr[level][i].highlight(false);
                    }
                    else if (this.levelArr[level][i].state.settingsDependent)
                    {
                        this.ribbon.vsClient.once('GetSceneSettings', ev =>
                        {
                            if (this.levelArr[level])
                            {
                                this.levelArr[level][i].checkSettings(true);
                            }
                        })
                    }
                }
            }
            if (button.context.interactionClass)
            {
                switch (button.context.interactionClass)
                {
                    case 'LMB':
                        this.ribbon.vsClient.emit('LMB', button.context.interaction);
                        break;
                    case 'Select':
                        this.ribbon.vsClient.emit('Select', button.context.interaction);
                        break;
                    case 'RenderMode':
                        this.ribbon.vsClient.emit('RenderMode', button.context.interaction);
                        break;
                }
            }
        })
        this.ribbon.vsClient.on('@changeLang', key =>
        {
            for (let i = 0; i < this.buttons.length; i++)
            {
                settings.lang = key;
                this.buttons[i].update();
            }
        })
    }

    load(ribbonConf, element)
    {
        this.ribbonConf = ribbonConf;
        ribbonConf.push(appRibbon);
        try
        {
            if (Array.isArray(_CustomRibbon))
            {
                for (let group in _CustomRibbon)
                {
                    if (!_CustomRibbon.hasOwnProperty(group)) {
                        continue;
                    }
                    ribbonConf.push(_CustomRibbon[group]);
                }
            }
            else
            {
                ribbonConf.push(_CustomRibbon);
            }
        }
        catch(e){}
        this.firstLevel = [];
        let row = this.createRow(ribbonConf, this.firstLevel, undefined);
        if (row)
        {
            this.registerRow(this.firstLevel, row);
        }
    }

    createRow(parent, row, button)
    {
        let level = 0;
        if (button && button.state.active === true)
        {
            level = button.state.level;
            if (level < this.levelArr.length)
            {
                let length = this.levelArr.length - 1;
                for (let start = length; start >= level; start--)
                {
                    this.deleteRow(start);
                }
            }
            return;
        }
        let context: Context;
        let rowDiv = document.createElement('div');
        rowDiv.className = 'mobile-ribbon-row-wrapper';
        //Compute if this level is already openend
        if (button)
        {
            level = button.state.level;
            if (level < this.levelArr.length)
            {
                this.closeHigherLevel(level);
            }
        }
        if (level === 0)
        {
            rowDiv.style.bottom = '0';
            rowDiv.style.top = '0';
            rowDiv.style.border = 'none';
        }
        else
        {
            rowDiv.style.borderRadius = '8px';
            rowDiv.style.bottom = level * 49 + (level-1) * 15 +'px';
        }
        for (let i = 0; i < parent.length; i++)
        {
            let entry = parent[i];
            //Hide open/import in backend mode oder after url open
            if ((this.backendMode || this.urlOpenMode )
                && (entry.langId === 'ID_RIBBON_FILE' || entry.langId === 'ID_RIBBON_IMPORT'))
            {
                continue;
            }
            //Hide VisShare save button
            if (entry.langId === 'ID_RIBBON_VISSHARE_SAVE' && this.visShareState !== 2)
            {
                continue;
            }
            //Hide VisShare save as button
            if (entry.langId === 'ID_RIBBON_VISSHARE_FILE' && this.visShareState === 0)
            {
                continue;
            }
            try
            {
                if (entry.langId in _functions && _functions[entry.langId] === false)
                {
                    continue;
                }
            }
            catch (e) { }
            try
            {
                if (entry.langId in _RibbonGroups && _RibbonGroups[entry.langId] === false )
                {
                    continue;
                }
            }
            catch (e) { }
            try
            {
                if (entry.langId in _Tabs && _Tabs[entry.langId] === false)
                {
                    continue;
                }
            }
            catch (e) { }
            try
            {
                let fileType = appStates.fileType;
                if (fileType)
                {
                    if (fileType === '3D')
                    {
                        if (_3DReducedSet.indexOf(entry.langId) !== -1)
                        {
                            continue;
                        }
                    }
                    else if (fileType === '2D')
                    {
                        if (_2DReducedSet.indexOf(entry.langId) !== -1)
                        {
                            continue;
                        }
                    }
                }
            }
            catch (e) { }
            let button = this.getButtonByUID(entry.id);
            if (!button)
            {
                context = {
                    iconId: entry.iconId,
                    langId: entry.langId,
                    children: undefined,
                    childrenIdx: [],
                    ribbon: this.ribbon,
                    method: undefined,
                    paneId: entry.paneId
                };
                if (entry.children)
                {
                    context.children = entry.children;
                    context.childrenIdx = [];
                    for (let i = 0; i < entry.children.length; i++)
                    {
                        context.childrenIdx.push(entry.children[i].langId);
                    }
                }
                else if (entry.method)
                {
                    context.method = entry.method;
                }
                if (entry.interaction)
                {
                    context.interaction = entry.interaction;
                    context.interactionClass = entry.interactionClass;
                }
                button = new RibbonButton(entry.id, entry.langId, rowDiv, context, this);

                if (entry.interactionClass)
                {
                    switch (entry.interactionClass)
                    {
                        case 'Select':
                            this.selectionButtons.push(button);
                            break;
                        case 'LMB':
                            this.LMBButtons.push(button);
                            break;
                        case 'Interaction':
                            this.interactionButtons.push(button);
                            break;
                        case 'RenderMode':
                            this.renderModeButtons.push(button);
                            break;
                    }
                }
                //Highlight quick compare button if quick compare is active
                if (button.id === 'ID_RIBBON_COMPARE_MODEL' && ui.paneBadgeIdx.indexOf('wv-quickcompare') !== -1)
                {
                    button.state.active = true;
                    button.highlight(true);
                }
                this.buttons.push(button);
            }
            row.push(button);
        }
        let caretArr = [];
        let leftCaret = document.createElement('i');
        $(leftCaret).addClass('fa fa-angle-left mobile-ribbon-caret-left');
        caretArr.push(leftCaret);
        rowDiv.appendChild(leftCaret);

        let rightCaret = document.createElement('i');
        $(rightCaret).addClass('fa fa-angle-right mobile-ribbon-caret-right');
        caretArr.push(rightCaret);
        rowDiv.appendChild(rightCaret);

        $(rowDiv).kendoTouch({
            drag: (e) =>
            {
                this.dragRow(e, $(rowDiv), caretArr);

            },
            dragstart: (e) =>
            {
                this.startRowDrag(e, $(rowDiv));
            }
        })

        this.el.appendChild(rowDiv);
        return [rowDiv, leftCaret, rightCaret];
    }

    resize()
    {
        this.closeRibbons();
        let firstRow = $('.mobile-ribbon-row-wrapper').first();
        let widthDelta = document.body.clientWidth - firstRow.outerWidth();
        $('.mobile-ribbon-caret-left').css('display', 'none');
        if (widthDelta >= 0)
        {
            $('.mobile-ribbon-caret-right').css('display', 'none');
        }
        else
        {
            $('.mobile-ribbon-caret-right').css('display', 'block');
            $('.mobile-ribbon-caret-right').css('left', window.innerWidth - 15);
        }
        firstRow.css('left', '0');
    }

    getButton(langId)
    {
        for (let i = 0; i < this.buttons.length; i++)
        {
            if (this.buttons[i].id === langId)
            {
                return this.buttons[i];
            }
        }
        return undefined;
    }

    getButtonByUID (id)
    {
        for (let i = 0; i < this.buttons.length; i++)
        {
            if (this.buttons[i].UID === id)
            {
                return this.buttons[i];
            }
        }
        return undefined;
    }

    registerRow(row: RibbonButton[], rowElements: HTMLElement[])
    {
        $('#mobile-floating-deactivate-button').css('bottom', '+=63');
        $('#mobile-floating-action-button').css('bottom', '+=63');
        let windowWidth = $(window).width();
        if (this.levelArr.length > 1)
        {
            let lastRow = this.levelArr[this.levelArr.length - 1];
            for (let i = 0; i < lastRow.length; i++)
            {
                lastRow[i].hideText();
            }
            let lastRowWidth = $(lastRow[0].target).width();
            let lastRowWidthDiff = windowWidth - lastRowWidth;
            if (lastRowWidthDiff > 0)
            {
                lastRow[0].target.style.left = lastRowWidthDiff / 2 + 'px';
            }
        }
        this.levelArr.push(row);
        for (let button = 0; button < row.length; button++)
        {
            row[button].render(rowElements[0]);
            row[button].showText();
        }
        if (rowElements && $(rowElements[0]).width() > window.innerWidth)
        {
            rowElements[2].style.left = window.innerWidth - 15 + 'px';
            rowElements[2].style.display = 'block';
        }
        let rowWidth = $(rowElements[0]).width();
        let rowWidthDiff = windowWidth - rowWidth;
            if (rowWidthDiff > 0)
            {
                rowElements[0].style.left = rowWidthDiff / 2 + 'px';
            }
    }

    deleteRow(index)
    {
        let wrapper: HTMLElement;
        for (let buttonIndex = 0; buttonIndex < this.levelArr[index].length; buttonIndex++)
        {
            let button: RibbonButton;
            button = this.levelArr[index][buttonIndex];
            if (button.target && !wrapper)
            {
                wrapper = button.target
            }
            button.destroy();
        }
        if (wrapper)
        {
            $(wrapper).remove();
        }
        this.levelArr.splice(index, 1);
        $('#mobile-floating-deactivate-button').css('bottom', '-=63');
        $('#mobile-floating-action-button').css('bottom', '-=63');
    }

    closeRibbons()
    {
        for (let i = this.levelArr.length; i > 1; i--)
        {
            this.deleteRow(i - 1);
        }
        let mockButton = new RibbonButton('mockButtonToClose', 'mockButtonToClose', undefined, {}, this);
        mockButton.state.level = 1;
        this.ribbon.emit('button-clicked', mockButton);
    }

    closeHigherLevel(level)
    {
        let length = this.levelArr.length - 1;
        for (let start = length; start >= level; start--)
        {
            this.deleteRow(start);
        }
        let row = this.levelArr[level - 1];
        for (let button = 0; button < row.length; button++)
        {
            row[button].showText();
        }
    }

    startRowDrag(e, el)
    {
        startDrag = e.touch.x.location - parseInt(el.css('left'));
    }

    dragRow(e, el, caretArr)
    {
        if (el.width() <= window.innerWidth)
        {
            return;
        }
        caretArr[0].style.display = 'block';
        caretArr[1].style.display = 'block';
        let newPosition = startDrag - e.touch.x.location;
        if (newPosition < 0)
        {
            caretArr[0].style.display = 'none';
            newPosition = 0;

        }
        if (newPosition > el.width() - window.innerWidth + 2)
        {
            caretArr[1].style.display = 'none';
            newPosition = el.width() - window.innerWidth + 2;
        }
        caretArr[0].style.left = newPosition + 'px';
        caretArr[1].style.left = newPosition + window.innerWidth - 15 + 'px';
        el.css('left', newPosition * -1);
    }


    handleActivateInteraction(mode: string)
    {
        if (appStates.interactionMode.indexOf(mode) === -1)
        {
            this.ribbon.activateInteraction(mode);
        } else
        {
            this.ribbon.deactivateInteraction(mode);
        }
    }
}


