import { CustomPalette } from '../../../../pane/custom-palette'
var tinycolor = require("tinycolor2");

export class ModalColorPicker
{
	colorpicker: HTMLElement
	palette: CustomPalette
	isDefaultSet: boolean = false
	alpha: number // [0, 1]
    showAlpha: boolean
    parentEl: HTMLElement
    color: string
    callback: any

    constructor(parentEl: HTMLElement, callback: any)
    {
        this.parentEl = parentEl;
        this.callback = callback;

        this.colorpicker = document.createElement('span');
        this.colorpicker.classList.add('modal-colorpicker');
        this.parentEl.appendChild(this.colorpicker);

		this.colorpicker.addEventListener('click', ev => {
            this.openPalette();
		}, false)
	}

	openPalette() {
        let activeColor = this.colorpicker.dataset['lastPickedColor'];
        this.palette = new CustomPalette(activeColor, this.showAlpha);

        this.palette.on('customColorChosen', color => this.updateElementState(color));

	}

	updateElementState(color)
	{
        this.colorpicker.dataset['lastPickedColor'] = color;
        this.colorpicker.style.backgroundColor = color;
        let hashHex = tinycolor(color).toHexString();
        this.color = hashHex;
        this.callback(this.color);
	}

	setColor(hexColor: any) {
        let RGBAColor = hexToRgba(hexColor);
        this.alpha = RGBAColor.a;
        let cssColor;
		if(this.showAlpha) {
            cssColor = `rgba(${RGBAColor.r}, ${RGBAColor.g}, ${RGBAColor.b}, ${this.alpha})`;
		}
		else {
            cssColor = `rgb(${RGBAColor.r}, ${RGBAColor.g}, ${RGBAColor.b})`;
		}
		if (!this.isDefaultSet) {
            this.colorpicker.dataset['defaultColor'] = cssColor;
            this.isDefaultSet = true;
		}

        this.colorpicker.dataset['lastPickedColor'] = cssColor;
        this.colorpicker.style.backgroundColor = cssColor;
	}
}

function hexToRgba(hex) {
	var bigint, r, g, b, a;
	// Remove '#' character or '0x'
	var aRgb
	if (hex[0] == '#') {
		aRgb = hex.substr(1)
	} else {
		aRgb = hex.substr(2)
	}


	bigint = parseInt(aRgb, 16);

	r = (bigint >> 16) & 255;
	g = (bigint >> 8) & 255;
	b = bigint & 255;
	var rgb = r + "," + g + "," + b;
	a = ((bigint >> 24) & 255) / 255;

	return {
		a: a,
		r: r,
		g: g,
		b: b
	}
}


