    buildHeader (template: any[], type: string)
    {
        $(this.tableHeaderRowEl).empty();
        this.headerKeys = [];
        for (let h = 0; h < template.length; h++)
        {
            let rowHeader = document.createElement('th');
            rowHeader.textContent = template[h].DisplayName;
            rowHeader.className = 'pane-item-entry-name';
            this.headerKeys.push(template[h].Id);
            this.headerNames.push(template[h].DisplayName);

            let sortWrapper = document.createElement('span');
            rowHeader.appendChild(sortWrapper);
            let sortIcon = document.createElement('i');
            sortIcon.className = 'fas fa-sort bom-pane-sort-icon';
            sortWrapper.appendChild(sortIcon);

            rowHeader.onclick = e =>
            {
                //Change to sort down
                if (sortIcon.className.indexOf('fa-sort-up') !== -1)
                {
                    sortIcon.className = 'fas fa-sort-down bom-pane-sort-icon';
                    this.sortTable(false, template[h].Id, type);
                }
                else
                {
                    sortIcon.className = 'fas fa-sort-up bom-pane-sort-icon';
                    this.sortTable(false, template[h].Id, type);
                }
            }

            this.tableHeaderRowEl.appendChild(rowHeader);
        }
    }
    buildBody (rows: any[], type: string)
    {
        if (!rows)
        {
            return;
        }
        this.calloutInputs = [];
        this.rows = [];
        this.sum = 0;
        $(this.tableBodyEl).empty();
        let placeholderRow = document.createElement('tr');
        this.tableBodyEl.appendChild(placeholderRow);
        for (let r = 0; r < rows.length; r++)
        {
            this.buildRow(rows[r], type);
        }
        if (type === 'BOM')
        {
            this.buildSumRow();
        }
    }

    buildRow (row, type: string)
    {
        this.rows.push(row);
        let rowEl = document.createElement('tr');
        rowEl.className = 'pane-item';
        rowEl.id = type + '_ROW_ID' + row['Id'];
        rowEl.onclick = e =>
        {
            if (type === 'BOM')
            {
                this.BOMRowClicked(row, e);
            }
            else if (type === 'clash')
            {
                this.clashRowClicked(row);
            }
        }
        this.tableBodyEl.appendChild(rowEl);
        for (let i = 0; i < this.headerKeys.length; i++)
        {
            let cellEl = document.createElement('td');
            cellEl.className = 'pane-item-entry-name';
            if (type === 'BOM')
            {
                if (this.headerKeys[i] === 'CalloutId')
                {
                    this.calloutInputs.push(cellEl);
                    cellEl.contentEditable = 'true';
                    cellEl.onblur = e =>
                    {
                        if (cellEl.textContent && cellEl.textContent !== '')
                        {
                            let xml = '<Call Method="UpdateBillOfMaterialRowCalloutId">';
                            xml += '<RowId>' + row['Id'] + '</RowId>';
                            xml += '<CalloutId>' + cellEl.textContent + '</CalloutId></Call>';
                            row.CalloutId = cellEl.textContent;
                            this.client.command.executeXML(xml);
                        }
                        else
                        {
                            let xml = '<Call Method="ClearBillOfMaterialCalloutId">';
                            xml += '<RowId>' + row['Id'] + '</RowId></Call>';
                            row.CalloutId = cellEl.textContent;
                            this.client.command.executeXML(xml);
                        }

                    }
                    cellEl.onkeydown = e =>
                    {
                        if (e.keyCode === 13)
                        {
                            cellEl.blur();
                        }
                    }
                }
                cellEl.textContent = this.defaultCols.indexOf(this.headerKeys[i]) === -1 ? this.searchAttribute(row, this.headerKeys[i]) : row[this.headerKeys[i]];
                if (this.headerKeys[i] === 'Quantity')
                {
                    this.sum += parseInt(row[this.headerKeys[i]]);
                }
            }
            else if (type === 'clash')
            {
                cellEl.textContent = row[this.headerKeys[i]];
            }
            rowEl.appendChild(cellEl);
        }
    }

    BOMRowClicked (row, event: MouseEvent)
    {
        let xml = '<Call Method="SelectBillOfMaterialRows">';
        xml += '<RowId>' + row.Id + '</RowId>';
        xml += '<AddToSelection>' + (event.ctrlKey || this.multiselect) + '</AddToSelection>';
        xml += '</Call>';
        this.client.command.executeXML(xml);
    }

    clashRowClicked (row)
    {
        let xml = '<Call Method="SelectClashIndex">';
        xml += '<Index>' + row.Id + '</Index>';
        xml += '</Call>';
        this.selectedIndex = row.Id;
        this.client.command.executeXML(xml);
    }

    sortTable (asc: boolean, key: string, type: string)
    {
        let sortedRows = this.rows.sort((item1, item2) =>
        {
            let val1, val2;
            let preVal1 = item1[key];
            let preVal2 = item2[key];
            if (type === 'BOM')
            {
                if (item1[key] === undefined)
                {
                    for (let i = 0; i < item1['Attribute'].length; i++)
                    {
                        if (item1['Attribute'][i]['Name'] === key)
                        {
                            preVal1 = item1['Attribute'][i]['__text__'];
                            preVal2 = item2['Attribute'][i]['__text__'];
                        }
                    }
                }
            }
            val2 = isNaN(parseFloat(preVal1)) ? preVal1.toUpperCase() : parseFloat(preVal2);
            val1 = isNaN(parseFloat(preVal1)) ? preVal2.toUpperCase() : parseFloat(preVal1);
            if (val1 > val2)
            {
                return asc ? 1 : -1;
            }
            else
            {
                return asc ? -1 : 1;
            }
        });
        this.buildBody(sortedRows, type);
    }

    searchAttribute (row, key: string)
    {
        if (row.Attribute)
        {
            let attributes = Array.isArray(row.Attribute) ? row.Attribute : [row.Attribute];
            for (let i = 0; i < attributes.length; i++)
            {
                if (attributes[i]['Name'] === key)
                {
                    return attributes[i]['__text__'];
                }
            }
            return '';
        }
    }

