import { VSWebClient } from '../../base/vsweb-client-impl'
import { appStates } from '../../app/app-states'
import { settings } from '../../app/app-settings'
import events = require('events')
import * as ui from '../app/ui'
import { ribbonConf } from './template'
import * as adapter from './adapter'

declare var vsClient: VSWebClient
declare var _AboutLinks: Object;
declare var _UseOfflineHelp: boolean;

export class MobileRibbon extends events.EventEmitter
{
    ribbonBody: HTMLElement
    languageSwitch: HTMLElement
    aboutDialog: HTMLElement
    vsClient: VSWebClient

    aboutOpen: boolean

    adapter: adapter.RibbonAdapter


    constructor(el: HTMLElement)
    {
        super()
        this.vsClient = vsClient;
        this.aboutOpen = false;
        let canvas = document.getElementById('wv-canvas');
        canvas.onclick = () =>
        {
            this.adapter.closeRibbons();
        }

        this.aboutDialog = this._buildAboutDialog();
        document.body.appendChild(this.aboutDialog);
        this.adapter = new adapter.RibbonAdapter(el, this);
    }

    initialize()
    {
        this.render()

        vsClient.on('ActivateInteraction', (ev) =>
        {
            let mode: string;
            mode = ev['Interaction'];
            if (mode && mode.indexOf('Split') === 0)
            {
                return;
            }
            if (!mode || mode === 'Flythrough_SelectStartPositionAndDirection')
            {
                return;
            }
            switch (mode)
            {
                case 'CuttingPlane_FromPointNormal':
                    mode = 'Section_FromPointNormal'
                    break
                case 'CuttingPlane':
                    mode = 'Section'
                    break
            }
            let paneId = this.getPaneIdForInteraction(mode);
            if (appStates.interactionMode.indexOf(mode) === -1)
            {
                appStates.interactionMode.push(mode);
            }
            if (!paneId)
            {
               paneId = 'no_pane_' + mode;
            }
            //Highlight button
            for (let i = 0; i < this.adapter.interactionButtons.length; i++)
            {
                let button = this.adapter.interactionButtons[i];
                if (appStates.interactionMode.indexOf(button.context.interaction) !== -1)
                {
                    button.highlight(true)
                }
            }
            //Highlight badge
            for (let i = 0; i < ui.interactionBadges.length; i++)
            {
                if (ui.interactionBadges[i].id === paneId)
                {
                    $(ui.interactionBadges[i]).addClass('mobile-badge-highlighted');
                }
            }
            //Create badge
            ui.createInteractionBadge(paneId);
            //Activate fly controls
            if (mode === 'Flythrough')
            {
                ui.createFlyControls();
                vsClient.command.executeXML('<Call Method="GetSceneSettings"></Call>');
            }
            //Emit transform changed event
            if (mode.indexOf('Transformation') === 0)
            {
                vsClient.emit('ActivateTransform', mode);
            }
            else
            {
                vsClient.emit('DeactivateTransform', mode);
            }
            if (mode.indexOf('Measurement') === 0)
            {
                let event = { Mock: true, Type: 'MeasurementChanged' };
                if (mode === 'Measurement_EdgeLengthAccumulated')
                {
                    event['AccumulatedLength'] = 0;
                }
                vsClient.emit('MeasurementChanged', event);
            }
        })

        vsClient.on('DeactivateInteraction', (ev) =>
        {
            let mode: string;
            mode = ev['Interaction'];
            if (mode && mode.indexOf('Split') === 0)
            {
                return;
            }
            switch (mode)
            {
                case 'CuttingPlane_FromPointNormal':
                    mode = 'Section_FromPointNormal'
                    break
                case 'CuttingPlane':
                    mode = 'Section'
                    break
            }
            let paneId = this.getPaneIdForInteraction(mode);
            if (appStates.interactionMode.indexOf(mode) !== -1)
            {
                appStates.interactionMode.splice(appStates.interactionMode.indexOf(mode), 1);
            }
            if (!paneId)
            {
                paneId = 'no_pane_' + mode;
            }
            //De-highlight button
            for (let i = 0; i < this.adapter.interactionButtons.length; i++)
            {
                let button = this.adapter.interactionButtons[i];
                if (appStates.interactionMode.indexOf(button.context.interaction) === -1)
                {
                    button.highlight(false);
                }
            }
            //De-highlight badge
            for (let i = 0; i < ui.interactionBadges.length; i++)
            {
                if (ui.interactionBadges[i].id === paneId)
                {
                    $(ui.interactionBadges[i]).removeClass('mobile-badge-highlighted');
                }
            }
            if (mode !== 'Flythrough_SelectStartPositionAndDirection')
            {
                //Delte badge
                ui.deleteInteractionBadge(paneId);
            }
            //Deactivate fly controls
            if (mode === 'Flythrough')
            {
                ui.removeFlyControls();
            }
            //Emit deactivate transform changed event
            if (mode.indexOf('Transformation') === 0)
             {
                vsClient.emit('DeactivateTransform', mode);
            }
        })

        vsClient.on('LMB', ev =>
        {
            appStates.mouseLeftButtonMode = ev;
            //Change highlighting
            for (let i = 0; i < this.adapter.LMBButtons.length; i++)
            {
                let button = this.adapter.LMBButtons[i];
                if (appStates.mouseLeftButtonMode === button.context.interaction)
                {
                    button.highlight(true);
                }
                else
                {
                    button.highlight(false);
                }
            }
        });

        vsClient.on('Select', ev =>
        {
            let oldMode = appStates.selectionMode;
            appStates.selectionMode = ev;
            if (ev === 'Neighbourhood')
            {
                if ($('#mobile-neighbourhood-search-panel').length === 0)
                {
                    ui.buildNeighbourhoodSearchPanel();
                }
                ui.togglePaneBadge('NeighbourhoodSearchPane');
                $('#mobile-neighbourhood-search-panel').show();
            }
            else
            {
                ui.deletePaneBadge('wv-neighbourhoodsearch');
                $('#mobile-neighbourhood-search-panel').hide();
            }
            //Change highlighting
            for (let i = 0; i < this.adapter.selectionButtons.length; i++)
            {
                let button = this.adapter.selectionButtons[i];
                if (appStates.selectionMode === button.context.interaction)
                {
                    button.highlight(true);
                }
                else
                {
                    button.highlight(false);
                }
            }
            if (ev === 'Ghost')
            {
                ui.toggleGhostBadges(true, oldMode);
            }
            else
            {
                ui.toggleGhostBadges(false, oldMode);
            }
        });

        vsClient.on('RenderMode', ev =>
        {
            let modes = appStates.renderingModes;
            if (Array.isArray(ev))
            {
                let renderModes = ev;
                appStates.renderingModes = renderModes;
            }
            else
            {
                if (modes.indexOf(ev) !== -1)
                {
                    modes.splice(modes.indexOf(ev), 1);
                }
                else
                {
                    modes.push(ev);
                    if (modes.length === 1 && modes[0] === 'Perspective')
                    {
                        modes.push('Solid');
                    }
                }
                var xml = '<Call Method="SetSceneSettings"><Settings><Scene><Visualization><RenderMode>';
                for (let i = 0; i < modes.length; i++)
                {
                    xml = xml + modes[i] + ' ';
                }
                xml = xml + '</RenderMode></Visualization></Scene></Settings></Call>';
                vsClient.command.executeXML(xml);
            }
            //Change highlighting
            for (let i = 0; i < this.adapter.renderModeButtons.length; i++)
            {
                let button = this.adapter.renderModeButtons[i];
                if (modes.indexOf(button.context.interaction) !== -1)
                {
                    button.highlight(true);
                }
                else
                {
                    button.highlight(false);
                }
            }
        });
    }

    getPaneIdForInteraction(mode)
    {
        switch (mode)
        {
            case 'CuttingPlane_FromPointNormal':
                mode = 'Section_FromPointNormal';
                break;
            case 'CuttingPlane':
                mode = 'Section';
                break;
        }
        let generalMode = mode.split('_')[0];
        let paneId;
        switch (generalMode)
        {
            case 'Measurement':
                paneId = 'wv-measurement';
                break;
            case 'Section':
                paneId = 'wv-section';
                break;
            case 'Transformation':
                paneId = 'wv-transform';
                break;
            case 'DraftAngle':
                paneId = 'wv-draftangle';
                break;
            case 'Markup':
                if (mode === 'Markup_Circle' || mode === 'Markup_Freehand' || mode === 'Markup_Rectangle')
                {
                    paneId = 'wv-markup-reduced';
                }
                else
                {
                    paneId = 'wv-markup';
                }
                break;
            case 'Flythrough':
                paneId = 'wv-flythrough';
                break;
            case 'BoundaryCondition_Axis':
                paneId = 'no_pane_BoundaryCondition_Axis';
                break;
            case 'BoundaryCondition_AxisByCircle':
                paneId = 'no_pane_BoundaryCondition_AxisByCircle';
                break;
            case 'BoundaryCondition_AxisByLine':
                paneId = 'no_pane_BoundaryCondition_AxisByLine';
                break;
            case 'Callout':
                paneId = 'no_pane_' + mode;
                break;
            default:
                paneId = undefined;
                break;
        }
        return paneId;
    }

    render()
    {
        this.adapter.load(ribbonConf.ribbonConf, undefined);
    }

    resize()
    {
        this._buildAboutDialog();
        this.adapter.resize();
    }

    reset()
    {
    }

    openPage(url)
    {
        let offlineHelp: boolean = false;
        try
        {
            offlineHelp = _UseOfflineHelp;
        }
        catch (e)
        {
        }
        if (url === 'https://viewer.kisters.de/help/webviewer')
        {
            url = offlineHelp ? '/Help/webviewer/' + ui.mapCountry(settings.lang) + '/index.html' : url + '/' + appStates.version + '/' + ui.mapCountry(settings.lang) + '/index.php';
        }
        window.open(url, '_blank');
    }


    _buildAboutDialog(): HTMLElement
    {
        let heightWidthRatio = 0.54;
        //Calculate max width for the image
        let bodyHeight = document.body.clientHeight;
        let bodyWidth = document.body.clientWidth;
        let widthRatio = 0.9;
        let imageHeight = bodyWidth * heightWidthRatio * widthRatio;

        while (imageHeight + 50 > bodyHeight)
        {
            widthRatio -= 0.05;
            imageHeight = bodyWidth * heightWidthRatio * widthRatio;
        }
        //Calculate offset top if possible
        let offsetTop = 0;
        if (bodyHeight - imageHeight > 50)
        {
            offsetTop = (bodyHeight - imageHeight - 50) / 2;
        }
        //Calculate button width and offset
        let buttons;
        try
        {
            buttons = _AboutLinks;
        }
        catch (e)
        {
            buttons = appStates.aboutLinks;
        }
        let buttonWidth = imageHeight * 0.11;
        let buttonPadding = buttonWidth * 0.265;
        if ($('#aboutDialog').length > 0)
        {
            $('#aboutDialogImage').css('marginTop', offsetTop);
            $('#aboutDialogImage').attr('width', widthRatio * 100 + '%');
            let offsetRight = bodyWidth - (bodyWidth * widthRatio) - (bodyWidth * 0.05);
            let leftButtonPos = ((buttons.length - 1) * buttonWidth) + ((buttons.length - 1) * buttonPadding) + offsetRight;
            $('.mobile-ribbon-about-button').each(function (index)
            {
                $(this).css({
                    'width': buttonWidth + 'px',
                    'height': buttonWidth * 0.95 + 'px',
                    'bottom': offsetTop + 52 + (buttonWidth * 0.05) + 'px',
                    'right': leftButtonPos - index * buttonWidth - (index * buttonPadding) + 'px'
                });
            });
            $('.mobile-ribbon-about-default-text-wrapper').css({
                'bottom': offsetTop + 50 + (buttonWidth * 1.2) + 'px',
                'left': bodyWidth * 0.05 + (bodyWidth * widthRatio * 0.13) + 'px',
                'width': bodyWidth * widthRatio - (bodyWidth * 0.16),
                'font-size': imageHeight * 0.04 + 'px',
                'height': imageHeight * 0.05 + 'px'
            });
            $('.mobile-ribbon-about-default-copyright').css({
                'font-size': imageHeight * 0.03 + 'px',
            });

            $('.mobile-ribbon-about-version-wrapper').css({
                'bottom': offsetTop + 50 + (buttonWidth * 2.8) + 'px',
                'left': bodyWidth * 0.05 + (bodyWidth * widthRatio * 0.56) + 'px',
                'width': bodyWidth * widthRatio - (bodyWidth * widthRatio * 0.56),
                'font-size': imageHeight * 0.04 + 'px'
            });
            return;
        }

        let aboutDialogWrapper = document.createElement('div');
        aboutDialogWrapper.classList.add('ribbon-about-wrapper');
        aboutDialogWrapper.id = "aboutDialog";
        aboutDialogWrapper.style.display = 'none';
        aboutDialogWrapper.style.width = '100%';
        aboutDialogWrapper.style.height = '100%';

        let aboutDialogImage = document.createElement('img');
        aboutDialogImage.src = '3D-VS_About-Screen.png';
        aboutDialogImage.id = 'aboutDialogImage';
        aboutDialogImage.setAttribute('width', widthRatio * 100 + '%');
        aboutDialogImage.style.marginTop = offsetTop + 'px';
        aboutDialogImage.style.marginLeft = '5%';

        let defaultTextWrapper = document.createElement('div');
        defaultTextWrapper.classList.add('mobile-ribbon-about-default-text-wrapper');

        let defaultTextEdition = document.createElement('span');
        defaultTextEdition.classList.add('mobile-ribbon-about-default-text');
        defaultTextEdition.textContent = "WebViewer-Edition";

        let defaultTextCopyRight = document.createElement('span');
        defaultTextCopyRight.classList.add('mobile-ribbon-about-default-copyright');
        defaultTextCopyRight.textContent = "\u00A92020 by KISTERS All rights reserved";

        let versionTextWrapper = document.createElement('div');
        versionTextWrapper.classList.add('mobile-ribbon-about-version-wrapper');

        let programVersionWrapper = document.createElement('div');

        let programVersionTitle = document.createElement('span');
        programVersionTitle.textContent = _lang_data['ID_ABOUT_PROGRAMVERSION'][settings.lang] + " "

        let programVersion = document.createElement('span');
        programVersion.id = "program_version_text";

        programVersionWrapper.appendChild(programVersionTitle);
        programVersionWrapper.appendChild(programVersion);

        let versionImporterWrapper = document.createElement('div');

        let versionImporterTitle = document.createElement('span');
        versionImporterTitle.textContent = "Importer: ";

        let versionImporter = document.createElement('span');
        versionImporter.id = "version_importer_text";

        versionImporterWrapper.appendChild(versionImporterTitle);
        versionImporterWrapper.appendChild(versionImporter);

        vsClient.on('@changeLang', lang =>
        {
            programVersionTitle.textContent = _lang_data['ID_ABOUT_PROGRAMVERSION'][settings.lang] + " ";
            versionImporterTitle.textContent = "Importer: ";
        })

        versionTextWrapper.appendChild(programVersionWrapper);
        versionTextWrapper.appendChild(versionImporterWrapper);
        aboutDialogWrapper.appendChild(versionTextWrapper);
        defaultTextWrapper.appendChild(defaultTextEdition);
        defaultTextWrapper.appendChild(defaultTextCopyRight);
        aboutDialogWrapper.appendChild(defaultTextWrapper);
        aboutDialogWrapper.appendChild(aboutDialogImage);
        //Buttons
        let offsetRight = bodyWidth - (bodyWidth * widthRatio) - (bodyWidth * 0.05);
        let leftButtonPos = ((buttons.length - 1) * buttonWidth) + ((buttons.length - 1) * buttonPadding) + offsetRight;
        for (let i = 0; i < buttons.length; i++)
        {
            let button = document.createElement('div');
            button.classList.add('mobile-ribbon-about-button');
            button.style.right = (leftButtonPos - (i * buttonWidth)) + 'px';
            button.style.width = buttonWidth + 'px';
            button.style.height = buttonWidth + 'px';
            $(button).click((e) =>
            {
                e.preventDefault();
                this.openPage(buttons[i]);
            });
            aboutDialogWrapper.appendChild(button);
        }

        vsClient.on('openAbout', () =>
        {
            if (this.aboutOpen === false)
            {
                let xml = '<Call Method="GetVersionInfo"></Call>';
                vsClient.command.executeXML(xml);
                vsClient.once("GetVersionInfo", response =>
                {
                    let versionArr = response.Version.split('.');
                    versionArr.pop();
                    appStates.version = versionArr.join('.');
                    $('#program_version_text').text(response.LongVersion);
                    $('#version_importer_text').text(response.ImporterVersion);
                })
                // document.body.appendChild(aboutDialogWrapper)
                $(aboutDialogWrapper).show();
                $(document).on('click', this.closeAboutDialog);
                $(document).on('keyup', this.closeAboutDialog);
                this.aboutOpen = true;
            }
            else
            {
                //document.body.removeChild(aboutDialogWrapper)
                $(aboutDialogWrapper).hide();
                $(document).off('click', this.closeAboutDialog);
                $(document).off('keyup', this.closeAboutDialog);
                this.aboutOpen = false;
            }
        })
        return aboutDialogWrapper;
    }

    closeAboutDialog = (e) =>
    {
        if ($(e.target).hasClass('mobile-ribbon-about-button') ||
            $(e.target).parent().parent().parent().attr('id') === 'ID_RIBBON_ABOUT' ||
            $(e.target).parent().attr('id') === 'ID_RIBBON_ABOUT' ||
            $(e.target).attr('id') === 'ID_RIBBON_ABOUT' ||
            e.target.nodeName === 'svg' || e.target.nodeName === 'path')
        {
            return;
        }
        let dialog = document.getElementById('aboutDialog')
        let versionWrapper = document.getElementsByClassName('mobile-ribbon-about-version-wrapper')[0]
        if ((!versionWrapper.contains(e.target) && e.type === 'click') || (e.type === 'keyup' && e.keyCode === 27))
        {
            this.aboutOpen = false;
            dialog.style.display = 'none';
            $(document).off('click', this.closeAboutDialog);
            $(document).off('keyup', this.closeAboutDialog);
        }
    }

    activateInteraction(action: string)
    {
        var s = '<Call Method="ActivateInteraction"><Interaction>' + action + '</Interaction></Call>';
        vsClient.command.executeXML(s);
    }

    deactivateInteraction(mode: string = 'Deactivate_AllInteractions')
    {

        let modeCategory = mode.split('_')[0];
        let categoryToDeactivate = 'Deactivate_' + modeCategory;

        var s = '<Call Method="ActivateInteraction"><Interaction>' + categoryToDeactivate + '</Interaction></Call>';
        vsClient.command.executeXML(s);
    }

    deleteShareRibbon (deleteSaveAs: boolean)
    {
        this.adapter.visShareState = deleteSaveAs ? 0 : 1;
    }

}



